.rd-scrollbar {

    &-container {
        overflow:hidden !important;
        position:relative;
    }

    &-inner {
        overflow:scroll;
        overflow-x:hidden;
        height:100%;
        *position:absolute;
        *left:0;
        *right:0;

        -ms-overflow-style:none;

        &::-webkit-scrollbar {
            width:0;
            height:0;
        }
    }

    &-bar-y, &-bar-x {
        cursor:default;
        position:absolute;
        border-radius:1px;
        z-index:1;
        background:$color-primary;

        transition:opacity .2s;

        &:hover {
            opacity:.8;
        }

        &:active {
            opacity:.3;
        }
    }

    &-bar-y {
        top:0;
        right:0;
        width:3px;
    }

    &-bar-x {
        display:none;
    }

    &-hidden-y > &-bar-y, &-hidden-x > &-bar-x {
        opacity:0;
        visibility:hidden;
        transition:opacity .2s, visibility step-end .3s;
    }
}

.custom-scroll_bar-x {
    display:none;
}

.rd-scrollbar-hidden-y > .rd-scrollbar-bar-y,
.rd-scrollbar-hidden-x > .rd-scrollbar-bar-x {
    opacity: 0;
    visibility: hidden;
    transition: opacity .2s, visibility step-end .3s;
}
