.rd-sidebar {
    $root:&;
    display:none;
    position:fixed;
    top:0;
    right:0;
    bottom:0;
    z-index:106;
    height:100%;
    width:102px;

    color:$color-text;
    background:$color-base;
    border-left:1px rgba($color-text, 0.1) solid;

    transition:background 0.4s;

    @media (min-width:$screen-md-min) {
        display:table;
    }

    &-outer {
        position:relative;
        height:100%;

        @media (min-width:$screen-md-min) {
            margin-right:102px;
        }
    }

    &-items {
        display:table-cell;
        width:100%;
        height:100%;
        vertical-align:middle;
    }

    &-item {
        $this:&;
        position:relative;
        display:table;
        height:150px;
        width:100%;

        background:none;
        color:inherit;

        &-inner {
            display:table-cell;
            overflow:hidden;
            vertical-align:middle;
            text-align:center;

            position:relative;
            z-index:2;
        }

        &-hover {
            position:absolute;
            display:flex;
            top:0;
            right:102px;
            bottom:0;
            height:100%;
            padding:0 20px;
            z-index:3;
            transform:scaleX(0);
            transform-origin:right center;
            white-space:nowrap;
            align-items:center;
            justify-content:center;

            background:$color-fill;

            transition:transform 0.4s 0.4s;

            &:lang(ar) {
                direction:rtl;
            }

            &-col {
                padding:0 18px;
                opacity:0;
                transform:scale(0);
                vertical-align:middle;

                @for $i from 1 through 6 {
                    &:nth-child(#{$i}) {
                        transition:all 0.4s ease-in-out ($i * 0.05s);
                    }
                }

                &.-center {
                    text-align:center;
                }

                &.-visible {
                    transform:scale(1);
                    opacity:1;

                    @for $i from 1 through 6 {
                        &:nth-child(#{7 - $i}) {
                            transition:all 0.4s ease-in-out (0.10s + ($i * 0.05s));
                        }
                    }
                }
            }

            &-title {
                position:relative;
                margin:11px 0;
                left:2px;

                color:lighten($color-text, 55%);
                font-size: 13px;
                font-weight: 500;
                letter-spacing:0.22px;
                .-center{
                    text-align: center;
                }
                &:lang(ar){
                    line-height: 17px;
                }
            }

            &-icon {
                margin:10px 0;
                font-size:20px;

                &:lang(ar) {
                    margin: -6px 0;
                }
            }

            &-day {
                margin:5px 0;

                color:lighten($color-text, 55%);
                font-size:9px;
                font-weight:500;
                text-transform:uppercase;
            }

            &-time {
                margin:5px 0;
                font-size:13px;
                font-weight:bold;
                letter-spacing:0.22px;
            }

            &.-info{
                width: 600px;
                display: flex;
                justify-content: center;
            }
        }

        &-icon {
            display:block;
            margin:0 auto 18px auto;

            opacity:0.6;

            font-size:27px;
            line-height:1;

            transform:translateX(-102px);
        }

        &-text {
            position:relative;
            display:inline-block;
            max-width:85px;

            opacity:0.45;

            font-size:12px;
            font-weight:500;
            letter-spacing:0.2px;
            line-height:17px;

            transform:scale(0);

            &:lang(ar) {
                font-size:12px;
            }
            &.-block{
                display: block;
                margin: 10px auto 0;
                white-space: normal;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        &-pray{
            .rd-sidebar-item-hover-col{
                padding: 0 15px
            }
            .rd-sidebar-item-hover-title{
                margin-bottom: 5px;
            }
        }

        &-weather{
            .rd-sidebar-item-hover-col{
                padding: 0 26px
            }
        }

        @for $i from 1 through 6 {
            &:nth-child(#{$i}) &-icon {
                transition:transform 0.4s cubic-bezier(.62, .45, .16, .84) (0.10s + ($i * 0.08s));
            }

            &:nth-child(#{$i}) &-text {
                transition:transform 0.4s ease-in-out (0.20s + ($i * 0.08s));
            }
        }
        &:nth-child(1) .rd-sidebar-item-icon{
            margin-bottom: 7px;
        }

        &:after {
            content:"";
            display:block;
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            z-index:0;
            transform:scaleX(0);
            transform-origin:right center;
            transition:transform 0.2s 0.7s;

            background:$color-fill;
        }

        &:before {
            content:"";
            display:block;
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            transform:scaleX(0);
            transform-origin:right center;
            transition:all 0.2s;
            z-index:1;

            background:$color-fill;
        }

        @at-root a#{&} {
            &:after {
                transition-delay:0s;
            }
        }

        &:hover, &:focus {
            color:inherit;
            text-decoration:none;

            &:after {
                transform:scaleX(1);
                transition-delay:0s;
            }

            #{$this} {
                &-hover {
                    transform:scaleX(1);
                    transition-delay:0.1s;

                    &-col {
                        @extend .-visible;
                    }
                }
            }
        }

        &.-visible {
            #{$this} {
                &-icon {
                    transform:translateX(0px);
                }

                &-text {
                    transform:scale(1);
                }
            }
        }

        &.-active {
            background:none !important;
            color:$color-base;

            &:before {
                transform:scaleX(1);
            }

            #{$this} {
                &-icon, &-text {
                    opacity:1;
                }
            }
        }

        &.-inverse {
            color:$color-text;
        }
    }

    &-overlay {
        display:table;
        position:absolute;
        top:0;
        left:0;
        bottom:0;
        height:100%;
        width:100%;
        z-index:3;

        background:$color-base;

        transition:left 0.3s cubic-bezier(.62, .45, .16, .84), background 0.4s;

        &-inner {
            display:table-cell;
            vertical-align:middle;
        }

        &-logo {
            display:block;
            margin:-35px 0 0 0;

            color:$color-text;
            line-height:1;
            text-align:center;

            transition:opacity 0.2s;

            &-icon {
                display:block;
                color:$color-primary;
                transition:color 0.4s;

                svg {
                    height:42px;
                    width:42px;
                }
            }

            &-text {
                display:block;
                margin:18px 0 0 0;
                transform:rotate(-90deg);

                &-ar {
                    svg {
                        width:65px;
                        height:22px;
                    }
                }

                &-en {
                    svg {
                        width:65px;
                        height:15px;
                    }
                }
            }

            &:hover, &:focus {
                opacity:0.7;
                color:inherit;
                text-decoration:none;
            }

            &:active {
                opacity:0.6;
            }
        }

        &.-hidden {
            left:102px;
        }
    }

    &-loader {
        position:fixed;
        top:0;
        right:101px;
        bottom:0;
        height:100%;
        width:100%;
        transform:scaleX(0);
        transform-origin:right center;
        transition:transform 0.55s;

        &.-visible {
            transform:scaleX(1);
            transition-delay:0.1s;
        }
    }

    &.-inverse {
        background:$color-graybase;
        border-color:rgba($color-base, 0.2);
        color:$color-base;

        #{$root} {
            &-overlay {
                background:$color-graybase;

                &-logo {
                    color:$color-base;
                }
            }

            &-item {
                &:after {
                    background:rgba($color-base, 0.1);
                }
            }
        }
    }

    &.-active {
        #{$root} {
            &-overlay {
                @extend .-hidden;
            }

            &-item {
                @extend .-visible;
            }
        }
    }

    &.-loading {
        #{$root} {
            &-loader {
                @extend .-visible;
            }
        }
    }

    /* Colors */
    &.-primary {
        #{$root} {
            &-item {
                &:before {
                    background:$color-primary;
                }
            }

            &-overlay-logo-icon {
                color:$color-primary;
            }

            &-loader {
                background:$color-primary;
            }
        }

        &.-inverse #{$root} {
            &-item.-active {
                color:$color-text;
            }
        }
    }

    &.-secondary {
        #{$root} {
            &-item {
                &:before {
                    background:$color-secondary;
                }
            }

            &-overlay-logo-icon {
                color:$color-secondary;
            }

            &-loader {
                background:$color-secondary;
            }
        }

        &.-inverse #{$root} {
            &-item.-active {
                color:$color-text;
            }
        }
    }

    &.-tertiary {
        #{$root} {
            &-item {
                &:before {
                    background:$color-tertiary;
                }
            }

            &-overlay-logo-icon {
                color:$color-tertiary;
            }

            &-loader {
                background:$color-tertiary;
            }
        }
    }

    &.-quaternary {
        #{$root} {
            &-item {
                &:before {
                    background:$color-quaternary;
                }
            }

            &-overlay-logo-icon {
                color:$color-quaternary;
            }

            &-loader {
                background:$color-quaternary;
            }
        }
    }

    &.-quinary {
        #{$root} {
            &-item {
                &:before {
                    background:$color-quinary;
                }
            }

            &-overlay-logo-icon {
                color:$color-quinary;
            }

            &-loader {
                background:$color-quinary;
            }
        }
    }

    &.-senary {
        #{$root} {
            &-item {
                &:before {
                    background:$color-senary;
                }
            }

            &-overlay-logo-icon {
                color:$color-senary;
            }

            &-loader {
                background:$color-senary;
            }
        }
    }
}
