.angular-google-map-container {
    height: 100vh;
}

.-sg-visible {
    display: block;
    opacity: 1;
}

.jobs-modal-apply-backdrop {
    position: fixed;
    background: rgba(0, 0, 0, 0.5);
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: 0;
}

.rd-mapbox-control-item {
    z-index: 1;
}

.rd-objectlist-filter-item {
    margin: 0;
}

.rd-objectlist-filter-item-count {
    color: #fff;
}

.rd-objectlist-filter-item-container {
    margin: 0 10px;
}

.rd-article-map .angular-google-map-container {
    height: 300px;

    @media (min-width:$screen-xl-min) {
        height:350px;
    }
}

.rd-article-text img {
    max-width: 100%;
    cursor:pointer;
}

.rd-modal_active {
    display: block;
    opacity: 1;
}

.sg-modals-white {
    background: white;
}

.rd-form-group.-error .rd-form-textarea {
    border-color: red;
}

.rd-form-textarea img {
    max-width: 100%;
}
