@import "./variables";
.rd-select {

    /* Bootstrap Select Approach */
    .bootstrap-select, .bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
        width:100%;
    }

    .bootstrap-select {

        position: relative;

        .filter-option {
            width:initial!important;;

            &:lang(ar) {
                letter-spacing:0;
                text-align:right;
                float:right!important;
            }
        }

        .dropdown-toggle {
            @extend .rd-form-input;

            height: 50px;

            &:hover {
                background:none;
            }

            &:focus, &:active {
                box-shadow:none;
                outline:none !important;
            }
        }

        .dropdown-menu {
            top: 52px;
            left: 0;
            min-width: initial;
            width: 100%;
            background: $color-base;
            margin:0;
            padding: 0;
            border: none;
            border-radius: 0;

            display:block;
            transform-origin:top center;
            transform:scaleY(0);
            overflow:hidden;
            opacity:0;
            transition:all 0.2s ease-in-out;

            &:lang(ar) {
                text-align:right;
            }

            a {
                padding:3px 20px;
                outline: none !important;
                font-size: 13px;
                font-weight: 500;
                color: $color-text;
                letter-spacing: 0.6px;
                line-height: 35px;
                transition:all 0.15s;

                &:active {
                    outline: none !important;
                }

                &:hover {
                    color:#000;
                    padding-left:30px;

                    &:lang(ar) {
                        padding-left:initial;
                        padding-right:30px;
                    }
                }
            }
        }

        .caret {
            transform:rotate(0deg);
            transition:all 0.2s ease-in-out;

            &:lang(ar) {
                left: 12px;
                right:initial;
            }
        }

        &.open {
            .dropdown-toggle {
                box-shadow: none;
                border-color:lighten($color-text, 75%);
            }

            .dropdown-menu {
                opacity:1;
                transform:scaleY(1);
            }

            .caret {
                transform:rotate(-180deg);
            }
        }
    }

    &.-multiple {
        .bootstrap-select {
            .dropdown-toggle {
                .filter-option {
                    display:block;
                    float:none !important;
                    overflow:hidden;
                    white-space:nowrap;
                    text-overflow: ellipsis;
                    padding:0 20px 0 0;

                    &:lang(ar) {
                        padding:0 0 0 20px;
                    }
                }
            }

            .dropdown-menu {
                max-height:200px !important;
                overflow-y:auto;
            }

            .dropdown-menu.inner > li {
                a {
                    &:before {
                        content: '';

                        display:inline-block;
                        width:18px;
                        height:18px;

                        border:1px #D8D8D8 solid;

                        vertical-align:middle;

                        transition:all 0.2s;

                        .windows & {
                            padding:5px 0 0 0;
                        }
                    }
                    &:after{
                        content: '';
                        visibility: hidden;

                        display:inline-block;
                        width: 5px;
                        height: 9px;
                        transform: rotate(45deg);

                        border: 1px solid #93c252;
                        border-top: none;
                        border-left: 0;

                        position: absolute;
                        top: -1px;
                        bottom: 0;
                        left: 27px;
                        margin: auto;
                    }
                    &:lang(ar){
                        &:after{
                            left: calc(100% - 31px);
                        }
                    }

                    &:hover {
                        padding:3px 20px;
                    }

                    span {
                        display:inline-block;
                        margin:0 20px;
                        vertical-align:middle;

                        color:$color-disabled;
                    }
                }

                &.selected {
                    a {
                        &:after {
                            visibility: visible;
                        }
                    }
                }
            }
        }
    }

    &.-error {
        .dropdown-toggle {
            border:1px $color-error solid;

            &:hover, &:active, &:focus {
                border:1px $color-error solid;
            }
        }
    }
}
