.rd-navbar {
    $root:&;
    position:fixed;
    top:0;
    left:0;
    right:0;
    z-index:100;

    direction:ltr;
    white-space:nowrap;

    pointer-events:none;
    transition:padding 0.4s 0.2s, right 0.6s;

    @media (min-width:$screen-sm-min) {
        padding:10px 0 0 0;
    }

    &-outer {
        height:100%;
        padding-top:65px;

        @media (min-width:$screen-sm-min) {
            padding-top:78px;
        }

        &.-auto {
            height:auto;
        }
    }

    &-inner {
        position:relative;
        display:table;
        margin:15px 0 0 0;
        width:100%;
        z-index:2;

        @media (min-width:$screen-sm-min) {
            margin:20px 0 0 0;
        }
    }

    &-container {
        @extend .rd-container;
    }

    &-left {
        position:relative;
        display:table-cell;
        vertical-align:middle;
    }

    &-right {
        position:relative;
        display:table-cell;
        vertical-align:middle;
        text-align:right;
    }

    &-bg {
        position:absolute;
        left:0px;
        right:0;
        top:-78px;
        height:65px;
        z-index:1;
        pointer-events:auto;

        background:$color-fill;
        transition:top 0.4s cubic-bezier(.55, .49, .24, .92) 0.5s;

        @media (min-width:$screen-sm-min) {
            height:78px;
        }

        &.-visible {
            top:0;
            transition-delay:0s;
        }
    }

    &-expand {
        display:inline-block;
        vertical-align:middle;

        color:$color-text;
        pointer-events:auto;

        @media (min-width:$screen-lg-min) {
            display:none;
        }

        a {
            display:block;
            width:20px + 5px;
            height:16px + 10px;
            padding:5px 5px 5px 0;

            color:inherit;
            transition:opacity 0.2s;

            &:hover, &:focus {
                text-decoration:none;

                span {
                    width:10px;

                    &:nth-child(2) {
                        width:15px;
                    }

                    &:last-child {
                        width:100%;
                    }
                }
            }

            &:active {
                text-decoration:none;
            }
        }

        span {
            position:relative;
            display:block;
            height:2px;
            width:100%;
            margin-bottom:5px;
            top:0;

            background:currentColor;
            transition:transform 0.2s, top 0.2s 0.2s, opacity 0.2s 0.2s, width 0.2s;

            &:last-child {
                width:10px;
            }
        }

        &.-active {
            span {
                transition:width 0.2s, top 0.2s 0.2s, opacity 0.2s 0.2s, transform 0.2s 0.4s;
                width:100%;

                &:nth-child(1) {
                    top:7px;
                    transform:rotate(-45deg);
                    transform-origin:center center;
                }

                &:nth-child(2) {
                    opacity:0;
                }

                &:nth-child(3) {
                    top:-7px;
                    transform:rotate(45deg);
                    transform-origin:center center;
                }
            }
        }
    }

    &-nav {
        display:none;

        color:$color-text;
        vertical-align:middle;
        white-space:nowrap;

        pointer-events:auto;

        @media (min-width:$screen-lg-min) {
            display:inline-block;
            transition:color 0.4s;
        }

        &-link {
            padding:0;
            margin:0 -14px;
            text-align:left;

            @media (min-width:$screen-lg-min * 1.1) {
                margin:0 -17px;
            }

            &-item {
                position:relative;
                display:inline-block;
                padding:0 14px;

                color:inherit;
                font-size:17px;
                font-weight:bold;
                letter-spacing:1.66px;
                line-height:1;
                vertical-align:middle;

                transition:opacity 0.2s;

                @media (min-width:$screen-lg-min * 1.1) {
                    padding:0 17px;
                }

                &:hover, &:focus {
                    color:inherit;
                    opacity:0.8;
                    text-decoration:none;
                }

                &:active {
                    opacity:0.6;
                }

                &.-home, &.-twitter {
                    font-size:19px;
                }

                &.-lang {
                    font-size:14px;

                    svg {
                        display:inline-block;
                    }

                    span {
                        display:none;

                        font-family:$font-family;
                        font-size:16px;
                        font-weight:700;
                        letter-spacing:1.85px;
                    }

                    &:lang(ar) {
                        svg {
                            display:none;
                        }

                        span {
                            display:inline-block;
                            vertical-align:middle;
                        }
                    }
                }
            }
        }

        &.-inverse {
            @media (min-width:$screen-lg-min) {
                color:$color-base;
            }
        }
    }

    &-title {
        position:relative;
        display:inline-block;
        margin:0 0 0 17px;
        pointer-events:auto;
        z-index:-2;
        vertical-align:middle;

        color:$color-text;

        @media (min-width:$screen-lg-min) {
            margin:0 0 0 62px;
            transition:transform 0.4s ease-in-out;
            transform:translateY(-80px);
        }

        &-text {
            font-size:15px;
            font-weight:bold;
            letter-spacing:0.66px;
            line-height:1;

            transition:color 0.4s;

            @media (min-width:$screen-md-min) {
                font-size:20px;
                font-weight:bold;
                letter-spacing:0.31px;
            }
        }

        &.-inverse {
            color:$color-base;
        }

        &.-visible {
            color:$color-text;
            transform:none;

            @media (min-width:$screen-lg-min) {
                transition-delay:0.4s;
                transform:translateY(0px);
            }
        }
    }

    &-action {
        position:relative;
        display:inline-block;
        vertical-align:middle;
        pointer-events:auto;
        z-index:-2;

        &:lang(ar) {
            direction:rtl;
            text-align:right;
        }

        &-item {
            position:relative;
            display:inline-block;
            padding:0 25px 0 0;
            transform:translateY(-80px);
            vertical-align:middle;

            @media (min-width:$screen-sm-min) {
                padding:0 40px 0 0;
            }

            &:lang(ar) {
                padding:0 0 0 25px;

                @media (min-width:$screen-sm-min) {
                    padding:0 0 0 40px;
                }
            }

            @for $i from 1 through 4 {
                &:nth-child(#{$i}) {
                    transition:all 0.2s, transform 0.5s (0.15s + ($i * 0.1s));
                }
            }
            &:last-child {
                padding:0;
            }

            &-toggle {
                color:inherit;
                font-size:15px;
                font-weight:500;
                letter-spacing:0.88px;
                line-height:1;

                &:lang(ar) {
                    letter-spacing:0;
                }

                &:hover, &:focus {
                    color:inherit;
                    opacity:.8;
                    text-decoration:none;
                }

                &:active {
                    opacity:1;
                }
            }

            &-icon {
                font-size:19px;
                line-height:1;

                @media (min-width:$screen-sm-min) {
                    display:none;
                }

                &.-inverse {
                    display:none;
                    font-size:12px;
                    margin:0 0 0 12px;
                    vertical-align:top;

                    @media (min-width:$screen-sm-min) {
                        display:inline-block;
                        vertical-align:middle;
                    }

                    &:lang(ar) {
                        margin:0 12px 0 0;
                    }
                }
            }

            &-text {
                display:none;

                @media (min-width:$screen-sm-min) {
                    display:inline;
                }

                svg {
                    font-size:11px;
                }
            }
        }

        &.-visible &-item {
            transform:translateY(0);
        }
    }

    &-logo {
        $this:&;

        display:inline-block;
        position:absolute;
        top:-3px;
        right:0;
        max-width:0px;
        line-height:1;
        overflow:hidden;
        vertical-align:middle;
        pointer-events:auto;
        z-index:-2;

        color:$color-text;

        transition:opacity 0.2s, max-width 0.1s 0.3s, margin 0.1s 0.2s, top 0.3s 0.7s, transform 0.3s 0.4s;

        @media (min-width:$screen-md-min) {
            position:relative;
            top:0 !important;
        }

        &-text {
            display:none;
            margin:0 5px 0 0;
            vertical-align:middle;

            transition:color 0.3s 0.3s;

            @media (min-width:$screen-sm-min) {
                display:inline-block;
            }

            &-ar {
                display:block;
                opacity:0;

                transition:opacity 0.3s, transform 0.4s;
                transform:translateX(10px);

                svg {
                    width:54px;
                    height:19px;
                    transition:width 0.4s, height 0.4s;
                }
            }

            &-en {
                display:block;
                opacity:0;
                transition:opacity 0.3s 0.1s, transform 0.4s 0.1s;
                transform:translateX(10px);

                svg {
                    width:54px;
                    height:12px;
                    transition:width 0.4s, height 0.4s;
                }
            }
        }

        &-beta {
            margin:3px 0 0 0;
            max-height:25px;

            font-size:11px;
            font-weight:bold;
            letter-spacing:0.79px;
            line-height:13px;

            transition:max-height 0.4s 0.4s, opacity 0.3s 0.4s;

            &:lang(ar) {
                margin:0;
                letter-spacing:0;
                line-height:18px;
            }

            &-ar {
                display:none;

                &:lang(ar) {
                    display:block;
                }
            }

            &-en {
                text-transform:uppercase;

                &:lang(ar) {
                    display:none;
                }
            }
        }

        &-icon {
            display:inline-block;
            vertical-align:middle;

            transition:color 0.3s 0.3s;

            svg {
                height:33px;
                width:33px;

                @media (min-width:$screen-sm-min) {
                    height:35px;
                    width:35px;
                }

                border-radius:50%;
                box-shadow:inset 0 0 0 18px;
                transform:scale(0);

                transition:transform 0.4s cubic-bezier(.77, .33, .17, .88), box-shadow 0.6s 0.2s;
            }
        }

        &:hover, &:focus {
            opacity:0.8;
            text-decoration:none;
        }

        &:active {
            opacity:0.7;
        }

        &.-visible {
            @media (min-width:$screen-md-min) {
                max-width:100px;
                margin:0 0 0 70px;
            }

            #{$this} {
                &-text {
                    &-ar {
                        opacity:1;
                        transform:translateX(0);
                        transition-delay:0.4s, 0.4s;
                    }

                    &-en {
                        opacity:1;
                        transform:translateX(0);
                        transition-delay:0.5s, 0.5s;
                    }
                }

                &-icon svg {
                    box-shadow:inset 0 0 0 0;
                    transform:scale(1);
                }
            }
        }

        &.-force {
            @media (max-width:$screen-sm-max) {
                max-width:100px;
            }

            transform:scale(1.3);

            #{$this} {
                &-text {
                    &-ar {
                        @media (max-width:$screen-sm-max) {
                            opacity:1;
                            transform:translateX(0);
                            transition-delay:0.4s, 0.4s;
                        }
                    }

                    &-en {
                        @media (max-width:$screen-sm-max) {
                            opacity:1;
                            transform:translateX(0);
                            transition-delay:0.5s, 0.5s;
                        }
                    }
                }
            }
        }

        &.-inverse {
            color:$color-base;
        }

        &.-compact {
            color:$color-text;
            top:-100px;
            transition-delay:0s, 0.2s, 0.2s, 0s, 0s;
            transform:scale(1);

            #{$this} {
                &-beta {
                    opacity:0;
                    max-height:0px;
                    transition-delay:0s, 0s;
                }
            }
        }
    }

    &.-modal {
        display:none;
        position:absolute;

        @media (min-width:$screen-md-max) {
            display:block;
        }
    }

    &.-toolbar {
        #{$root} {
            &-action {

                &-item {
                    transform:translateY(0);

                    @media (min-width:$screen-md-min) {
                        transform:translateY(-80px);
                    }
                }
            }
        }
    }

    &.-fixed {
        padding-top:0;

        #{$root} {
            &-bg {
                @extend .-visible;
            }

            &-nav {
                color:$color-text;
            }

            &-title {
                @extend .-visible;
            }

            &-action {
                @extend .-visible;
            }

            &-logo {
                @extend .-compact;
            }
        }
    }

    &.-offseted {
        @media (min-width:$screen-md-min) {
            right:102px;
        }
    }

    &.-half {
        @media (min-width:$screen-md-min) {
            right:50%;
        }

        &.-offseted {
            @media (min-width:$screen-md-min) {
                right:calc(50% + 102px / 2);
            }
        }
    }

    &.-indent {
        @extend .-half;

        @media (min-width:$screen-xl-min) {
            right:((100% / 6) * 2);
        }

        &.-offseted {
            @media (min-width:$screen-xl-min) {
                right:calc(((100% / 6) * 2) + 51px);
            }
        }
    }

    /* Colors */
    &.-primary {
        #{$root} {
            &-expand {
                color:$color-primary;
            }

            &-logo-icon {
                color:$color-primary;
            }
        }
    }

    &.-secondary {
        #{$root} {
            &-expand {
                color:$color-secondary;
            }

            &-logo-icon {
                color:$color-secondary;
            }
        }
    }

    &.-tertiary {
        #{$root} {
            &-expand {
                color:$color-tertiary;
            }

            &-logo-icon {
                color:$color-tertiary;
            }
        }
    }

    &.-quaternary {
        #{$root} {
            &-expand {
                color:$color-quaternary;
            }

            &-logo-icon {
                color:$color-quaternary;
            }
        }
    }

    &.-quinary {
        #{$root} {
            &-expand {
                color:$color-quinary;
            }

            &-logo-icon {
                color:$color-quinary;
            }
        }
    }

    &.-senary {
        #{$root} {
            &-expand {
                color:$color-senary;
            }

            &-logo-icon {
                color:$color-senary;
            }
        }
    }

    &.-base {
        #{$root} {
            &-expand {
                color:$color-base;
            }

            &-logo-icon {
                color:$color-base;
            }
        }
    }
}
