.rd-badge {
    display:inline;

    &_count {
        display:inline-block;
        height:30px;
        width:30px;

        font-size:13px;
        font-weight:bold;
        letter-spacing:0;
        line-height:30px;
        text-align:center;
        vertical-align:middle;

        color:$color-base;

        &.-responsive {
            @media (min-width:$screen-xs-min) {
                height:34px;
                width:34px;

                font-size:16px;
                line-height:34px;
            }

            @media (min-width:$screen-sm-min) {
                height:38px;
                width:38px;

                font-size:16px;
                line-height:38px;
            }
        }

        &.-inverse {
            color:$color-text;
        }

        /* Colors */
        &.-primary {
            background:$color-primary;
        }

        &.-secondary {
            background:$color-secondary;
        }

        &.-tertiary {
            background:$color-tertiary;
        }

        &.-quaternary {
            background:$color-quaternary;
        }

        &.-quinary {
            background:$color-quinary;
        }

        &.-senary {
            background:$color-senary;
        }
    }

    &_photo {
        display:inline-block;
        position:relative;
        width:76px;
        height:76px;

        border-radius:50%;
        background-position:center center;
        background-size:cover;

        transition:all 0.2s;

        &-count {
            display:inline-block;
            position:absolute;
            height:100%;
            width:100%;
            top:0;
            left:0;
            right:0;
            bottom:0;

            color:$color-base;
            background:rgba($color-text, 0.3);
            border-radius:50%;

            font-size:17px;
            font-weight:bold;
            letter-spacing:1.96px;
            line-height:76px;
            text-align:center;
        }

        @at-root a#{&} {
            &:hover, &:focus {
                opacity:0.8;
            }

            &:active {
                opacity:0.75;
            }
        }

        &.-responsive {
            width:56px;
            height:56px;

            @media (min-width:$screen-sm-min) {
                width:76px;
                height:76px;
            }
        }

        &.-responsive &-count {
            line-height:56px;

            @media (min-width:$screen-sm-min) {
                line-height:76px;
            }
        }
    }

}
