.rd-datepicker {
    display:inline;
    position:relative;

    &-icon {
        display:inline-block;
        position:absolute;
        right:15px;
        top:0;

        &:lang(ar) {
            right:initial;
            left:15px;
            top:0px;
        }
    }

    &-input {
        input {
            position:absolute;
            left:0;
            top:0;
            width:0;
            height:0;
            overflow:hidden;
            z-index:-99999;

            background:$color-fill;
            border:none;
        }
    }

    &-change {
        display:table-cell;
        text-align:right;

        &:lang(ar) {
            text-align:left;
        }

        &-item {
            display:inline-block;
            vertical-align:middle;
            margin:0 0 0 15px;

            font-size:11px;
            letter-spacing:0;
            line-height:13px;
            text-transform:uppercase;
            font-weight:700;

            color:rgba($color-text, 0.3);

            &:hover {
                text-decoration:none;
                color:rgba($color-text, 0.3);
                opacity:0.8;
            }

            &:lang(ar) {
                margin:0 15px 0 0;
            }

            &.-active {
                color:$color-tertiary;
            }
        }
    }

    &-toggle {
        display:inline;

        &-icon {
            display:inline-block;
            margin:0 5px;

            font-size:11px;
            line-height:1;
            vertical-align:middle;

            transition:transform 0.2s;
        }
    }

    /* Calendars Approach */
    .calendars {
        width:auto !important;
        text-align:left;

        &:lang(ar) {
            direction:rtl;
        }

        &-popup {
            top:auto !important;
            left:0 !important;
            right:0 !important;
            min-width:190px;
            z-index:100;
            padding:15px;

            background:$color-base;
            border:1px #d9d9d9 solid;
            border-top:0;

            &:lang(ar) {
                right:auto !important;
                left:-100px !important;
            }
        }

        &-ctrl {
            display:none;
            margin:13px 0 0 0;
        }

        &-nav {
            display:table;
            width:100%;
        }

        &-cmd {
            display:table-cell;
            vertical-align:middle;

            color:inherit;

            font-size:11px;
            font-weight:bold;
            letter-spacing:0.86px;
            line-height:1;

            transition:color 0.2s;

            &:lang(ar) {
                text-align:right;
            }

            &:hover, &:focus {
                color:$color-primary;
                text-decoration:none;
            }

            &-prev {
                text-align:left;

                &:lang(ar) {
                    text-align:right;
                }
            }

            &-today {
                font-size:14px;
                text-align:center;

            }

            &-next {
                text-align:right;

                &:lang(ar) {
                    text-align:left;
                }
            }

            &-close {
                display:none;
            }
        }

        &-month {
            margin:0 -6px;

            &-header {
                margin:15px 6px;
            }

            &-year {
                padding:0 20px 0 0;
                max-width:90px;

                appearance:none;
                cursor:pointer;
                background:none;
                border:0;
                outline:0;

                font-size:13px;
                font-weight:bold;

                &:lang(ar) {
                    padding:0 0 0 20px;
                    max-width:100px;
                }

                &::-ms-expand {
                    display:none;
                }

                &:hover, &:focus {
                    opacity:0.9;
                }
            }

            &-arr {
                display:inline-block;
                font-size:7px;
                line-height:1;
                vertical-align:middle;
                pointer-events:none;
                transform:translateX(-10px);

                &:lang(ar) {
                    transform:translateX(10px);
                }
            }

            table {
                padding:0;
                width:100%;

                th, td {
                    font-size:13px;
                    letter-spacing:0;
                    text-align:center;
                }

                th {
                    font-weight:500;
                }

                a, span {
                    display:block;
                    padding:4px;
                    width:100%;
                    color:inherit;
                }

                a {
                    &:hover, &:focus {
                        color:inherit;
                        background:rgba(0, 0, 0, 0.1);
                        text-decoration:none;
                    }
                }

                span {
                    color:$color-smooth;
                }
            }
        }

        &-today {
            background-color: #d2d2d2!important;
        }

        &-disabled {
            color:$color-smooth !important;
            pointer-events:none;
        }

        &-selected {
            color:$color-base !important;
            background:$color-primary !important;
        }
    }

    &.-open &-toggle-icon {
        transform:rotate(180deg);
    }

    &.-lg {
        @media (min-width:$screen-sm-min) {
            .calendars {
                &-popup {
                    min-width:348px;
                    padding:33px;
                }

                &-month {
                    margin:0 -11px;

                    &-header {
                        margin:25px 11px;
                    }

                    &-year {
                        font-size:14px;
                    }

                    table {
                        a, span {
                            padding:11px;
                        }
                    }
                }
            }
        }
    }

    &.-box {
        @extend .-lg;

        .calendars {
            &-popup {
                margin:17px 0;
                border:0;
                box-shadow:0 6px 12px rgba(0, 0, 0, .175);

                &:lang(en) {
                    left:auto !important;
                }
            }
        }
    }

    &.-range {
        .calendars {
            &-ctrl {
                width:100%;
                display:table;
            }
        }
    }
}