.rd-loader {
    display:inline;

    &_overlay {
        position:fixed;
        top:0;
        right:0;
        bottom:0;
        height:100%;
        width:0%;
        z-index:99;
        overflow:hidden;

        color:$color-primary;
        background:$color-base;

        transition:all 0.5s cubic-bezier(.8, .57, .4, .92) 0.3s;

        &-inner {
            position:absolute;
            top:50%;
            left:0;
            right:0;
            margin: -43px 0 0 -86px;
            text-align:center;
        }

        &-logo {
            position:relative;
            opacity:0;
            transition:all 0.2s;

            svg {
                position:absolute;
                color:currentColor;
                width:82px;
                height:82px;
            }

            &-circle {
                svg {
                    animation:rd-loader-rotation 0.7s linear infinite;
                    transform-origin:center center;

                    circle {
                        fill:none;
                        stroke:currentColor;
                        stroke-width:3px;
                        stroke-dasharray:122.46px;
                        stroke-dashoffset:122.46px;
                        animation:rd-loader-filling 2s ease-in-out infinite;
                    }
                }
            }

            &-text {
                svg {
                    path {
                        fill:currentColor;
                    }
                }
            }
        }

        /* Colors */
        &.-primary {
            color:$color-primary;
        }

        &.-secondary {
            color:$color-secondary;
        }

        &.-tertiary {
            color:$color-tertiary;
        }

        &.-quaternary {
            color:$color-quaternary;
        }

        &.-quinary {
            color:$color-quinary;
        }

        &.-senary {
            color:$color-senary;
        }

        &.-inverse {
            color:$color-base;
        }

        &.-modal {
            z-index:106;
        }

        &.-front {
            z-index:99999;
        }

        &.-visible {
            left:0;
            width:100%;
            transition-delay:0s;
        }

        &.-visible &-logo {
            opacity:1;
            transition-delay:0.4s;
        }
    }

    &_inline {
        position:relative;
        width:35px;
        height:35px;
        margin:0 auto;

        color:$color-text;
        border:3px solid;
        border-radius:50%;
        border-top-color:transparent;
        border-left-color:transparent;

        animation:rd-loader-rotation 0.7s linear infinite;

        /* Colors */
        &.-primary {
            color:$color-primary;
        }

        &.-secondary {
            color:$color-secondary;
        }

        &.-tertiary {
            color:$color-tertiary;
        }

        &.-quaternary {
            color:$color-quaternary;
        }

        &.-quinary {
            color:$color-quinary;
        }

        &.-senary {
            color:$color-senary;
        }

        &.-inverse {
            color:$color-base;
        }
    }

    &_inside {
        position:relative;
        width:31px;
        height:31px;
        margin:0 auto;

        color:$color-base;
        border:2px solid $color-base;
        border-radius:50%;
        border-right-color:transparent;

        animation:rd-loader-rotation 0.7s linear infinite;
    }

    &_top {
        position:fixed;
        top:0;
        right:0;
        left:0;
        height:4px;
        z-index:99999;
        pointer-events:none;
        transform:scaleX(1);
        transform-origin:left center;

        opacity:0;
        background:$color-text;
        box-shadow:0 0 10px rgba($color-text, 0.2);

        transition:all 0.4s 0.2s;

        /* Colors */
        &.-primary {
            background:$color-primary;
            box-shadow:rgba($color-primary, 0.5);
        }

        &.-secondary {
            background:$color-secondary;
            box-shadow:rgba($color-secondary, 0.5);
        }

        &.-tertiary {
            background:$color-tertiary;
            box-shadow:rgba($color-tertiary, 0.5);
        }

        &.-quaternary {
            background:$color-quaternary;
            box-shadow:rgba($color-quaternary, 0.5);
        }

        &.-quinary {
            background:$color-quinary;
            box-shadow:rgba($color-quinary, 0.5);
        }

        &.-senary {
            background:$color-senary;
            box-shadow:rgba($color-senary, 0.5);
        }

        &.-inverse {
            background:$color-base;
            box-shadow:rgba($color-base, 0.5);
        }

        &.-visible {
            opacity:1;
            animation:rd-loader-progress 20s ease-out both;
        }
    }
}

@keyframes rd-loader-rotation {
    0% {
        transform:rotate(0deg);
    }

    100% {
        transform:rotate(360deg);
    }
}

@keyframes rd-loader-progress {
    0%, 1% {
        transform:scaleX(0);
    }

    5% {
        transform:scaleX(0.2);
    }

    10% {
        transform:scaleX(0.25);
    }

    15% {
        transform:scaleX(0.3);
    }

    60% {
        transform:scaleX(0.8);
    }

    100% {
        transform:scaleX(0.9);
    }
}

@keyframes rd-loader-progress-out {
    to {
        transform:scaleX(1);
    }
}

@keyframes rd-loader-filling {
    $offset:122.46px;
    0%, 100% {
        stroke-dashoffset:$offset;
    }
    50% {
        stroke-dashoffset:$offset/4;
    }
}
