.rd-tourism {

    &:lang(ar) {
        direction:rtl;
    }

    width:100%;
    height:100%;

    &-inner {
        width:100%;
        height:100%;
    }

    &-fill {
        width:100%;
        height:100%;
        background:$color-quaternary;
        position: absolute;
        left:1px;
        bottom:0;
        z-index: 1;

        animation: rd-tourism-fill-animation 1.2s cubic-bezier(1,0,0,0) forwards;
    }

    &-row {
        display:block;
        position:relative;
        width:100%;
        height:33.3333%;
        float:left;
        overflow:hidden;

        @media (min-width:$screen-md-min) {
            width:33.3333%;
            height:100%;
        }
    }

    &-bg {
        width:100%;
        height:100%;
        position:relative;
        z-index:-1;

        background-size: cover;
        background-position: center center;

        animation: rd-tourism-bg-animation 1.25s ease-in-out forwards;
    }

    &-overlay {
        position: absolute;
        left:0;
        right:0;
        bottom:0;
        height:35%;
        background: linear-gradient(to top,rgba(0,0,0, .7),transparent);
    }

    &-content {
        position:absolute;
        top:0;
        right:0;
        bottom:0;
        left:0;
        width:100%;
        height:100%;

        &-inner {
            position:absolute;
            left:20px;
            bottom:30px;

            max-width: 311px;

            @media (min-width:$screen-sm-min) {
                left:40px;
                bottom:30px;
            }
            @media (min-width:$screen-sm-min) {
                left:40px;
                bottom:50px;
            }
            @media (min-width:$screen-md-min) {
                margin: 0 0 60px 0;
            }
            @media (min-width:$screen-xl-min) {
                left:60px;
                bottom:50px;
            }

            &:lang(ar) {
                left:initial;
                right:20px;

                max-width:371px;

                @media (min-width:$screen-sm-min) {
                    right:40px;
                }
                @media (min-width:$screen-sm-min) {
                    right:40px;
                }
                @media (min-width:$screen-xl-min) {
                    right:60px;
                }
            }
        }

        &-rating {
            margin-bottom:10px;
            position: relative;

            transform:translateY(90px);
            transition:.4s ease-in-out;

            animation: rd-tourism-text-animation .7s cubic-bezier(1,0,0,.1) forwards;
            animation-delay: .1s;
        }

        &-title {
            font-size:26px;
            line-height:30px;
            font-weight:900;
            color:$color-base;

            margin-bottom:10px;
            position: relative;

            transform:translateY(90px);
            transition:.3s ease-in-out;

            animation: rd-tourism-text-animation .8s cubic-bezier(1,0,0,.1) forwards;
            animation-delay: .2s;

            @media (min-width:$screen-sm-min) {
                font-size:40px;
                line-height:47px;
            }
            @media (min-width:$screen-xl-min) {
                font-size:60px;
                line-height:67px;
            }
        }

        &-description {
            height:90px;
            font-size:16px;
            line-height:27px;
            color:$color-base;

            transform:translateY(90px);
            opacity:0;

            transition:.3s ease-in-out;

            max-width:311px;
        }

        &:hover {
            text-decoration: none;
        }

        &:hover &-rating {
            transform:translateY(0px);
            transition:.3s ease-in-out;
        }
        &:hover &-title {
            transform:translateY(0px);
            transition:.3s ease-in-out 0.1s;
        }
        &:hover &-description {
            opacity:1;
            transform:translateY(0px);
            transition:.3s ease-out 0.2s;
        }
    }
}

@keyframes rd-tourism-fill-animation {
    from {
        transform:translateX(0);
    }
    to {
        transform:translateX(100%);
    }
}
@keyframes rd-tourism-bg-animation {
    from {
        transform:translateX(-100%);
    }
    to {
        transform:translateX(0);
    }
}
@keyframes rd-tourism-text-animation {
    from {
        left:-100%;
        opacity:0;
    }
    to {
        left:0;
        opacity:1;
    }
}
