.rd-dropdown {
    display:inline;
    position:relative;

    &_rounded {
        position:relative;

        &:lang(ar) {
            direction:rtl;
        }

        &-toggle {
            position:relative;
            margin:0 -20px;
            padding:0 10px;
            z-index:3;

            button {
                background:$color-text !important;
            }

            svg {
                transition:all 0.3s;
                transform-origin:center center;
            }
        }

        &-menu {
            position:absolute;
            width:245px;
            left:-20px;
            right:-20px;
            bottom:-10px;
            padding:0 0 50px 0;
            z-index:2;

            background:$color-text;

            transition:all 0.2s;

            opacity:0;
            transform:scale(0);
            transform-origin:20% 95%;

            &-inner {
                max-height:250px;
                padding:15px 0;
                overflow-y:auto;

                &::-webkit-scrollbar {
                    background:none;
                    width:4px;
                }

                &::-webkit-scrollbar-thumb {
                    border:2px solid rgba(0, 0, 0, 0);
                    border-left-width:0;
                    border-top-width:17px;
                    border-bottom-width:17px;
                    background-color:rgba($color-fill, 0.2);
                    background-clip:padding-box;
                }
            }

            &-item {
                $this:&;

                display:block;

                cursor:pointer;
                color:darken($color-base, 50%);
                font-size:13px;
                font-weight:500;
                line-height:31px;
                opacity:0;

                transition:color 0.2s, opacity 0.4s;

                &-title {
                    display:block;
                    position:relative;
                    padding:2px 30px;
                    z-index:1;

                    svg {
                        position:absolute;
                        top:12px;
                        right:30px;
                        font-size:9px;

                        &:lang(ar) {
                            right:auto;
                            left:30px;
                            transform:rotate(180deg);
                        }
                    }

                    &:before {
                        content:"";
                        position:absolute;
                        display:block;
                        top:0;
                        left:0;
                        height:35px;
                        width:100%;
                        transform:scaleX(0);
                        transform-origin:right center;
                        z-index:-1;

                        background:$color-graybase;

                        transition:transform 0.4s;
                    }

                    &:lang(ar):before {
                        transform-origin:left center;
                    }
                }

                &-sub {
                    position:absolute;
                    width:245px;
                    top:0;
                    left:0;
                    right:0;
                    bottom:0;
                    z-index:-1;

                    opacity:0;
                    background:$color-graybase;

                    transition:transform 0.4s 0.2s, opacity 0.2s;

                    &-inner {
                        width:100%;
                        height:100%;
                        padding:15px 0;
                        overflow-y:auto;

                        &::-webkit-scrollbar {
                            background:none;
                            width:4px;
                        }

                        &::-webkit-scrollbar-thumb {
                            border:2px solid rgba(0, 0, 0, 0);
                            border-left-width:0;
                            border-top-width:17px;
                            border-bottom-width:17px;
                            background-color:rgba($color-fill, 0.2);
                            background-clip:padding-box;
                        }
                    }

                    &-item {
                        position:relative;
                        display:block;
                        padding:4px 30px;

                        color:darken($color-base, 50%);
                        font-size:13px;
                        font-weight:500;
                        line-height:31px;

                        transition:color 0.2s, opacity 0.4s;

                        &:hover, &:focus {
                            color:$color-base;
                            text-decoration:none;
                        }

                        &.-active {
                            color:$color-secondary;
                            text-decoration:none;
                        }
                    }

                    &.-visible {
                        opacity:1;
                        transition-delay:0s, 0.1s;
                        transform:translateX(55px);
                        z-index:2;

                        &:lang(ar) {
                            transform:translateX(-55px)
                        }

                        @media (min-width:$screen-sm-min) {
                            transform:translateX(245px);

                            &:lang(ar) {
                                transform:translateX(-245px);
                            }
                        }
                    }
                }

                &:hover, &:focus {
                    color:inherit;
                    text-decoration:none;

                    #{$this} {
                        &-title {
                            &:before {
                                transform:scaleX(1);
                                transform-origin:left center;
                            }

                            &:lang(ar):before {
                                transform-origin:right center;
                            }
                        }
                    }
                }

                &.-active {
                    color:$color-secondary;
                    text-decoration:none;
                }
            }
        }

        &.open &-toggle {
            svg {
                @media (min-width:$screen-sm-min){
                    transform:rotate(180deg);
                }
            }
        }

        &.open &-menu {
            opacity:1;
            transform:scale(1);
        }

        &.open &-menu-item {
            opacity:1;
            transition-delay:0s, 0.2s;
        }
    }

    &-toggle {
        display:inline;

        &-icon {
            display:inline-block;
            margin:0 5px;

            font-size:11px;
            line-height:1;
            vertical-align:middle;

            transition:transform 0.2s;
        }
    }

    &-menu {
        display:none;
        position:absolute;
        margin:17px -30px;
        padding:20px 30px;
        min-width:250px;
        max-height:250px;
        overflow:auto;
        z-index:1000;

        list-style:none;

        background:$color-base;
        border:none;
        border-radius:0;

        &:lang(ar) {
           // right:0;
        }

        &::-webkit-scrollbar {
            background:none;
            width:4px;
        }

        &::-webkit-scrollbar-thumb {
            border:2px solid rgba(0, 0, 0, 0);
            border-left-width:0;
            border-top-width:17px;
            border-bottom-width:17px;
            background-color:rgba($color-text, 0.2);
            background-clip:padding-box;
        }

        &.-right {
            right:0;

            &:lang(ar) {
                left:0;
            }
        }

        &.-aright {
            left:auto;
            right:0;

            @media (min-width:$screen-md-min) {
                right:auto;

                &:lang(ar) {
                    right:0;
                }
            }
        }

        &.-xs {
            min-width:97px;

            &:lang(ar) {
                right:-45px;
                left:initial;

                @media (min-width:$screen-md-min) {
                    right:0;
                }
            }
        }

        &.-sm {
            min-width:155px;
        }

        &.-social {
            margin-top:25px;
            min-width:165px;
            left:0;
            padding:20px 30px;

            box-shadow:0 0 12px rgba(0, 0, 0, .05);
        }

        > li {
            display:block;

            font-size:13px;
            font-weight:500;
            line-height:35px;
            text-align:left;

            color:$color-disabled;

            &:lang(ar) {
                text-align:right;
            }

            &.-checkbox {
                position:relative;

                label {
                    cursor:pointer;
                    height:100%;
                    padding:0;
                    margin:0;
                    font-style:normal;
                    font-weight:normal;

                    input {
                        position:absolute;
                        z-index:-999;
                        opacity:0;
                        visibility:hidden;
                    }

                    span {
                        &:after {
                            color:$color-text;
                        }
                    }

                    input:checked + span {

                        color:$color-text;

                        &:after {
                            opacity:1;
                        }
                    }
                }
            }

            &:hover {
                color:$color-text;
            }
        }
    }

    &.open &-menu {
        display:block;
        animation:rd-dropdown-show 0.2s;
    }

    &.open &-toggle-icon {
        transform:rotate(180deg);
    }
}

@keyframes rd-dropdown-show {
    0% {
        opacity:0;
        transform:translateY(-10px);
    }

    100% {
        opacity:1;
        transform:translateY(0);
    }
}
