.rd-intro {
    $root:&;

    position:relative;
    height:100%;
    width:100%;
    direction:ltr;

    background:$color-fill;

    &-wrap {
        width:100%;
        height:100%;
    }

    &-container {
        padding:0 20px;

        @media (min-width:$screen-sm-min) {
            padding:0 25px 0 45px;
        }
    }

    &-bg {
        position:absolute;
        top:0;
        left:-5%;
        right:0;
        bottom:0;
        width:110%;
        height:100%;
        z-index:1;

        &-photo {
            position:relative;
            width:100%;
            height:100%;
            z-index:1;

            background-size:cover;
            background-position:right center;
        }
    }

    &-overlay {
        position:absolute;

        &.-side {
            top:0;
            left:0;
            bottom:0;
            width:60%;
            height:100%;
            z-index:2;

            opacity:0.5;
            background:linear-gradient(to right, rgba(0, 0, 0, 1) 0%, transparent 100%);
        }

        &.-bottom {
            left:0;
            right:0;
            bottom:0;
            height:30%;
            z-index:2;

            opacity:0.7;
            background:linear-gradient(to top, rgba(0, 0, 0, 1) 0%, transparent 100%);
        }
    }

    &-nav {
        position:absolute;
        top:-100%;
        left:0;
        padding:40px 0 0 0;
        margin:0;
        z-index:4;

        transition:top 0.3s 0.2s;

        @media (min-width:$screen-md-min) {
            margin:0 0 0 25px;
        }

        &:lang(ar) {
            direction:rtl;
            left:auto;
            right:50%;
            margin:0 -4px;

            @media (min-width:$screen-lg-min) {
                right:67%;
            }
        }

        &-item {
            display:inline-block;
            position:relative;
            padding:0 2px 15px 2px;
            width:21px;

            color:rgba($color-base, 0.4);
            font-size:12px;
            font-weight:500;
            text-align:center;

            transition:color 0.2s;

            @media (min-width:$screen-sm-min) {
                padding:0 2px 20px 2px;
                width:38px;
                font-size:14px;
            }

            &:before {
                content:"";
                margin:0 2px;
                left:0;
                right:0;
                bottom:8px;
                position:absolute;
                display:block;
                height:2px;

                background:currentColor;

                transition:height 0.2s;
            }

            &:after {
                content:"";
                margin:0 2px;
                left:0;
                right:0;
                bottom:8px;
                position:absolute;
                display:block;
                height:2px;
                transform:scaleX(0);
                transform-origin:right center;

                background:$color-base;

                transition:transform 0.5s;
            }

            &:lang(ar):after {
                transform-origin:left center;
            }

            span {
                display:none;

                @media (min-width:$screen-sm-min) {
                    display:inline;
                }
            }

            &:hover, &:focus {
                color:lighten($color-text, 80%);
                text-decoration:none;

                &:before {
                    height:3px;

                    @media (min-width:$screen-sm-min) {
                        height:4px;
                    }
                }
            }

            &:active {
                color:$color-base;
            }

            &.-active {
                color:$color-base;
                cursor:default;

                &:before {
                    height:0px;
                }

                &:after {
                    transform:scaleX(1);
                    transition-duration:0s;
                }

                ~ a {
                    &:after {
                        transform-origin:left center !important;
                    }

                    &:lang(ar):after {
                        transform-origin:right center !important;
                    }
                }
            }
        }
    }

    &-more {
        position:absolute;
        z-index:4;
        bottom:25px;

        color:$color-base;

        &:lang(ar) {
            direction:rtl;
        }

        @media (min-width:$screen-sm-min) {
            bottom:55px;
        }

        @media (min-width:$screen-lg-min) {
            bottom:108px;
        }

        a {
            color:inherit;
            font-size:16px;
            font-weight:bold;
            letter-spacing:0.25px;
            line-height:1;

            @media (min-width:$screen-sm-min) {
                font-size:24px;
                letter-spacing:0.37px;
            }

            svg {
                position:relative;
                margin:0 10px 0 0;
                left:0;

                font-size:14px;
                vertical-align:middle;

                transition:left 0.3s;

                &:lang(ar) {
                    margin:0 0 0 10px;
                    direction:rtl;
                    transform:rotate(180deg);
                }

                @media (min-width:$screen-sm-min) {
                    margin:0 14px 0 0;
                }

                @media (min-width:$screen-lg-min) {
                    margin:0 28px 0 0;
                }
            }

            span {
                vertical-align:middle;
            }

            &:hover, &:focus {
                color:inherit;
                text-decoration:none;

                svg {
                    left:4px;
                }
            }
        }

        &.-offseted {
            @media (min-width:$screen-sm-min) {
                margin:0 0 0 30px;
            }

            @media (min-width:$screen-md-min) {
                margin:0 0 0 70px;
            }

            @media (min-width:$screen-lg-min) {
                margin:0 0 0 (100% / 6);
            }
        }
    }

    &-sub {
        position:relative;
        height:100%;
        width:100%;
        overflow:hidden;

        &-body {
            position:relative;
            z-index:5;

            &:lang(ar) {
                max-width:(100% / 6) * 3;

                @media (min-width:$screen-lg-min) {
                    max-width:(100% / 6) * 2;
                }
            }
        }

        &-item {
            position:absolute;
            top:0;
            left:0;
            bottom:0;
            right:0;
            display:none;
            height:100%;
            width:100%;

            &-inner {
                display:table-cell;
                padding:0 0 100px 0;
                vertical-align:middle;
            }

            &.-in, &.-out {
                display:table;
            }
        }

        &-item.-out &-body {
            z-index:4;
        }
    }

    &-header {
        color:$color-base;

        &:lang(ar) {
            direction:rtl;
        }

        @media (min-width:$screen-md-min) {
            margin:0 0 0 25px;
        }

        @media all and (max-width: 750px) {
            margin:40rem 0 0 0;
        }
        
        &:after {
            content:"";
            display:block;
            clear:both;
        }

        a, h1, h2, h3, h4 {
            color:inherit;
            font-size:26px;
            font-weight:500;
            letter-spacing:0;
            line-height:30px;
            white-space:nowrap;

            &:lang(ar) {
                font-size:18px;
                font-weight:bold;
            }

            span, div {
                position:relative;
                display:block;
                float:left;
                clear:left;

                &:lang(ar) {
                    float:right;
                    clear:right;
                }
            }

            @media (min-width:$screen-xs-min) {
                font-size:33px;
                line-height:36px;

                &:lang(ar) {
                    font-size:22px;
                }
            }

            @media (min-width:$screen-sm-min) {
                font-size:60px;
                line-height:67px;

                &:lang(ar) {
                    font-size:44px;
                    line-height:55px;
                }
            }
        }

        a {
            display:block;

            span {
                &:before {
                    position:absolute;
                    content:"";
                    display:block;
                    width:100%;
                    height:4px;
                    left:0;
                    right:0;
                    bottom:6px;
                    z-index:-1;
                    transform:scaleX(0);
                    transform-origin:right center;

                    background:$color-smooth;

                    transition:transform 0.3s;

                    @media (min-width:$screen-xs-min) {
                        height:6px;
                    }

                    @media (min-width:$screen-sm-min) {
                        height:12px;
                    }
                }

                &:lang(ar):before {
                    transform-origin:left center;
                }

                @for $i from 1 through 6 {
                    &:nth-child(#{$i}):before {
                        transition-delay:($i * 0.1s);
                    }
                }
            }

            &:hover, &:focus {
                span {
                    &:before {
                        transform:scaleX(1);
                        transform-origin:left center;
                    }

                    &:lang(ar):before {
                        transform-origin:right center;
                    }
                }
            }
        }
    }

    &-forecast {
        display:none;
        margin:0 30px 0 0;

        opacity:0.8;
        color:$color-base;

        font-size:13px;
        font-weight:500;
        line-height:1;

        @media (min-width:$screen-lg-min) {
            display:block;
            position:absolute;
            right:0;
            bottom:80px;
            z-index:4;
        }

        &:lang(ar) {
            font-size:13px;
            font-weight:bold;
        }

        &-time {
            display:inline-block;
            vertical-align:middle;

            &:lang(ar) {
                direction:rtl;
            }
        }

        &-weather {
            display:inline-block;
            margin:0 0 0 18px;
            vertical-align:middle;

            &-temp {
                display:inline-block;
                vertical-align:middle;
            }

            &-icon {
                display:inline-block;
                margin:0 0 0 10px;

                font-size:18px;
                vertical-align:middle;
            }
        }
    }

    /* Colors */
    &.-primary {
        #{$root} {
            &-header a span:before {
                background:$color-primary;
            }
        }
    }

    &.-secondary {
        #{$root} {
            &-header a span:before {
                background:$color-secondary;
            }
        }
    }

    &.-tertiary {
        #{$root} {
            &-header a span:before {
                background:$color-tertiary;
            }
        }
    }

    &.-quaternary {
        #{$root} {
            &-header a span:before {
                background:darken($color-quaternary, 10%);
            }
        }
    }

    &.-quinary {
        #{$root} {
            &-header a span:before {
                background:darken($color-quinary, 10%);
            }
        }
    }

    &.-senary {
        #{$root} {
            &-header a span:before {
                background:darken($color-senary, 10%);
            }
        }
    }
}
