.rd-form {

    &-group {
        margin-bottom:50px;
        position:relative;

        @media (max-width:$screen-sm-min) {
            margin-bottom:24px;
        }

        &:lang(ar) {
            text-align:right;
        }

        &.-lg {
            margin-bottom:50px;
        }

        &-row {
            margin-bottom:50px;

            @media (max-width:$screen-sm-min) {
                margin-bottom:24px;
            }

            &.-zero {
                margin-bottom:0;
            }
        }

        &-addon {
            position:absolute;

            &.-top {
                top:5px;
            }

            &.-left {
                left:0;
            }

            &.-right {
                right:0;

                &:lang(ar) {
                    right:initial;
                    left:0;
                }
            }

            &.-bottom {
                bottom:0;
            }
        }

        &.-indent {
            @media (min-width:$screen-md-min) {
                margin-bottom:0;
            }
        }

        &.-right {
            text-align:right;
        }

        &.-flat {
            margin-bottom:22px;
        }

        &.-lg {
            margin-bottom:40px;
        }

        &.-md {
            margin-bottom:30px;

            @media (min-width:$screen-sm-min) {
                margin-bottom:50px;
            }
        }

        &.-error {
            margin-bottom:40px;

            &.-indent {
                margin-bottom:68px;
            }
        }
    }

    &-submit {
        display:table;
        width:100%;
        margin:42px 0 0 0;

        @media (max-width:$screen-sm-min) {
            margin:24px 0 0 0;
        }

        &:lang(ar) {
            margin:38px 0 0 0;
            direction:ltr;
        }

        &-close {
            font-size:13px;
            letter-spacing:1.5px;
            font-weight:600;
            text-transform:uppercase;
            text-decoration:none;

            color:$color-text;

            margin-right:40px;
            transition:.2s;

            &:hover, &:focus, &:active {
                color:$color-text;
                text-decoration:none;
                opacity:.8;
            }

            @media (max-width:$screen-sm-min) {
                margin-right:30px;
            }

            &:lang(ar) {
                letter-spacing:0;
                line-height:24px;
            }
        }

        &.-flat {
            margin:30px 0 0 0;

            &:lang(ar) {
                margin:22px 0 0 0;
            }
        }

        &.-indent {
            margin:0;

            @media (max-width:$screen-sm-min) {
                margin:24px 0 0 0;
            }
        }

        &.-xs {
            margin:0;
        }

        &.-lg {
            button {
                @media (max-width:$screen-md-min) {
                    width:100%;
                }
            }
        }

        &-col {
            display:table-cell;
            vertical-align:middle;

            &.-right {
                text-align:right;
            }

            &.-left {
                @media (max-width:$screen-sm-min) {
                    text-align:left;
                }
            }

            &.-primary {
                &:lang(ar) {

                    width:50%;

                    @media (max-width:$screen-sm-min) {
                        text-align:left;
                    }
                }
            }
        }

        &.-right {
            text-align:right;
        }

        &.-sm {
            margin:20px 0 0 0;
        }
    }

    &-label {
        font-size:13px;
        font-weight:700;

        line-height:15px;
        letter-spacing:1.5px;
        text-transform:uppercase;
        color:lighten($color-text, 70%);

        margin-bottom:15px;

        &:lang(ar) {
            letter-spacing:0;
            line-height:24px;
        }
    }

    &-input {
        height:50px;
        width:100%;
        padding:10px;
        position:relative;

        color:$color-text;
        background:$color-base;
        border:1px lighten($color-text, 85%) solid;
        border-radius:0;

        font-family:$font-family;
        font-size:16px;
        letter-spacing:0px;
        line-height:27px;

        transition:all 0.2s;

        &:lang(ar) {
            letter-spacing:0.88px;
            text-align:right;
            direction:rtl;
            font-family:$font-family-tertiary;
        }

        &.-disabled {
            color:lighten($color-text, 80%);

            &:hover, &:active, &:focus {
                border:1px lighten($color-text, 90%) solid;
            }
        }

        &.-er {
            border:1px $color-error solid;

            &:hover, &:active, &:focus {
                border:1px $color-error solid;
            }
        }

        &.-right {
            text-align:right;
        }

        &.-time {
            width:120px;
            font-size:14px;
            font-weight:700;
            letter-spacing:.88px;
            padding-left:10px;
        }
        &.-calendar {
            width:196px;
            font-size:14px;
            font-weight:700;
            letter-spacing:.88px;
            padding-left:10px;
        }

        &.-search {

            font-size:20px;
            letter-spacing:.31px;
            line-height:24px;

            border:none;

            font-weight:400;

            &::-webkit-input-placeholder {
                color:rgba($color-text, 0.2);
                transition:all .3s;
            }
            &::-moz-placeholder {
                color:rgba($color-text, 0.2);
                transition:all .3s;
            }
            &:-moz-placeholder {
                color:rgba($color-text, 0.2);
                transition:all .3s;
            }
            &:-ms-input-placeholder {
                color:rgba($color-text, 0.2);
                transition:all .3s;
            }

            &.-lg {
                height:auto;
                font-size:30px;
                line-height:67px;
                font-weight:700;

                padding:10px 0;

                &::-webkit-input-placeholder {
                    text-align:center;

                    @media (min-width:$screen-sm-min) {
                        text-align:left;
                    }
                }
                &::-moz-placeholder {
                    text-align:center;

                    @media (min-width:$screen-sm-min) {
                        text-align:left;
                    }
                }
                &:-moz-placeholder {
                    text-align:center;

                    @media (min-width:$screen-sm-min) {
                        text-align:left;
                    }
                }
                &:-ms-input-placeholder {
                    text-align:center;

                    @media (min-width:$screen-sm-min) {
                        text-align:left;
                    }
                }

                @media (min-width:$screen-sm-min) {
                    font-size:60px;
                    line-height:67px;
                }

                @media (min-width:$screen-md-min) {
                    font-size:82px;
                    line-height:67px;
                }
            }
        }

        &-wrap {
            position:relative;
        }

        &-toggle {
            position:absolute;
            top:50%;
            right:20px;
            transform:translateY(-50%);

            &:lang(ar) {
                right:auto;
                left:20px;
            }

            font-size:11px;
            letter-spacing:1.27px;

            label {
                display:inline-block;
                position:relative;
                margin:0;
                padding:0;

                cursor:pointer;

                input {
                    position:absolute;
                    padding:0;
                    margin:0;
                    z-index:-999;
                    opacity:0;

                    &:checked + span {
                        color:$color-primary;
                    }
                }

                &:hover {
                    opacity:0.8;
                }
            }
        }

        &-error {
            font-size:13px;
            letter-spacing:1.5px;
            line-height:15px;
            color:$color-error;

            font-weight:500;

            position:absolute;
            margin:5px 0 0 0;
        }

        &-link {
            color:$color-base;
            font-size:16px;
            position:absolute;
            right:0;
            bottom:20px;
            transition:all .2s;
            letter-spacing:.57px;

            &:lang(ar) {
                left:0;
                right:initial;
            }

            @media (max-width:$screen-sm-min) {
                font-size:14px;
            }

            &:hover, &:active, &:focus {
                color:$color-base;
                text-decoration:none;
                opacity:.8;
            }
        }

        &-icon {
            position:absolute;
        }

        &:hover {
            border-color:lighten($color-text, 75%);
        }

        &:focus {
            border-color:lighten($color-text, 75%);
        }

        &::-webkit-input-placeholder {
            color:rgba($color-text, 0.5);
        }
        &::-moz-placeholder {
            color:rgba($color-text, 0.5);
        }
        &:-moz-placeholder {
            color:rgba($color-text, 0.5);
        }
        &:-ms-input-placeholder {
            color:rgba($color-text, 0.5);
        }

        &.-flat {
            max-width:400px;
            padding:14px 0;

            color:$color-base;
            background:none;
            border:0;
            border-bottom:1px rgba($color-base, 0.4) solid;

            font-size:20px;
            font-weight:normal;
            letter-spacing:0.3px;

            &:lang(ar) {
                padding:12px 0;
                letter-spacing:0px;
            }

            &.-error {
                border-bottom:1px solid $color-error !important;

                &:hover, &:active, &:focus {
                    border-bottom:1px solid $color-error !important;
                }
            }

            @media (max-width:$screen-sm-min) {
                font-size:16px;
            }

            &:hover {
                border-bottom:1px $color-base solid;
                color:$color-base;

                &::-webkit-input-placeholder {
                    color:$color-base
                }
                &::-moz-placeholder {
                    color:$color-base
                }
                &:-moz-placeholder {
                    color:$color-base
                }
                &:-ms-input-placeholder {
                    color:$color-base
                }
            }

            &:focus {
                border-bottom:1px $color-base solid;
            }

            &.-xs {
                padding:8px 0;
                font-size:15px;
                line-height:15px;
                letter-spacing:1px;

                &:lang(ar) {
                    letter-spacing:0px;
                }

                &::-webkit-input-placeholder {
                    font-size:15px;
                }
                &::-moz-placeholder {
                    font-size:15px;
                }
                &:-moz-placeholder {
                    font-size:15px;
                }
                &:-ms-input-placeholder {
                    font-size:15px;
                }
            }

            &.-sm {
                padding:12px 0;
                font-size:15px;
                letter-spacing:1px;
            }

            &.-spaced {
                padding-left:65px;
                padding-right:65px;
            }

            &::-webkit-input-placeholder {
                color:rgba($color-base, 0.5);
                transition:all .3s;
                font-size:20px;
            }
            &::-moz-placeholder {
                color:rgba($color-base, 0.5);
                transition:all .3s;
                font-size:20px;
            }
            &:-moz-placeholder {
                color:rgba($color-base, 0.5);
                transition:all .3s;
                font-size:20px;
            }
            &:-ms-input-placeholder {
                color:rgba($color-base, 0.5);
                transition:all .3s;
                font-size:20px;
            }

            &.-inverse {
                color:$color-text;
                border-color:rgba($color-text, 0.2);

                &:hover {
                    border-color:rgba($color-text, 0.3);
                }

                &:focus {
                    border-color:rgba($color-text, 0.5);
                }

                &::-webkit-input-placeholder {
                    color:rgba($color-text, 0.3);
                    transition:all .3s;
                    letter-spacing:0;
                }
                &::-moz-placeholder {
                    color:rgba($color-text, 0.3);
                    transition:all .3s;
                    letter-spacing:0;
                }
                &:-moz-placeholder {
                    color:rgba($color-text, 0.3);
                    transition:all .3s;
                    letter-spacing:0;
                }
                &:-ms-input-placeholder {
                    color:rgba($color-text, 0.3);
                    transition:all .3s;
                    letter-spacing:0;
                }
            }
        }
    }

    &-toolbar {

        &-item {
            cursor:pointer;
            color:$color-text;
            margin-bottom:15px;

            display:inline-block;
            border:none;
            background:none;

            position:relative;
            overflow:hidden;

            &:hover {
                opacity:.8;
            }

            &:lang(ar) {
                margin-left:initial;
            }

            &.-active {
                filter:drop-shadow(0 0 3px rgba(0, 0, 0, 0.4));
            }

            input {
                cursor:pointer;
                font-size:0;
                opacity:0;
                position:absolute;
                right:0;
                top:0;
                left:0;
                bottom:0;
            }
        }
    }

    &-text {
        color:$color-link;
        font-size:16px;
        font-weight:normal;
        letter-spacing:0.27px;
        line-height:23px;
    }

    &-fileinput {
        white-space:nowrap;

        &:lang(ar) {
            text-align:left;
        }

        &-button {
            position:relative;
            display:inline-block;

            padding:5px 22px;

            border:1px solid $color-text;

            font-size:13px;
            letter-spacing:1.5px;
            font-weight:700;
            line-height:43px;
            text-align:center;
            text-transform:uppercase;

            cursor:pointer;
            overflow:hidden;

            @media (max-width:$screen-sm-min) {
                display:inline-block;
            }

            &:lang(ar) {
                letter-spacing:0;
            }

            input {
                cursor:pointer;
                font-size:100px;
                filter:alpha(opacity=1);
                -moz-opacity:0.01;
                opacity:0.01;
                position:absolute;
                right:0;
                top:0;
            }

            &:hover {
                opacity:.8;
            }
        }

        &-box {
            display:none;

            @media (max-width:$screen-sm-min) {
                margin-left:18px;
            }

            &-name {
                display:inline-block;
                margin-left:32px;

                color:$color-text;
                font-size:16px;
                font-weight:300;
                letter-spacing:.57px;
                line-height:29px;
                vertical-align:middle;

                @media (max-width:$screen-sm-min) {
                    margin:0 auto;
                }
            }

            &-reset {
                display:inline-block;
                margin:0 0 0 14px;
                z-index:3;

                font-size:8px;
                color:$color-text;
                vertical-align:middle;

                &:hover {
                    opacity:.8;
                    color:$color-text;
                }
            }

            &.-visible {
                display:inline-block;
                vertical-align:middle;
            }
        }
    }

    &-tagsinput {

        &.-error {

            .bootstrap-tagsinput {
                border:1px $color-error solid;

                &:hover, &:active, &:focus {
                    border:1px $color-error solid;
                }
            }
        }

        .bootstrap-tagsinput {

            @extend .rd-form-input;

            padding:6px 7px 7px 7px;

            .tag {
                position:relative;
                display:inline-block;
                padding:3px 13px;

                font-size:13px;
                letter-spacing:0;
                color:$color-base;

                background:$color-primary;

                &:nth-last-child(2) {
                    &:after {
                        display:none;
                    }
                }

                &:lang(ar) {
                    direction:rtl;
                }

                span {
                    cursor:pointer;
                    display:block;
                    position:absolute;
                    top:0;
                    left:0;
                    right:0;
                    bottom:0;
                    width:100%;
                    height:100%;

                    background:$color-primary;
                    opacity:0;

                    &:hover {
                        opacity:0.1;
                    }
                }
            }

            input {
                max-width:100%;
                margin:4px 0;
                padding:0;

                border:0;
                background:none;

                font-size:16px;
                letter-spacing:0;
                vertical-align:baseline !important;

                &:lang(ar) {
                    font-family:$font-family-tertiary;
                }
            }

            &.focus {
                @extend .rd-form-input:focus;

                .tag {
                    background:lighten($color-primary, 5%);

                    &:nth-last-child(2) {
                        &:after {
                            display:inline;
                        }
                    }
                }
            }
        }

        &.-filled {
            input {
                &::-webkit-input-placeholder {
                    color:transparent;
                }
                &::-moz-placeholder {
                    color:transparent;
                }
                &:-moz-placeholder {
                    color:transparent;
                }
                &:-ms-input-placeholder {
                    color:transparent;
                }
            }
        }
    }

    &-imgdrop {
        text-align:center;

        &-inner {
            display:table-cell;
            vertical-align:middle;
        }

        &-upload {
            position:relative;
            display:table;
            width:100%;
            height:100%;
            height:210px;
            cursor:pointer;
            background:$color-fill;
            border:1px $color-fill solid;
            color:$color-smooth;
            transition:background 0.2s;

            &.-error {
                border:1px $color-error dashed;
                background:rgba($color-error, .02);
            }

            &-inner {
                display:table-cell;
                vertical-align:middle;
            }

            &-input {
                opacity:0;

                input {
                    width:100%;
                    height:100%;
                    cursor:pointer;
                    position:absolute;
                    top:0;
                    left:0;
                    right:0;
                    bottom:0;
                    opacity:0;
                }
            }

            &-icon {
                font-size:44px;
            }

            &-text {
                font-size:13px;
                font-weight:600;
                letter-spacing:1.5px;
                line-height:22px;
                text-transform:uppercase;

                margin-top:12px;

                span {
                    display:block;
                }

                &:lang(ar) {
                    margin-top:-10px;

                    font-weight:700;
                    letter-spacing:0;

                    span {
                        display:inline-block;
                    }
                }
            }

            &-link {
                color:$color-text;
                transition:.2s;

                &-text {
                    display:inline-block;
                    vertical-align:middle;
                    font-size:13px;
                    letter-spacing:1.5px;
                    line-height:15px;

                    font-weight:700;
                    text-transform:uppercase;

                    &:lang(ar) {
                        letter-spacing:0;
                    }
                }

                &:hover {
                    opacity:.8;
                    text-decoration:none;
                    color:$color-text;
                }
            }

            &:hover {
                background:darken($color-fill, 2%);
            }
        }

        &-preview {
            display:none;
            position:relative;
            height:100%;

            &-img {
                background-repeat:no-repeat;
                background-position:center center;
                background-size:cover;
                height:142px;
                width:143px;
                margin-bottom:15px;
                position:relative;
            }

            &-overlay {
                display:table;
                text-align:center;
                position:absolute;
                left:0;
                top:0;
                background:rgba(0, 0, 0, .6);

                width:100%;
                height:100%;
            }

            &-icon {
                display:table-cell;
                vertical-align:middle;
                font-size:33px;

                cursor:pointer;
            }

            &-number {
                background:$color-primary;
                position:absolute;
                right:0;
                bottom:0;
                width:24px;
                height:24px;

                color:$color-base;

                text-align:center;
                font-size:12px;
                line-height:24px;
                font-weight:600;
            }

            &-row {
                display:table;

                @media (max-width:$screen-sm-min) {
                    display:block;
                }
            }

            &-col {
                display:table-cell;
                vertical-align:middle;
                position:relative;

                @media (max-width:$screen-sm-min) {
                    display:block;
                }

                @media (min-width:$screen-sm-min) {
                    padding:0 30px 30px 0;

                    &:lang(ar) {
                        &:first-child {
                            padding:0 0 30px 0;
                        }
                    }
                }
            }

            &-link {
                display:block;

                font-size:11px;
                font-weight:600;
                letter-spacing:1.27px;
                line-height:21px;
                text-transform:uppercase;
                text-align:left;
                color:$color-text;

                &:hover {
                    opacity:.8;
                    color:$color-text;
                    text-decoration:none;
                }

                &:focus {
                    color:$color-text;
                    text-decoration:none;
                }

                &:lang(ar) {
                    letter-spacing:0;
                    text-align:right;
                }
            }

            &-add {
                text-align:left;
                max-width:159px;

                &:lang(ar) {
                    direction:initial;
                    text-align:right;
                }

                &-images, &-video {
                    display:inline-block;
                    width:159px;
                    height:43px;
                    border:1px solid $color-text;

                    font-size:13px;
                    font-weight:600;
                    letter-spacing:1.5px;
                    line-height:43px;
                    color:$color-text;
                    text-align:center;
                    text-transform:uppercase;

                    vertical-align:middle;

                    cursor:pointer;

                    &:hover {
                        opacity:.8;
                        border:1px solid rgba($color-text, .8);
                    }

                    &:lang(ar) {
                        letter-spacing:0;
                    }
                }

                &-video {
                    width:53px;
                    height:43px;

                    margin-left:15px;

                    &-icon {
                        vertical-align:middle;
                        display:inline-block;
                        font-size:24px;
                        line-height:17px;

                        margin-left:10px;

                        &:lang(ar) {
                            margin-left:initial;
                            margin-right:10px;
                        }
                    }
                }
            }
        }

        &.-error &-upload {
            border:1px $color-error dashed;
            color:$color-error;
        }

        &.-dragover &-upload {
            background:darken($color-fill, 2%);
            border:1px $color-disabled dashed;
            color:$color-smooth;
        }

        &.-loaded &-upload {
            display:none;
        }

        &.-loaded &-preview {
            display:block;
        }
    }

    &-textarea {
        display:block;
        height:160px;
        width:100%;
        padding:10px;

        color:$color-text;
        background:$color-base;
        border:1px lighten($color-text, 85%) solid;

        font-size:16px;
        font-weight:400;
        line-height:27px;
        text-align:left;

        resize:none;
        overflow-y:auto;

        transition:all 0.2s;

        &:lang(ar) {
            text-align:right;
        }

        &:focus {
            border-color:lighten($color-text, 75%);
        }

        &:hover {
            border-color:lighten($color-text, 75%);
        }

        @mixin placeholder {
            color:lighten($color-text, 80%);
            transition:all 0.2s;
        }

        &.-error {
            border:1px $color-error solid;

            &:hover, &:active, &:focus {
                border:1px $color-error solid;
            }
        }

        &.-placeholder {
            @include placeholder;
        }

        &::-webkit-input-placeholder {
            @include placeholder;
        }
        &::-moz-placeholder {
            @include placeholder;
        }
        &:-moz-placeholder {
            @include placeholder;
        }
        &:-ms-input-placeholder {
            @include placeholder;
        }

        &.-lg {
            height:261px;
        }

        &.-flat {
            border:0;
            height:128px;
            padding:15px 90px 19px 20px;
            overflow:hidden;

            background:$color-fill;
            font-size:14px;
            line-height:normal;

            &:lang(ar) {
                padding:15px 20px 19px 90px;
            }

            &:hover, &:focus {
                &::-webkit-input-placeholder {
                    color:lighten($color-text, 70%);
                }
                &::-moz-placeholder {
                    color:lighten($color-text, 70%);
                }
                &:-moz-placeholder {
                    color:lighten($color-text, 70%);
                }
                &:-ms-input-placeholder {
                    color:lighten($color-text, 70%);
                }
            }

            &.-inverse {
                color:$color-base;
                background:#202020;

                &::-webkit-input-placeholder {
                    color:lighten($color-text, 50%);
                }
                &::-moz-placeholder {
                    color:lighten($color-text, 50%);
                }
                &:-moz-placeholder {
                    color:lighten($color-text, 50%);
                }
                &:-ms-input-placeholder {
                    color:lighten($color-text, 50%);
                }
            }
        }
    }

}

.filter-option {
    font-size:14px;
    font-weight:700;
    letter-spacing:.88px;
}
