@font-face {
    font-family: "riyadh-icons";
    src: url('../fonts/icons/riyadh-icons.eot');
    src: url('../fonts/icons/riyadh-icons.eot?#iefix') format('eot'),
    //url('../fonts/icons/riyadh-icons.woff2') format('woff2'),
    url('../fonts/icons/riyadh-icons.woff') format('woff'),
    url('../fonts/icons/riyadh-icons.ttf') format('truetype'),
    url('../fonts/icons/riyadh-icons.svg#riyadh-icons') format('svg');
}

@mixin icon-styles {
    font-family: "riyadh-icons";
    font-style: normal;
    font-variant: normal;
    font-weight: normal;
    // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
    text-decoration: none;
    text-transform: none;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

