/* Main Colors */
$color-base:#fff;
$color-primary:#93C252;
$color-secondary:#C3DB63;
$color-tertiary:#D0564F;
$color-quaternary:#6963A3;
$color-quinary:#DAB446;
$color-senary:#62C4B1;

$color-graybase:#171717;
$color-gray:#AAABAD;
$color-fill:#F4F6F8;
$color-smooth:#B2B2B2; // #000 / 0.3
$color-muted:#ECF0F3; // #000 / 0.15
$color-disabled:#8C8C8C;

$color-error:#EB4242;
$color-error-secondary:#F33126;
$color-info:blue;
$color-success:#51C67D;
$color-warning:#FF9F4D;
$color-danger:$color-error;

$color-bg:$color-base;
$color-text:#000;
$color-link:#4328B7;
$color-link-hover:#4A40C2;
/* Fonts */
$font-family:"Roboto", sans-serif;
$font-family-secondary:"Mada", sans-serif;
$font-family-tertiary:"Riyadhcity", "Roboto", sans-serif;
$font-size:14px;
$font-weight:normal;
$letter-spacing:normal;
$line-height:normal;

/* Screen sizes */
$screen-xs:375px;
$screen-mobile:576px;
$screen-sm:768px;
$screen-md:1024px;
$screen-lg:1270px;
$screen-xl:1600px;

$screen-xl-min:$screen-xl;
$screen-lg-max:($screen-xl-min - 1);
