$title-max-height: 69px;
.rd-entry {
    overflow:hidden;

    &-wrap {
        padding:45px 0;

        &.-sm {
            padding:0;
        }

        &.-lg {
            padding:24px 0;

            @media (min-width:$screen-sm-min) {
                padding:34px 0 0 0;
            }

            @media (min-width:$screen-md-min) {
                padding:50px 0 0 0;
            }
        }
    }

    &-row {
        margin:0 -10px;
        @include clearfix;

        @media (min-width:$screen-md-min) {
            margin:0 -9px;
        }

        @media (min-width:$screen-lg-min) {
            margin:0 -12px;
        }

        @media (min-width:$screen-xl-min) {
            margin:0 -15px;
        }
    }

    &-banner {
        width:100%;
        height:120px;
        display:inline-block;
        margin:0 auto 10px;
        padding:0 10px;

        @media (min-width:$screen-sm-min) {
            margin:0 auto 18px;
        }

        @media (min-width:$screen-md-min) {
            padding:0 9px;
        }

        @media (min-width:$screen-lg-min) {
            padding:0 12px;
            margin:0 auto 30px;
        }

        @media (min-width:$screen-xl-min) {
            padding:0 15px;
        }

        &-link {
            display:block;
            text-align:center;
        }

        &-img {
            height:100%;
            margin:0 auto;
            max-width:280px;

            @media (min-width:$screen-xs-min) {
                display:none;
            }

            &.-xs {
                display:none;

                @media (min-width:$screen-xs-min) {
                    display:block;
                    max-width:335px;
                }

                @media (min-width:$screen-sm-min) {
                    display:none;
                }
            }

            &.-sm {
                display:none;

                @media (min-width:$screen-sm-min) {
                    display:block;
                    max-width:702px;
                }

                @media (min-width:$screen-md-min) {
                    display:none;
                }
            }

            &.-md {
                display:none;

                @media (min-width:$screen-md-min) {
                    display:block;
                    max-width:832px;
                }

                @media (min-width:$screen-lg-min) {
                    display:none;
                }
            }

            &.-lg {
                display:none;

                @media (min-width:$screen-lg-min) {
                    display:block;
                    max-width:1078px;
                }

                @media (min-width:$screen-xl-min) {
                    display:none;
                }
            }

            &.-xl {
                display:none;

                @media (min-width:$screen-xl-min) {
                    display:block;
                    max-width:1408px;
                }
            }
        }

    }

    &-item {
        $this:&;
        position:relative;
        display:table;
        width:100%;
        height:177px;
        margin:0 0 10px 0;

        padding:0 10px;

        cursor:pointer;
        transition:opacity 0.2s;

        &:lang(ar) {
            direction:rtl;
            text-align:right;
            float:right;
        }

        @media (min-width:$screen-sm-min) {
            float:left;
            height:223px;
            width:50%;
            margin:0 0 18px 0;
        }

        @media (min-width:$screen-md-min) {
            height:177px;
            width:33.3333%;
            padding:0 9px;
        }

        @media (min-width:$screen-lg-min) {
            height:300px;
            padding:0 12px;
            margin:0 0 30px 0;
        }

        @media (min-width:$screen-xl-min) {
            padding:0 15px;
        }

        &:hover {
            opacity:0.8;
        }

        &.-xs {
            width:100%;

            @media (min-width:$screen-md-min) {
                width:50%;
            }
        }

        &.-sm {
            width:100%;
            min-height:188px;

            @media (min-width:$screen-sm-min) {
                width:50%;
                min-height:224px;
            }
            @media (min-width:$screen-md-min) {
                width:50%;
                min-height:271px;
            }
            @media (min-width:$screen-lg-min) {
                width:33.3333%;
                min-height:300px;
            }
        }

        &.-lg {
            height:223px;
            width:100%;

            @media (min-width:$screen-sm-min) {
                height:400px;
            }
            @media (min-width:$screen-md-min) {
                height:372px;
                width:66.66667%;
            }
            @media (min-width:$screen-lg-min) {
                height:630px;
            }

            &:lang(ar) {
                float:right;
            }
        }

        &.-md {
            width:100%;

            @media (min-width:$screen-lg-min) {
                width:33.3333%;
            }
        }

        &.-full {
            width:100%;
        }

        &.-right {
            float:right;

            &:lang(ar) {
                float:left;
            }
        }

        &-inner {
            display:table;

            position:relative;
            width:100%;
            height:100%;

            background:$color-base;
        }

        &-widget {
            background-position:center center;
            height:100%;
        }

        &-tags {
            display:table-cell;
            width:100%;
            height:100%;

            max-height:300px;

            background:$color-base;

            &-inner {
                height:100%;
                padding:30px 45px;
            }

            &-title {
                font-size:26px;
                line-height:30px;
                font-weight:900;

                margin:0 0 17px 0;

                @media (min-width:$screen-md-min) {
                    font-size:30px;
                    line-height:37px;
                }
            }

            &-list {
                margin:0;
                padding:0;
                max-height:180px;
                overflow:hidden;

                @media (min-width:$screen-sm-min) {
                    max-height:120px;
                }
                @media (min-width:$screen-lg-min) {
                    max-height:180px;
                }
            }

            &-item {
                list-style:none;
                display:inline-block;

                margin:0 17px 17px 0;

                &:lang(ar) {
                    margin:0 0 17px 17px;
                }
            }

            &-link {
                font-size:13px;
                letter-spacing:.46px;
                line-height:15px;
                font-weight:700;

                color:lighten($color-text, 70%);

                transition:.2s;

                &:hover, &:active, &:focus {
                    color:$color-senary;
                    text-decoration:none;
                }
            }
        }

        &-img {
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            width:100%;
            height:100%;

            background-size:cover;
            background-position:center center;

            box-shadow: inset 0 -270px 160px -180px $color-text;

            &-title {
                display:table;
                position:absolute;
                padding:20px;
                width:100%;
                bottom:0;
                z-index:1;

                color:$color-base;

                font-size:14px;
                font-weight:700;
                letter-spacing:0.3px;
                line-height:20px;

                @media (min-width:$screen-lg-min) {
                    font-size:18px;
                    letter-spacing:0.39px;
                    line-height:23px;

                    padding:20px 30px;
                }

                &:lang(ar) {
                    letter-spacing:0;
                }

                &-col {
                    display:table-cell;
                    vertical-align:middle;

                    &.-xs {
                        width:23px;
                    }
                }

                &-text {
                    max-height: $title-max-height;

                    &.-sm {
                        max-width:350px;
                    }

                    &.-lg {
                        @media (min-width:$screen-sm-min) {
                            max-width:initial;
                        }
                    }

                    mark {
                        background:$color-primary;
                        color:$color-base;
                    }
                }

                &-description {
                    font-size:13px;
                    letter-spacing:1px;
                    line-height:27px;

                    text-transform:uppercase;

                    color:rgba($color-base, 0.5);
                }

                &-times {
                    font-size:13px;
                    letter-spacing:1px;
                    line-height:27px;

                    text-transform:uppercase;

                    color:rgba($color-base, 0.5);

                    @media (min-width:$screen-xl-min) {
                        display:inline-block;
                    }

                    &:lang(ar) {
                        letter-spacing:0;
                    }

                    &.-head {
                        @media (min-width:$screen-sm-min) {
                            display:inline-block;
                        }
                    }
                }

                &-icon {
                    padding:0 15px 0 0;
                    font-size:20px;
                    line-height:1;

                    &:lang(ar) {
                        padding:0 0 0 15px;
                    }
                }

                &-rating {
                    @media (min-width:$screen-xl-min) {
                        display:inline-block;
                        //margin:0 0 0 17px;
                    }

                    &:lang(ar) {
                        vertical-align:bottom;

                        @media (min-width:$screen-xl-min) {

                        }
                    }

                    &.-head {
                        @media (min-width:$screen-sm-min) {
                            display:inline-block;
                            //margin:0 0 0 17px;
                        }

                        &:lang(ar) {
                            vertical-align:bottom;

                            @media (min-width:$screen-sm-min) {
                                //margin:0 17px 0 0;
                            }
                        }
                    }
                }

                &.-rating {
                    padding:0 22px;
                }
            }

            &-overlay {
                position:absolute;
                top:0;
                left:0;
                right:0;
                bottom:0;
                height:100%;
                width:100%;

                background:rgba(0, 0, 0, 0.2);

                &-icon {
                    position:absolute;
                    top:50%;
                    left:50%;
                    transform:translate(-50%,-50%);

                    color:$color-base;

                    font-size:35px;
                    line-height:1;
                    vertical-align:middle;
                    text-align:center;

                    @media (min-width:$screen-lg-min) {
                        font-size:45px;
                    }
                }
            }
        }

        &-date {
            position:absolute;
            top:0;
            left:0;
            width:60px;
            height:60px;
            z-index:2;

            @media (min-width:$screen-lg-min) {
                width:72px;
                height:72px;
            }

            background:$color-base;
            text-align:center;

            &:lang(ar) {
                right:0;
            }

            &.-inverse {
                background:$color-fill;
            }

            &-day {
                margin:15px 0 0 -5px;
                font-size:21px;
                font-weight:bold;
                letter-spacing:0.32px;

                @media (min-width:$screen-lg-min) {
                    font-size:24px;
                    letter-spacing:0.37px;
                }

                &:lang(ar) {
                    margin:5px 0 0 -5px;
                    letter-spacing:0;
                }
            }

            &-mnt {
                margin:1px 0 0 15px;
                color:lighten($color-text, 70%);
                font-size:11px;
                letter-spacing:.24px;
                line-height:13px;
                text-align:left;

                &:lang(ar) {
                    margin:-5px 0 0 15px;
                    letter-spacing:0;
                }

                @media (min-width:$screen-lg-min) {
                    margin:0 0 0 20px;
                    font-size:13px;
                    letter-spacing:.28px;
                    line-height:15px;
                }
            }
        }

        &-bookmark {
            position:absolute;
            left:0;
            top:0;
            height:36px;
            width:36px;
            background:#DAB446;
            display:table;
            z-index:1;

            text-align:center;

            &.-secondary {
                background:$color-secondary;
            }

            &.-tertiary {
                background:$color-tertiary;
            }

            &.-quinary {
                background:$color-quinary;
            }

            &.-quaternary {
                background:$color-quaternary;
            }

            &-icon {
                font-size:17px;
                line-height:17px;
                color:$color-base;
                display:table-cell;
                vertical-align:middle;
            }

            &:lang(ar) {
                left:initial;
                right:0;
            }
        }

        &-free {
            &.-quinary {
                background : $color-quinary
            }
            position:absolute;
            left:0;
            top:0;
            height:36px;
            width:67px;

            line-height:36px;
            text-align:center;

            &:lang(ar) {
                left:auto;
                right:0;
            }

            &-text {
                font-size:13px;
                letter-spacing:1px;
                line-height:15px;
                font-weight:700;
                text-transform:uppercase;

                color:$color-base;
            }

            &.-indent-left {
                left:37px;

                &:lang(ar) {
                    left:auto;
                    right:37px;
                }
            }

            &.-tertiary {
                background:$color-tertiary;
            }
        }

        &-brief {
            display:table-cell;
            vertical-align:middle;

            position:relative;
            padding:0 55px;
            z-index:3;

            @media (min-width:$screen-lg-min) {
                padding:0 75px;
            }

            &-title {
                max-height: $title-max-height;
                font-size:14px;
                font-weight:700;
                letter-spacing:0.3px;
                line-height:20px;

                &:lang(ar) {
                    font-size:18px;
                    letter-spacing:0.39px;
                }

                @media (min-width:$screen-lg-min) {
                    font-size:18px;
                    letter-spacing:0.39px;
                    line-height:23px;
                }

                mark {
                    background:$color-primary;
                    color:$color-base;
                }
            }

            &-text {
                margin:5px 0 0 0;
                color:lighten($color-text, 30%);
                font-size:14px;
                letter-spacing:0.3px;
                line-height:23px;
                font-weight:400;

                &:lang(ar) {
                    font-size:15px;
                    letter-spacing:0.32px;
                    font-weight:200;
                }

                height:46px;
                overflow:hidden;
                text-overflow:ellipsis;

                .innerText{
                    max-height: 160px;
                    height: 100%;
                }

                @media (min-width:$screen-sm-min) {
                    height:70px;
                }

                @media (min-width:$screen-md-min) {
                    height:46px;
                }

                @media (min-width:$screen-lg-min) {
                    height:auto;
                    margin:10px 0 0 0;
                }

                &.-inverse {
                    color:rgba($color-base, 0.8);
                }

                mark {
                    background:none;
                    font-weight:700;
                    color:inherit;
                }

            }

            &-times {
                position:absolute;
                left:30px;
                bottom:20px;

                font-size:13px;
                letter-spacing:1px;
                line-height:27px;

                text-transform:uppercase;

                color:lighten($color-text, 50%);

                &:lang(ar) {
                    letter-spacing:0;
                }
            }
        }
    }

    &-more {
        margin:30px 0 0 0;

        @media (min-width:$screen-md-min) {
            margin:45px 0 0 0;
        }
    }

}
