.rd-addbox {

    overflow-x:hidden;

    &:lang(ar) {
        direction:rtl;
    }

    position:relative;
    @include clearfix;

    &-pin {
        display:inline-block;
        position:relative;
        height:34px;
        width:34px;
        padding:12px;

        cursor:pointer;
        border-radius:50%;

        transform:scale(0);
        transition:all 0.2s;

        &:before {
            content:"";
            display:block;
            position:absolute;
            height:100%;
            width:100%;
            top:0;
            left:0;

            background:rgba($color-text, 0.1);
            border:2px lighten($color-text, 60%) solid;
            border-radius:50%;

            animation:rd-addbox-pin-glow 2s infinite alternate;
            animation-play-state:paused;
        }

        &:after {
            content:"";
            display:block;
            position:relative;
            height:10px;
            width:10px;
            z-index:1;

            background:$color-text;
            border-radius:50%;
        }

        &.-visible {
            transform:scale(1);

            &:before, &:after {
                animation-play-state:running;
            }
        }

        &.-disabled {
            &:after {
                background:rgba($color-base, 0.3);
            }
        }
    }

    &-fill {

        @media (min-width:$screen-md-min) {
            position:fixed;
            left:0;
            bottom:0;

            width:190px;
            height:100%;
            background:$color-fill;
        }

        &:lang(ar) {
            right:0;
            left:initial;
        }
    }

    &-close {
        display:inline-block;
        position:absolute;
        top:100px;
        right:30px;
        z-index:2;

        &-link {
            font-size:18px;
            color:$color-text;
            transition:.2s;

            @media (min-width:$screen-md-min) {
                font-size:28px;
            }

            &:hover, &:focus, &:active {
                color:lighten($color-text, 50%);
            }
        }
    }

    &-wrap {
        position:relative;
        z-index:1;
        float:right;

        width:100%;
        padding:0 20px;
        height:100%;

        @media (min-width:$screen-sm-min) {
            padding:0;
        }

        @media (min-width:$screen-md-min) {
            width:calc(100% - 190px);
        }

        &:lang(ar) {
            @media (min-width:$screen-md-min) {
                width:100%;
                margin-right:190px;
            }
        }
    }

    &-inner {
        width:100%;
        height:100%;
        padding:120px 0 50px;

        @media (min-width:$screen-md-min) {
            padding:180px 0 60px;
        }
    }

    &-container {
        width:100%;

        @media (min-width:$screen-sm-min) {
            margin:0 55px;
        }
        @media (min-width:$screen-md-min) {
            margin:0 15%;
        }
        @media (min-width:$screen-xl-min) {
            margin:0 0 0 352px;
        }

        &:lang(ar) {
            @media (min-width:$screen-sm-min) {
                margin:0 55px;
            }
            @media (min-width:$screen-md-min) {
                margin:0 172px 0 0;
            }
            @media (min-width:$screen-xl-min) {
                margin:0 352px 0 0;
            }
        }

        &.-full {

            @media (min-width:$screen-sm-min) {
                max-width:650px;
            }
            @media (min-width:$screen-md-min) {
                max-width:475px;
                margin:0 0 0 175px;
            }
            @media (min-width:$screen-xl-min) {
                max-width:650px;
                margin:0 0 0 352px;
            }

            &:lang(ar) {
                @media (min-width:$screen-md-min) {
                    margin:0 175px 0 0;
                }
                @media (min-width:$screen-xl-min) {
                    margin:0 352px 0 0;
                }
            }
        }
    }

    &-content {
        height:100%;
    }

    &-header {
        margin-bottom:50px;

        &-text {
            font-size:16px;
            line-height:27px;
            font-weight:400;
            letter-spacing:0;

            @media (min-width:$screen-sm-min) {
                font-size:24px;
                line-height:35px;
                font-weight:300;
            }

            &:lang(ar) {
                text-align:justify;
            }
        }
    }

    &-list {

        &-item {
            margin-bottom:70px;

            &-heading {
                margin-bottom:20px;

                &:lang(ar) {
                    text-align:right;
                }
            }

            &-number {
                display:inline-block;
                width:38px;
                height:38px;

                font-size:16px;
                font-weight:600;
                line-height:38px;

                background:$color-primary;
                color:$color-base;

                text-align:center;
            }

            &-description {
                display:inline-block;

                font-size:18px;
                line-height:23px;
                letter-spacing:.39px;
                font-weight:700;

                margin-left:22px;
                vertical-align:middle;

                &:lang(ar) {
                    margin-right:22px;
                    max-width:180px;

                    @media (min-width:$screen-sm-min) {
                        max-width:240px;

                    }
                    @media (min-width:$screen-lg-min) {
                        max-width:320px;

                    }
                }
            }

            &-text {
                font-size:16px;
                line-height:27px;
                font-weight:400;
                letter-spacing:0;

                margin:0 0 20px 0;

                &:lang(ar) {
                    text-align:justify;
                    .-en-text{
                        display: none
                    }
                }

                &:lang(en) {
                    .-ar-text{
                        display: none
                    }
                }
            }

            ul {
                margin:0 0 20px 0;
            }

            li {
                font-size:16px;
                line-height:27px;
                font-weight:400;
                letter-spacing:0;

                margin:0 0 5px 0;
            }
        }
    }

    &-title {
        display:block;
        color:$color-text;
        font-weight:900;
        font-size:26px;
        line-height:30px;
        margin:0 0 24px 0;

        position:initial;

        @media (min-width:$screen-sm-min) {
            margin:0 0 40px 0;

            line-height:47px;
            font-size:40px;

        }
        @media (min-width:$screen-md-min) {
            position:fixed;
            top:180px;

            left:55px;
            display:inline-block;
        }
        @media (min-width:$screen-xl-min) {
            font-size:60px;
            line-height:67px;

            left:35px;
        }

        &:lang(ar) {
            @media (min-width:$screen-md-min) {
                right:55px;
                left:initial;
            }
            @media (min-width:$screen-xl-min) {
                right:35px;
                left:initial;
            }
        }

        &:hover, &:focus, &:active {
            color:$color-text;
            opacity:.8;

            text-decoration:none;

            svg {
                padding:0 10px 0 0;

                @media (max-width:$screen-sm-min) {
                    padding:0 5px 0 0;
                }
            }
        }

        &.-xs {
            &:lang(ar) {
                @media (min-width:$screen-md-min) {
                    right:55px;
                    left:initial;
                    max-width:275px;
                }
                @media (min-width:$screen-xl-min) {
                    max-width:440px;
                }
            }
        }

        &-text {
            display:inline-block;
            vertical-align:top;

            @media (min-width:$screen-md-min) {
                max-width:170px;
            }

            @media (min-width:$screen-xl-min) {
                max-width:360px;
            }
        }

        &-icon {
            font-size:28px;
            color:$color-text;

            display:inline-block;
            vertical-align:top;
            margin:0 25px 0 0;

            transition:.2s;

            @media (max-width:$screen-sm-min) {
                font-size:18px;
            }

            @media (min-width:$screen-md-min) {
                margin:0 40px 0 0;
            }

            svg {
                position:relative;
                transition:padding .2s;
                vertical-align:middle;
            }

            &:lang(ar) {
                transform:rotate(180deg);
                margin:0 0 0 25px;

                @media (min-width:$screen-md-min) {
                    margin:0 0 0 40px;
                }
            }
        }

        p {

            display:inline-block;
            margin:0;

            @media (min-width:$screen-md-min) {
                display:block;
            }
        }
    }

    &-form {
        width:100%;

        @media (min-width:$screen-sm-min) {
            width:660px;
        }

        &-row {
            display:table;
            width:100%;
        }

        &-col {
            display:table-cell;

            &.-right {
                text-align:right;

                &:lang(ar) {
                    text-align:left;
                }
            }
        }

        &-line {
            width:14px;
            height:1px;

            background:#d9d9d9;
        }

        &-toolbar {
            &:lang(ar) {
                text-align:left;
            }
        }

        &-time {

            &-row {
                @include clearfix;
            }

            &-col {
                float:left;
                position:relative;

                &:lang(ar) {
                    float:right;
                }

                @media (max-width:$screen-md-min) {
                    margin-bottom:34px;
                    width:100%;
                }

                &:last-child {
                    @media (max-width:$screen-md-min) {
                        margin-bottom:0;
                    }
                }

                &:first-child {
                    margin-right:60px;

                    &:lang(ar) {
                        margin-right:initial;
                        margin-left:60px;

                        &:after {
                            left:-36px;
                            right:initial;
                        }
                    }

                    &:after {
                        content:'';
                        height:1px;
                        width:14px;
                        background:lighten($color-text, 85%);

                        position:absolute;
                        right:-6px;
                        top:34%;

                        @media (max-width:$screen-md-min) {
                            height:14px;
                            width:1px;

                            right:initial;
                            top:initial;
                            left:13px;
                            bottom:-23px;
                        }
                    }
                }

                &.-right {
                    float:right;
                }
            }
        }

        &-errorbox {
            position:absolute;
            left:0;
            right:0;
            bottom:75px;
            margin:auto auto;

            background:$color-text;
            max-width:231px;

            @media (min-width:$screen-md-min) {
                bottom:95px;
            }

            &-inner {
                padding:12px;
            }

            &-text {
                color:$color-base;
            }
        }

        &-calendar {

            &-row {
                @include clearfix;
            }

            &-col {
                float:left;
                position:relative;
                margin:0 0 24px 0;
                width:100%;

                @media (min-width:$screen-md-min) {
                    width:auto;
                }

                &:first-child {

                    &:after {
                        content:'';
                        height:1px;
                        width:14px;
                        background:lighten($color-text, 85%);

                        position:absolute;
                        right:-7px;
                        top:50%;

                        @media (max-width:$screen-md-min) {
                            display: none;
                            height:14px;
                            width:1px;

                            right:initial;
                            top:initial;
                            left:15px;
                            bottom:0;
                        }
                    }

                    &:lang(ar) {
                        &:after {
                            left:-7px;
                            right:auto;
                        }
                    }
                }

                &.-right {
                    float:right;
                }
            }
        }

        &-group {
            position:relative;

            margin:0 0 24px 0;

            @media (min-width:$screen-md-min) {
                margin:0 0 50px 0;
            }

            &.-md {
                @media (min-width:$screen-md-min) {
                    margin:0 0 40px 0;
                }
            }

            &.-lg {
                margin:0 0 210px 0;
            }

            &.-error {

                input, textarea, .rd-form-textarea {
                    border:1px $color-error solid;

                    &:hover, &:active, &:focus {
                        border:1px $color-error solid;
                    }
                }
            }

            &:lang(ar) {

                @media (min-width:$screen-md-min) {

                    .row > div {
                        float:right;
                    }
                }
            }

            &-item {

                @media (max-width:$screen-md-min) {
                    margin:0;
                }

                &.-indent {

                    &:last-child {

                        @media (max-width:$screen-md-min) {
                            margin:0 0 24px 0;
                        }
                    }
                }

                input{
                    &[type=number]{
                        &::-webkit-inner-spin-button,
                        &::-webkit-outer-spin-button {
                            appearance: none;
                            margin: 0;
                        }
                    }
                }
            }

            .mb-md-24{
                @media (max-width:$screen-md) {
                    margin-bottom: 24px;
                }
            }
        }

        &-title {
            font-size:24px;
            font-weight:900;

            margin-bottom:18px;

            @media (min-width:$screen-sm-min) {
                margin-bottom:28px;
            }
        }

        &-label {
            font-size:13px;
            font-weight:700;

            line-height:15px;
            letter-spacing:1.5px;
            text-transform:uppercase;
            color:lighten($color-text, 70%);

            margin-bottom:15px;

            &:lang(ar) {
                letter-spacing:0;
            }
        }

        &-size {
            font-size:13px;
            letter-spacing:0.28px;
            line-height:17px;

            color:$color-smooth;
        }

        &-input {
            display:none;
            width:100%;
            padding:16px;

            color:$color-text;
            background:$color-base;
            border:1px lighten($color-text, 85%) solid;

            font-size:14px;
            font-weight:700;
            letter-spacing:0.88px;
            line-height:16px;

            transition:all 0.2s;

            &::-webkit-input-placeholder {
                color:lighten($color-text, 80%);
            }
            &::-moz-placeholder {
                color:lighten($color-text, 80%);
            }
            &:-moz-placeholder {
                color:lighten($color-text, 80%);
            }
            &:-ms-input-placeholder {
                color:lighten($color-text, 80%);
            }

            @media (max-width:$screen-sm-min) {
                display:block;
            }

            &.-right {
                text-align:right;
            }
            &.-error {
                border:1px $color-error solid;

                &:hover, &:active, &:focus {
                    border:1px $color-error solid;
                }
            }
        }

        &-textarea {
            height:71px;
            width:100%;
            padding:13px 10px;

            color:$color-text;
            background:$color-base;
            border:1px lighten($color-text, 85%) solid;

            font-size:40px;
            font-weight:700;
            line-height:43px;
            text-align:left;

            resize:none;

            transition:all 0.2s;

            &:focus {
                height:118px;
                border-color:lighten($color-text, 75%);

                @media (max-width:$screen-sm-min) {
                    height:80px;
                }
            }

            &:hover {
                border-color:lighten($color-text, 75%);
            }

            &.-lg {
                height:260px;
            }

            &.-right {
                text-align:right;
            }

            &.-error {
                border:1px $color-error solid;

                &:hover, &:active, &:focus {
                    border:1px $color-error solid;
                }
            }

            &::-webkit-input-placeholder {
                color:lighten($color-text, 80%);
            }
            &::-moz-placeholder {
                color:lighten($color-text, 80%);
            }
            &:-moz-placeholder {
                color:lighten($color-text, 80%);
            }
            &:-ms-input-placeholder {
                color:lighten($color-text, 80%);
            }

            @media (max-width:$screen-sm-min) {
                display:none;
            }

            &:lang(ar) {
                height:76px;
                text-align:right;

                &:focus {
                    height:118px;
                    border-color:lighten($color-text, 75%);

                    @media (max-width:$screen-sm-min) {
                        height:80px;
                    }
                }
            }
        }

        &-map {
            height:300px;
            margin-top:21px;
            position:relative;

            img {
                width:100%;
                height:100%;
            }
        }

        /* Typeahead Override */
        .twitter-typeahead {
            display:block !important;

            .tt-menu {
                @extend .rd-dropdown-menu;
                box-shadow:0 6px 12px rgba(0, 0, 0, .175);
                padding:0;
                width:100%;
                overflow:auto;

                .tt-suggestion {
                    cursor:pointer;

                    font-size:13px;
                    font-weight:500;
                    color:$color-text;
                    letter-spacing:0.6px;
                    line-height:35px;
                    transition:all 0.15s;

                    padding:3px 20px;

                    &:hover {
                        background:#f5f5f5;
                    }

                    &:active {
                        outline:none !important;
                    }

                    &:hover {
                        color:#000;
                        padding-left:30px;
                    }
                }
            }
        }
    }

    .rd-datepicker {
        width:196px;
    }
}

.rd-addbox-form .twitter-typeahead .tt-menu {
    margin:0;
}

@keyframes rd-addbox-pin-glow {
    0% {
        transform:scale(.99);
    }
    100% {
        transform:scale(0.85);
    }
}
