.rd-article {
  $root: &;

  &:lang(ar) {
    direction: rtl;
    text-align: justify;
  }

  &-wrap {
    padding: 25px 0 0 0;

    @media (min-width: $screen-md-min) {
      padding: 85px 0 0 0;
    }
  }

  &-block {
    &-header {
      margin: 40px 0 0 0;
    }

    &-title {
      font-size: 18px;
      letter-spacing: 0.39px;
      line-height: 23px;

      font-weight: 700;

      display: inline-block;
      margin: 0 0 0 22px;

      &:lang(ar) {
        margin: 0 22px 0 0;
      }
    }
  }

  &-header {
    margin: 7px 0 0 0;

    @media (min-width: $screen-sm-min) {
      margin: 12px 0 0 0;
    }

    @media (min-width: $screen-md-min) {
      margin: 22px 0 0 0;
    }

    &:lang(ar) {
      text-align: right;
    }

    &-inner {
      display: block;
      width: 100%;
      height: 100%;

      @media (min-width: $screen-sm-min) {
        display: table;
      }
    }

    &-col {
      display: block;
      vertical-align: top;

      @media (min-width: $screen-sm-min) {
        display: table-cell;
      }

      &.-left {
        width: 70px;
      }
    }

    &-img {
      width: 100px;
      height: 100px;
      margin: 0 auto 28px;

      @media (min-width: $screen-sm-min) {
        margin: 0 50px 0 0;
      }

      background-size: cover;
      background-position: center center;

      filter: drop-shadow(0 0 14px rgba(0, 0, 0, 0.06));

      &:lang(ar) {
        @media (min-width: $screen-sm-min) {
          margin: 0 0 0 50px;
        }
      }
    }

    &-title {
      max-width: 440px;
    }

    &-description {
      display: inline-block;
      margin-bottom: 7px;

      opacity: 0.3;
      font-size: 13px;
      letter-spacing: 1px;
      line-height: 24px;
      font-weight: 600;
      text-transform: uppercase;

      @media (min-width: $screen-sm-min) {
        margin-bottom: 22px;
      }

      &:lang(ar) {
        letter-spacing: 0;
      }
    }

    h1,
    h2,
    h3 {
      padding: 0;
      margin: 0;

      font-size: 26px;
      font-weight: 900;

      @media (min-width: $screen-sm-min) {
        font-size: 60px;
      }

      &.-inline {
        display: inline-block;
      }
    }

    &-mark {
      display: inline-block;
      vertical-align: top;
      width: 13px;
      height: 13px;
      margin: 0 0 0 15px;

      border-radius: 50px;

      &:lang(ar) {
        margin: 0 15px 0 0;
      }
    }

    &.-xs {
      h1 {
        margin: 10px 0 0 0;

        font-size: 26px;
        font-weight: 900;

        @media (min-width: $screen-sm-min) {
          font-size: 40px;
          line-height: 49px;
        }

        @media (min-width: $screen-lg-min) {
          margin: 0;
          font-size: 60px;
          line-height: 49px;
        }
      }
    }

    &.-sm {
      margin: 20px 0 0 0;

      h1,
      h2,
      h3 {
        font-size: 26px;
        font-weight: 900;

        @media (min-width: $screen-sm-min) {
          font-size: 40px;
        }
      }
    }

    &.-lg {
      h1,
      h2,
      h3 {
        padding: 0;
        margin: 0;

        font-size: 26px;
        font-weight: 900;

        @media (min-width: $screen-sm-min) {
          font-size: 40px;
          line-height: 49px;
        }
      }
    }
  }

  &-pagination {
    margin: 7px 0 0 0;

    font-size: 13px;
    letter-spacing: 0.28px;
    line-height: 23px;

    color: rgba($color-base, 0.4);

    &-title {
      display: inline-block;
      vertical-align: middle;
      cursor: pointer;
      &:hover {
        color: rgba(255, 255, 255, 0.4);
        opacity: 0.8;
      }
    }

    &-icon {
      display: inline-block;
      vertical-align: middle;
      margin: 0 0 0 18px;

      font-size: 8px;

      .-chevron {
        &-right:lang(ar) {
          display: none;
        }
        &-left:lang(en) {
          display: none;
        }
      }
      &:lang(ar) {
        margin: 0 18px 0 0;
      }

      //.rd-svgsprite{
      //    position: relative;
      //    &:before {
      //        content: '';
      //        position:  absolute;
      //        top: 1px;
      //        width: 75%;
      //        height: 75%;
      //        border-bottom: 0.8px solid rgba(255,255,255,.4);
      //        border-left: 0.8px solid rgba(255,255,255,.4);
      //        transform: rotate(225deg);
      //    }
      //    &:lang(ar):before{
      //        left: 4px;
      //        transform: rotate(45deg);
      //    }
      //}
    }

    &-subtitle {
      display: inline-block;
      vertical-align: middle;
      margin: 0 0 0 16px;

      color: rgba($color-base, 0.4);

      transition: 0.3s;

      &:lang(ar) {
        margin: 0 16px 0 0;
      }

      &:hover,
      &:focus {
        color: rgba($color-base, 0.4);

        text-decoration: none;

        opacity: 0.8;
      }

      &-mark {
        display: inline-block;
        vertical-align: middle;
        width: 7px;
        height: 7px;

        border-radius: 50px;
      }

      &-text {
        display: inline-block;
        vertical-align: middle;
        margin: 0 0 0 10px;

        &:lang(ar) {
          margin: 0 10px 0 0;
        }
      }
    }
  }

  &-time {
    display: inline-block;

    color: lighten($color-text, 70%);
    font-size: 13px;
    font-weight: bold;
    letter-spacing: 1px;
    line-height: 27px;
    white-space: nowrap;
  }

  &-date {
    display: inline-block;
    margin: 0 8px 0 0;

    font-size: 13px;
    color: lighten($color-text, 70%);
    letter-spacing: 1px;
    line-height: 27px;

    font-weight: 700;

    text-transform: uppercase;

    &:lang(ar) {
      margin: 0 0 0 8px;
    }
  }

  &-category {
    display: inline-block;

    font-size: 13px;
    color: lighten($color-text, 70%);
    letter-spacing: 1px;
    line-height: 27px;

    font-weight: 700;

    text-transform: uppercase;

    &-grid {
      display: table;
      width: 100%;

      &-col {
        display: table-cell;
        vertical-align: top;

        &.-right {
          padding: 0 0 0 20px;
          width: 1px;
          white-space: nowrap;

          &:lang(ar) {
            padding: 0 20px 0 0;
          }
        }
      }
    }
  }

  &-speech {
    display: flex;
    justify-content: end;

    &-btn {
      background-color: transparent;
      border: none;
      margin: 0;
    }
  }

  &-price {
    display: block;
    padding: 4px 7px;

    color: $color-base;
    background: $color-fill;

    font-size: 13px;
    letter-spacing: 1px;
    line-height: 15px;
    font-weight: 700;
    text-transform: uppercase;
    &.-inline-block {
      display: inline-block;
    }
  }

  &-author {
    margin: 20px 0 10px 0;

    @media (min-width: $screen-sm-min) {
      margin: 40px 0 20px 0;
    }

    @media (min-width: $screen-md-min) {
      margin: 60px 0 40px 0;
    }

    &-photo {
      display: inline-block;
      width: 30px;
      height: 30px;
      margin: 0 14px 0 0;
      vertical-align: middle;

      background-size: cover;
      background-position: center center;
      border-radius: 100%;

      &:lang(ar) {
        margin: 0 0 0 14px;
      }
    }

    &-title {
      display: inline-block;
      vertical-align: middle;

      &-name {
        color: inherit;

        font-size: 13px;
        font-weight: bold;
        line-height: 27px;

        @media (min-width: $screen-lg-min) {
          display: inline-block;
          vertical-align: middle;
        }

        &:hover,
        &:focus {
          color: inherit;
        }
      }

      &-twitter {
        color: $color-text;
        font-size: 13px;
        letter-spacing: 0.48px;

        @media (min-width: $screen-lg-min) {
          margin: 0 0 0 16px;
          display: inline-block;
          vertical-align: middle;

          &:lang(ar) {
            margin: 0 16px 0 0;
          }
        }

        &:hover,
        &:focus {
          color: $color-text;
        }

        &:lang(ar) {
          direction: initial;
        }

        &.-sm {
          margin: 16px 0 0 0;
        }
      }
    }
  }

  &-row {
    max-width: 320px;

    @media (min-width: $screen-sm-min) {
      max-width: 375px;
    }
    @media (min-width: $screen-md-min) {
      max-width: initial;
    }
  }

  &-tags {
    direction: ltr;
    position: relative;
    margin: 45px 0 0 0;
    padding: 0 0 0 63px;

    &:lang(ar) {
      text-align: right;
      padding: 0 63px 0 0;
    }

    @media (min-width: $screen-lg-min) {
      padding: 0 0 0 93px;

      &:lang(ar) {
        padding: 0 93px 0 0;
      }
    }

    &.-indent {
      margin: 0;
    }

    &:before {
      content: '';
      display: inline-block;
      height: 1px;
      width: 40px;
      position: absolute;
      left: 0;
      top: 6px;

      @media (min-width: $screen-lg-min) {
        width: 70px;
      }

      background: currentColor;
      opacity: 0.1;
    }

    &:lang(ar):before {
      left: auto;
      top: 14px;
      right: 0;
    }

    &-item {
      display: inline-block;
      margin: 0 7px 14px 7px;

      font-size: 13px;
      font-weight: bold;
      letter-spacing: 0.46px;
      vertical-align: middle;

      color: $color-text;

      transition: 0.3s;

      &:hover,
      &:focus {
        opacity: 0.8;
        color: $color-text;
      }

      &:lang(ar) {
        direction: rtl;
      }
    }
  }

  &-tabs {
    margin: 50px 0 0 0;

    &-item {
      position: relative;
      display: inline-block;
      padding: 0 0 15px 0;
      margin: 0 15px;

      opacity: 0.3;
      color: inherit;
      font-size: 14px;
      font-weight: bold;

      transition: all 0.2s;

      @media (min-width: $screen-sm-min) {
        font-size: 18px;
      }

      &:after {
        content: '';
        position: absolute;
        display: block;
        left: 0;
        right: 0;
        bottom: 0;
        width: 50%;
        height: 0;

        background: currentColor;
        transition: all 0.2s;
      }

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }

      &:hover,
      &:focus {
        opacity: 1;
        color: inherit;
        text-decoration: none;
      }

      &:lang(ar) {
        letter-spacing: 0;

        &:first-child {
          margin-left: 15px;
          margin-right: 0;
        }

        &:last-child {
          margin-left: 0;
        }
      }

      &.-active {
        opacity: 1;

        &:after {
          height: 4px;
        }
      }
    }

    &-content {
      display: none;

      &.-visible {
        display: block;
      }
    }

    &:after {
      content: '';
      display: block;
      height: 1px;
      width: 100%;

      background: currentColor;
      opacity: 0.1;
    }
  }

  &-gallery {
    padding: 35px 0;
    margin: -7px;

    &-img {
      margin: 7px;
      display: inline-block;
      float: left;
      width: calc(33.33% - 14px);

      background-size: cover;
      background-position: center center;
      cursor: pointer;

      &:after {
        content: '';
        display: block;
        padding-top: 100%; /* 1:1 */
      }

      &.-right {
        float: right;
      }

      &.-lg {
        width: calc(66.67% - 14px);
      }

      &.-long {
        &:after {
          content: '';
          display: block;
          padding-top: 206.1%; /* 1:2 */
        }
      }

      &.-full {
        width: calc(100% - 14px);

        &:after {
          padding-top: 56.25%; /* 16:9 */
        }
      }
    }

    &:after {
      content: '';
      display: block;
      clear: both;
    }
  }

  &-specs {
    display: table;
    margin: 50px 0 0 0;

    &-row {
      display: table-row;
    }

    &-title,
    &-value {
      display: table-cell;
      vertical-align: baseline;

      font-size: 14px;
      line-height: 27px;
    }

    &-title {
      padding: 0 70px 0 0;
      opacity: 0.5;

      &:lang(ar) {
        padding: 0 0 0 70px;
      }
    }
  }

  &-info {
    display: flex;
    flex-wrap: wrap;
    margin: 60px 0 0 0;

    &-col {
      width: (100% / 2);
      margin-bottom: 45px;

      @media (min-width: $screen-lg-min) {
        width: (100% / 3);
      }

      &.-visible {
        display: none;

        @media (min-width: $screen-lg-min) {
          display: block;
        }
      }
    }

    &-title {
      margin-bottom: 10px;
      min-height: 23px;

      font-size: 18px;
      font-weight: 700;
      letter-spacing: 0.39px;
      line-height: 23px;

      @media (min-width: $screen-sm-min) {
        font-size: 20px;
        letter-spacing: 0.43px;
        line-height: 23px;
      }
    }

    &-text {
      font-size: 14px;
      line-height: 27px;

      @media (min-width: $screen-sm-min) {
        font-size: 16px;
      }

      span {
        display: block;
      }

      p {
        margin: 0;
        padding: 0;
      }
    }

    &-link {
      font-size: 14px;
      line-height: 27px;

      color: $color-secondary;

      transition: 0.2s;

      &:hover,
      &:active,
      &:focus {
        color: $color-secondary;
        text-decoration: none;
        opacity: 0.7;
      }

      @media (min-width: $screen-sm-min) {
        font-size: 16px;
      }

      &.-visible {
        display: inline-block;

        @media (min-width: $screen-lg-min) {
          display: none;
        }
      }
    }
  }

  &-brief {
    margin: 25px 0 0 0;

    font-size: 24px;
    font-weight: 300;
    letter-spacing: 0;
    line-height: 35px;
  }

  &-text {
    margin: 40px 0 0 0;

    font-size: 16px;
    letter-spacing: 0;
    line-height: 27px;

    h3 {
      padding: 0;
      margin: 60px 0 20px 0;

      font-size: 20px;
      font-weight: bold;
      letter-spacing: 0.43px;
    }

    iframe {
      max-width: 100%;
      max-height: 383px;
    }
  }

  &-list {
    margin: 25px 0;
    padding: 0 15px;

    font-size: 13px;
    font-weight: 700;
    letter-spacing: 0;
    line-height: 27px;

    &-item {
      padding: 5px 25px;
    }
  }

  &-map {
    position: relative;
    margin: 40px 0 0 0;
    width: 100%;
    height: 300px;

    @media (min-width: $screen-xl-min) {
      height: 350px;
    }

    &-control {
      &-item {
        position: absolute;
        z-index: 1;

        &.-zoom {
          top: 50%;
          left: 20px;
          margin: -41px 0 0 0;
        }
      }
    }

    &-canvas {
      width: 100%;
      height: 100%;
      overflow: hidden;
    }
  }

  &-adress {
    margin: 20px 0;

    &-icon {
      display: inline-block;
      vertical-align: top;
    }

    &-text {
      display: inline-block;
      margin-left: 6px;

      font-size: 13px;
      line-height: 20px;
      font-weight: 500;
    }
  }

  &-attach {
    margin: 40px 0;

    &-image {
      cursor: pointer;
      position: relative;

      img {
        height: auto;
        max-width: 100%;
      }

      &-overlay {
        display: table;
        position: absolute;
        top: 50%;
        right: 0;
        bottom: 0;
        left: 0;

        transform: translateY(-50%);

        width: 100%;
        height: 100%;
      }

      &-icon {
        display: table-cell;
        vertical-align: middle;

        color: $color-base;
        font-size: 45px;
        text-align: center;
      }
    }

    &-caption {
      margin: 17px 0 0 0;
      font-size: 16px;
      letter-spacing: 0.33px;
      line-height: 28px;
      opacity: 0.5;
    }
  }

  &-details {
    display: block;
    margin: 30px 0 0 0;

    &-item {
      display: inline-block;
      margin: 0 30px 8px 0;

      font-size: 13px;

      &:lang(ar) {
        margin: 0 0 8px 30px;
      }

      svg {
        margin: -1px 8px 0 0;
        font-size: 14px;

        &:lang(ar) {
          margin: -1px 0 0 8px;
        }
      }

      a {
        color: inherit;

        &:hover,
        &:focus {
          color: inherit;
        }
      }

      &.-block {
        display: block;
      }
    }
  }

  &-counts {
    margin: 45px 0 0 0;

    &-like {
      display: inline-block;

      color: inherit;
      user-select: none;
      line-height: 1;

      transition: all 0.2s;

      &-icon {
        display: inline-block;
        font-size: 20px;
        line-height: 1;
        vertical-align: middle;

        transition: transform 0.2s;
      }

      &-num {
        display: inline-block;
        margin: 0 0 0 13px;

        font-size: 24px;
        font-weight: bold;
        line-height: 20px;
        vertical-align: middle;

        &:lang(ar) {
          margin: 0 13px 0 0;
        }
      }

      &:hover,
      &:focus {
        color: $color-disabled;
      }

      &:active &-icon {
        transform: scale(1.2);
      }
    }

    &-views {
      display: inline-block;
      margin: 0 0 0 25px;

      opacity: 0.3;
      color: inherit;

      font-size: 13px;
      letter-spacing: 0.28px;
      line-height: 1;
      vertical-align: middle;

      &:lang(ar) {
        margin: 0 25px 0 0;
      }
    }
  }

  &-related {
    $this: &;

    position: relative;
    width: 100%;
    margin: 50px 0 0 0;
    padding: 40px 0;
    user-select: none;

    @media (min-width: $screen-sm-min) {
      padding: 70px 0;
    }

    background: $color-disabled;

    &-header {
      @media (min-width: $screen-sm-min) {
        position: absolute;
        display: table;
        top: 50%;
        transform: translateY(-50%);
        opacity: 1;

        transition: opacity 0.4s;

        &-inner {
          display: table-cell;
          vertical-align: middle;
        }

        &.-hidden {
          opacity: 0;
        }
      }

      h2,
      h3,
      h4 {
        padding: 0;
        margin: 0;

        color: $color-base;

        font-size: 26px;
        font-weight: 900;
        line-height: 30px;

        @media (min-width: $screen-xs-min) {
          font-size: 33px;
          line-height: 36px;
        }

        @media (min-width: $screen-sm-min) {
          font-size: 40px;
          line-height: 49px;
        }

        span {
          margin: 0 5px 0 0;

          @media (min-width: $screen-sm-min) {
            margin: 0;
            display: block;
          }
        }
      }

      &.-inverse {
        h2,
        h3,
        h4 {
          color: $color-text;
        }
      }

      &.-static {
        position: static;
      }
    }

    &-cap {
      display: inline-block;
      width: 1px;

      @media (min-width: $screen-sm-min) {
        width: 240px;

        &:lang(ar) {
          width: 280px;
        }
      }

      @media (min-width: $screen-lg-min) {
        width: 260px;

        &:lang(ar) {
          width: 300px;
        }
      }

      @media (min-width: $screen-xl-min) {
        width: 290px;

        &:lang(ar) {
          width: 350px;
        }
      }

      &.-lg {
        display: inline-block;
        width: 1px;

        @media (min-width: $screen-sm-min) {
          width: 240px;
        }

        @media (min-width: $screen-xl-min) {
          width: 455px;
        }
      }
    }

    &-items {
      padding: 23px 0 0 10px;

      @media (min-width: $screen-sm-min) {
        padding: 0;
      }
    }

    &-item {
      position: relative;
      display: inline-block;
      min-height: 340px;
      margin: 0 10px;
      vertical-align: top;

      background: $color-base;
      color: $color-text;

      transition: all 0.2s;
      user-select: none;

      width: 277px;

      &:hover {
        opacity: 0.95;
      }

      &-wrap {
        width: 100%;
        height: 100%;
        min-height: 340px;
        display: table;
      }

      &-inner {
        position: absolute;
        left: 20px;
        bottom: 30px;
      }

      &-img {
        position: relative;
        display: block;
        height: 186px;
        width: 100%;
        overflow: visible;

        background: #fcfcfc;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;

        transition: all 0.2s;

        &.-full {
          position: absolute;
          height: 100%;
        }

        &-icon {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);

          color: rgba($color-text, 0.4);

          font-size: 48px;
          line-height: 1;
        }
      }

      &-date {
        position: absolute;
        top: 0;
        left: 0;
        display: inline-block;
        width: 67px;
        height: 67px;

        background: $color-base;
        text-align: center;

        &-day {
          display: block;
          margin: 12px 0 0 -5px;
          font-size: 24px;
          font-weight: bold;
          letter-spacing: 0.37px;

          &:lang(ar) {
            margin: 0;
          }
        }

        &-mnt {
          display: block;
          margin: 0 0 0 -10px;
          color: lighten($color-text, 70%);
          font-size: 13px;

          &:lang(ar) {
            margin: -10px 0 0 0;
          }
        }
      }

      &-title {
        display: block;
        height: 155px;
        padding: 20px;

        &.-full {
          display: table-cell;
          vertical-align: middle;
          height: initial;
        }

        &.-sm {
          height: 91px;
        }

        &.-lg {
          height: initial;
          padding: 0;
          margin: 10px 0 0 0;

          color: $color-base;

          font-size: 40px;
          font-weight: 700;
          letter-spacing: 0.86px;
          line-height: 47px;

          transition: 0.3s ease-in-out 0.1s;
        }
      }

      &-cat {
        display: block;
        color: lighten($color-text, 70%);
        margin: 0 0 7px 0;

        &-icon {
          display: inline-block;
          margin: 0 15px 0 0;
          font-size: 20px;
          vertical-align: middle;

          &:lang(ar) {
            margin: 0 0 0 15px;
          }
        }

        &-text {
          display: inline-block;
          font-size: 13px;
          letter-spacing: 0.28px;
          vertical-align: middle;
          overflow: hidden;
          padding-top: 7px;
        }
      }

      &-caption {
        display: block;
        font-size: 18px;
        font-weight: bold;
        letter-spacing: 0.39px;
        line-height: 23px;
        margin-bottom: 6px;
        height: 70px;
        overflow: hidden;
        &:lang(ar) {
          text-align: right;
        }
      }

      &-times {
        font-size: 13px;
        letter-spacing: 0.28px;
        line-height: 23px;

        color: lighten($color-text, 70%);
      }

      &:hover,
      &:focus {
        color: $color-text;
        text-decoration: none;
      }

      &:hover,
      &:focus {
        background: darken($color-base, 5%);
      }

      &-month {
        font-size: 13px;
        letter-spacing: 0.28px;
        line-height: 23px;

        color: lighten($color-text, 70%);

        text-transform: uppercase;
      }

      &-brief {
        font-size: 14px;
        letter-spacing: 0.3px;
        line-height: 23px;

        color: lighten($color-text, 30%);

        margin: 10px 0 0 0;
        display: block;

        &:lang(ar) {
          letter-spacing: 0;
        }
      }
    }

    &-scroll {
      position: relative;
      top: 20px;
      width: 100%;

      @media (min-width: $screen-sm-min) {
        top: 35px;
      }
    }

    &-scrollbar {
      height: 4px;
      width: 100%;

      cursor: pointer;
      background: rgba($color-text, 0.1);

      &-handle {
        height: 100%;
        width: 100%;

        background: $color-base;

        transition: background 0.2s;

        &:hover {
          background: rgba($color-base, 0.8);
        }
      }
    }

    &-control {
      &-item {
        position: absolute;
        display: none;
        left: 0;
        bottom: 15px;
        height: 40px;
        width: 60px;
        z-index: 10;
        align-items: center;
        justify-content: center;

        color: $color-text;
        background: $color-base;

        font-size: 15px;

        transition: width 0.2s;

        @media (min-width: $screen-sm-min) {
          display: flex;
        }

        &.-next {
          left: auto;
          right: 0;
        }

        &:hover {
          color: inherit;
        }

        &.-hidden {
          width: 0;
        }
      }
    }

    &-more {
      margin: 40px 0 0 0;
    }

    &.-vertical {
      background: $color-fill;

      #{$this} {
        &-header {
          position: static;
        }

        &-items {
          font-size: 0;
          margin: 0 -10px;
          padding: 23px 0 0 0;
        }

        &-item {
          margin: 10px;
          //width:calc(100% - 20px);

          //@media (min-width:$screen-sm-min) {
          //    width:calc(50% - 20px);
          //}
          //
          //@media (min-width:$screen-md-min) {
          //    width:calc(100% - 20px);
          //}
          //
          //@media (min-width:$screen-lg-min) {
          //    width:calc(50% - 20px);
          //}
          //
          //@media (min-width:$screen-xl-min) {
          //    width:calc(50% - 20px);
          //}
        }
      }
    }
  }

  &-comments {
    margin: 75px 0 0 0;

    &-header {
      h2,
      h3,
      h4 {
        display: inline-block;
        padding: 0;
        margin: -2px 22px 0 0; // -2 extra

        font-size: 26px;
        font-weight: 900;
        line-height: 30px;
        vertical-align: middle;

        @media (min-width: $screen-xs-min) {
          font-size: 33px;
          line-height: 36px;
        }

        @media (min-width: $screen-sm-min) {
          font-size: 40px;
          line-height: 49px;
        }

        &:lang(ar) {
          margin: -2px 0 0 22px; // -2 extra
        }
      }
    }

    &-box {
      margin: 13px 0 0 0;
    }
  }

  &-count {
    display: inline-block;
    height: 30px;
    width: 30px;

    font-size: 13px;
    font-weight: bold;
    letter-spacing: 0;
    line-height: 30px;
    text-align: center;
    vertical-align: middle;

    color: $color-base;

    @media (min-width: $screen-xs-min) {
      height: 34px;
      width: 34px;

      font-size: 16px;
      line-height: 34px;
    }

    @media (min-width: $screen-sm-min) {
      height: 38px;
      width: 38px;

      font-size: 16px;
      line-height: 38px;
    }
  }

  &-footer {
    display: block;
    padding: 60px 0;
    width: 100%;

    @media (min-width: $screen-sm-min) {
      display: table;
    }

    a {
      display: inline-block;

      color: inherit;
      font-size: 13px;
      font-weight: bold;
      letter-spacing: 1.5px;
      text-transform: uppercase;

      transition: all 0.2s;

      &:lang(ar) {
        letter-spacing: 0;
      }

      span {
        display: inline-block;
        padding: 0 15px;
        vertical-align: middle;
      }

      svg {
        display: inline-block;
        vertical-align: middle;
      }

      &:hover,
      &:focus,
      &:active {
        opacity: 0.7;
        color: inherit;
        text-decoration: none;
      }
    }

    &-col {
      display: block;
      vertical-align: middle;

      margin-bottom: 30px;

      @media (min-width: $screen-sm-min) {
        display: table-cell;
      }

      &.-right {
        text-align: right;

        @media (max-width: $screen-sm-min) {
          text-align: left;
        }

        &:lang(ar) {
          text-align: left;

          @media (max-width: $screen-sm-min) {
            text-align: right;
          }
        }
      }
    }
  }

  &-video {
    margin: 40px 0;
  }

  blockquote {
    font-size: 16px;
    letter-spacing: 0;
    line-height: 27px;

    padding: 0 0 0 34px;
    margin: 40px 0 25px;
    border-left: 3px solid;

    &:lang(ar) {
      padding: 0 34px 0 0;
      border-left: none;
      border-right: 3px solid;
    }
  }

  strong {
    font-weight: 700;
  }

  em {
    opacity: 0.6;
    font-style: italic;
  }

  ul li {
    list-style: square;
  }

  ol li span,
  ul li span {
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0;
    line-height: 22px;
  }

  &.-zoom {
    #{$root} {
      &-brief {
        font-size: 28px;
      }

      &-text {
        font-size: 19px;
      }
    }
  }

  /* Colors */
  &.-primary {
    #{$root} {
      &-related,
      &-count {
        background: $color-primary;
      }

      &-counts-like {
        &:hover,
        &:focus {
          color: $color-primary;
        }
      }

      &-tags-item,
      &-tabs-item:after {
        color: $color-primary;
      }

      &-info-link {
        color: $color-primary;
      }

      &-price {
        background: $color-primary;
      }

      &-brief a,
      &-text a,
      &-adress a {
        color: $color-primary;
      }
    }

    blockquote {
      border-color: $color-primary;
    }
  }

  &.-secondary {
    #{$root} {
      &-related,
