        color: $color-quaternary;
      }

      &-info-link,
      a {
        color: $color-quaternary;
      }

      &-price {
        background: $color-quaternary;
      }

      &-brief a,
      &-text a,
      &-adress a {
        color: $color-quaternary;
      }
    }

    blockquote {
      border-color: $color-quaternary;
    }
  }

  &.-quinary {
    #{$root} {
      &-related,
      &-count {
        background: $color-quinary;
      }

      &-counts-like {
