        &:focus {
          color: $color-quinary;
        }
      }

      &-tags-item,
      &-tabs-item:after {
        color: $color-quinary;
      }

      &-info-link,
      a {
        color: $color-quinary;
      }

      &-price {
        background: $color-quinary;
      }

      &-brief a,
