html {
    height:100%;
}

body {
    height:100%;
    margin:0;
    padding:0;

    background:$color-bg;
    color:$color-text;

    font-family:$font-family;
    font-size:$font-size;
    letter-spacing:$letter-spacing;
    line-height:$line-height;

    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing:grayscale;
    -ms-overflow-style:none;

    &:lang(ar) {
        font-family:$font-family-tertiary;
        font-weight:200;
        letter-spacing:0;
    }

    &::-webkit-scrollbar {
        width:0px;
        background:transparent;
    }
}

a {
    color:$color-link;
    cursor:pointer;

    &:hover, &:focus {
        color:$color-link-hover;
        text-decoration:underline;
    }
}

* {

    &:focus {
        outline:none !important;
    }
}

*::selection {
    background:$color-primary;
    background:rgba(0, 0, 0, 0.3);
    color:#fff;
}

*::-moz-selection {
    background:$color-primary;
    color:#fff;
}

.rd-article-info-text{
    &:lang(ar) {
        float: right;
        margin: 10px 0;
    }

    &:lang(en) {
        float: left;
        margin: 10px 0;
    }
}

.rd-article-info-img{
    &:lang(ar) {
        float: right;
        margin-left: 10px;
    }

    &:lang(en) {
        float: left;
        margin-right: 10px;
    }
}
#entity_image {
    &:lang(ar) {
    }

    &:lang(en) {
    }
}

#entity_image_upload {
    &:lang(ar) {
        float: left;
        margin-bottom: 30px;
    }

    &:lang(en) {
        float: right;
        margin-bottom: 30px;
    }
}

.upload-btn-wrapper {
    position: relative;
    overflow: hidden;
    display: inline-block;
}

.btn {
    border: 2px solid gray;
    color: gray;
    background-color: white;
    padding: 8px 20px;
    border-radius: 8px;
    font-size: 20px;
    font-weight: bold;
}

.upload-btn-wrapper input[type=file] {
    font-size: 100px;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
}

.spanx {
    &:lang(ar) {
        padding-right: 5px;
    }

    &:lang(en) {
        padding-left: 5px;
    }
}