.rd-object {
    position:relative;
    display:block;
    padding:20px 0;
    color:inherit;

    transition:opacity 0.2s;

    &:before {
        content:"";
        display:block;
        position:absolute;
        top:0;
        left:-30%;
        right:0;
        bottom:0;
        width:0%;
        z-index:-1;

        background:$color-fill;

        transition:cubic-bezier(.78, .31, .16, .85) 0.4s;
    }

    &:lang(ar):before {
        left:auto;
        right:-30%;
    }

    &-inner {
        display:table;
        max-width:420px;
    }

    &-col {
        display:table-cell;
        vertical-align:top;

        &.-middle {
            vertical-align:middle;
        }
    }

    &-img {
        position:relative;
        display:inline-block;
        height:70px;
        width:70px;
        margin:0 25px 0 0;

        background-color: lighten($color-text, 21%) - (lighten($color-text, 3%) / 2);
        background-size:cover;
        background-position:center center;
        background-repeat:no-repeat;

        line-height:70px;
        text-align:center;

        &[style] {
            background-color:transparent;
        }

        &:lang(ar) {
            margin:0 0 0 25px;
        }

        svg {
            opacity:0.3;
            font-size:27px;
        }

        &.-xs {
            height:50px;
            width:50px;
        }
    }

    &-title {
        display:block;

        &-inner {
            display:table;
        }

        &-col {
            display:table-cell;
            vertical-align:middle;
        }

        &-mark {
            display:inline-block;
            margin:0 12px 0 0;
            padding:3px;
            height:24px;
            width:24px;
            vertical-align:middle;
            text-align:center;

            color:$color-base;
            background:$color-tertiary;

            &:lang(ar) {
                margin:0 0 0 12px;
                padding:1px;
            }

            i, svg {
                font-size:13px;

                &:lang(ar) {
                    margin:-6px 0 0 0;
                }
            }
        }

        &-caption {
            display:inline-block;
            vertical-align:middle;

            font-size:18px;
            font-weight:bold;
            letter-spacing:0.39px;
            line-height:23px;

            mark {
                background:$color-primary;
                color:$color-base;
            }
        }

        &-name {
            font-size:16px;
            font-weight:bold;
            letter-spacing:0.39px;
            line-height:26px;

            mark {
                background:$color-primary;
                color:$color-base;
            }
        }
    }

    &-cat {
        display:block;
        margin:8px 0 0 0;

        color:darken($color-base, 50%);

        &-item {
            display:inline-block;
            font-size:13px;
            letter-spacing:0.28px;
            line-height:1;
        }

        &-sep {
            position:relative;
            display:inline-block;
            margin:0 11px 0 11px;
            top:-1px; // -1 extra

            font-size:10px;
            line-height:1;

            &:lang(ar) {
                transform:rotate(180deg);
            }
        }
    }

    &-metro {
        display:block;
        margin:8px 0 0 0;

        &-line {
            display:inline-block;
            width:7px;
            height:7px;

            border-radius:50px;
        }

        &-title {
            display:inline-block;
            margin:0 0 0 8px;

            font-size:13px;
            letter-spacing:0.28px;
            line-height:1;

            color:darken($color-base, 40%);

            &:lang(ar) {
                margin:0 8px 0 0;
            }
        }
    }

    &-time {
        display:block;
        margin:13px 0 11px 0;

        font-size:13px;
        font-weight:500;
        line-height:1;

        svg {
            margin:-1px 8px 0 0;
            font-size:16px;

            &:lang(ar) {
                margin:-1px 0 0 8px;
            }
        }
    }

    &-collapse {
        display:block;
        opacity:0;
        max-height:0px;
        overflow:hidden;

        transition:all 0.5s;

        &.-visible {
            opacity:1;
            max-height:200px;
        }
    }

    &-info {
        display:block;
        margin:8px 0 0 0;

        opacity:0.4;

        font-size:13px;
        letter-spacing:0.28px;
        line-height:20px;

        p {
            padding:0;
            margin:0;
        }
    }

    &-brief {
        display:block;
        margin:8px 0 0 0;

        color:darken($color-base, 50%);

        font-size:13px;
        font-weight:400;
        line-height:20px;

        @media (min-width:$screen-md-min) {
            font-size:14px;
            font-weight:normal;
            line-height:23px;
        }

        &.-inverse {
            color:lighten($color-text, 30%);
            max-width:325px;
        }

        p {
            padding:0;
            margin:0;
        }

        mark {
            background:none;
            font-weight:700;
            color:inherit;
        }
    }

    &-details {
        display:block;
        margin:16px 0 0 0;

        &-item {
            display:block;
            margin:0 30px 5px 0;

            font-size:13px;
            font-weight:500;
            line-height:24px;

            &:lang(ar) {
                margin:0 0 5px 30px;
            }
            &:lang(ar) > span {
                unicode-bidi: plaintext;
            }
            i, svg {
                margin:0 8px 0 0;
                font-size:16px;

                &:lang(ar) {
                    margin:0 0 0 8px;
                }
            }

            @at-root a#{&} {
                color:inherit;

                &:hover, &:focus {
                    color:inherit;
                }
            }

            &.-inline {
                display:inline-block;
            }
        }
    }

    @at-root a#{&}, &.-hover {
        cursor:pointer;

        &:hover, &:focus {
            opacity:0.8;
            color:inherit;
            text-decoration:none;
        }
    }

    &.-inverse {
        &:before {
            background:#202020;
        }
    }

    &.-expand {
        opacity:1 !important;

        &:before {
            width:150%;
        }
    }

    &.-expand &-collapse {
        @extend .-visible;
    }

    &.-active {
        &:before {
            width:400px;
        }
    }
}
