@media print {
    * {
        -webkit-print-color-adjust:exact !important;
        color-adjust:exact !important;
    }

    body * {
        visibility:hidden;
        overflow:visible !important;
        -webkit-print-color-adjust:exact;
    }

    .rd-wrap > * {
        height:auto !important;
    }

    .rd-article {
        color:#000;
        visibility:visible;
        page-break-before:always;

        &-wrap {
            padding:0;
        }

        &-tags {
            padding:0;

            &:before {
                display:none !important;
            }
        }

        * {
            visibility:visible;
        }

        ul, img {
            page-break-inside:avoid;
        }
    }

    .rd-intro, .rd-layout, .rd-splitbox-left, .rd-infobox-left, .rd-comments-form {
        display:none !important;
    }

    .rd-splitbox-right, .rd-infobox-right {
        display:block !important;
        width:100% !important;
    }

    .rd-modal {
        position:static;
    }
}