/* Roboto */
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900');

/* Mada */
@import url('https://fonts.googleapis.com/css?family=Mada:900&subset=arabic');

/* RiyadhCity */
@font-face {
    font-family:'Riyadhcity';
    src:url('../fonts/riyadhcity/Riyadhcity-SemiLight.eot');
    src:url('../fonts/riyadhcity/Riyadhcity-SemiLight.eot?#iefix') format('embedded-opentype'),
    url('../fonts/riyadhcity/Riyadhcity-SemiLight.woff') format('woff'),
    url('../fonts/riyadhcity/Riyadhcity-SemiLight.ttf') format('truetype');
    font-weight:200;
    font-style:normal;
}

@font-face {
    font-family:'Riyadhcity';
    src:url('../fonts/riyadhcity/Riyadhcity-Light.eot');
    src:url('../fonts/riyadhcity/Riyadhcity-Light.eot?#iefix') format('embedded-opentype'),
    url('../fonts/riyadhcity/Riyadhcity-Light.woff') format('woff'),
    url('../fonts/riyadhcity/Riyadhcity-Light.ttf') format('truetype');
    font-weight:300;
    font-style:normal;
}

