.rd-accordion {

    &_metro {
        $this:&;

        &-wrap {
            padding:0 0 80px 0;
        }

        &-grid {
            display:flex;
            justify-content:space-between;
            align-items:flex-start;
        }

        &-icon {
            position:relative;

            font-size:12px;

            color:$color-base;

            svg {
                transition:.25s;

                &:last-child {
                    display:none;
                }
            }

            &.-active {

                svg {

                    &:first-child {
                        display:none;
                    }

                    &:last-child {
                        display:block;
                    }
                }
            }
        }

        &-header {
            padding:30px 0 20px 0;

            line-height:1;

            cursor:pointer;

            &:hover &-title {

                &:before {
                    transform:scaleX(1);
                }
            }

            &-mark {
                display:inline-block;
                vertical-align:middle;
                width:7px;
                height:7px;
                margin:0 16px 0 0;

                border-radius:50px;

                &:lang(ar) {
                    margin:0 0 0 16px;
                }
            }

            &-title {
                position:relative;
                display:inline-block;
                vertical-align:middle;

                font-size:13px;
                //catch from prod <-- start
                letter-spacing: 0;
                //catch from prod <-- end
                font-weight:700;
                text-transform:uppercase;

                color:$color-base;

                &:Hover, &:focus {
                    text-decoration:none;

                    color:$color-base;
                }

                &:before {
                    content:'';
                    position:absolute;
                    left:0;
                    bottom:-6px;
                    width:100%;
                    height:2px;

                    background:rgba($color-base, 0.6);

                    transform:scaleX(0);
                    transform-origin:left center;

                    transition:transform .3s;
                }

                &:after {
                    content:'';
                    position:absolute;
                    left:0;
                    bottom:-6px;
                    width:100%;
                    height:2px;

                    background:rgba($color-base, 0.3);
                }

                span {
                    color:#8B8B8B;
                }
            }

            &:lang(ar){
                .rd-accordion_metro-header-title:before{
                    left: auto;
                    right: -100%;

                }

                &:hover .rd-accordion_metro-header-title:before {
                    transform: scaleX(-1);
                }
            }
        }

        &-body {
            display:none;
        }

        &.-disabled {

            #{$this} {

                &-header {

                    &-title {

                        &:before {
                            content:none;
                        }

                        &:after {
                            content:none;
                        }
                    }
                }

                &-body {
                    display:block;
                }
            }
        }
    }
}
