.rd-burger {
  $root: &;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  overflow: hidden;

  @media (min-width: $screen-lg-min) {
    display: none !important;
  }

  &-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;

    opacity: 0;
    background: rgba($color-text, 0.3);

    transition: all 0.4s;
  }

  &-close {
    position: absolute;
    top: 0;
    left: 0;
    padding: 22px 21px;
    z-index: 10;

    cursor: pointer;
    font-size: 18px;
    line-height: 1;

    @media (min-width: $screen-sm-min) {
      padding: 36px 33px;
    }

    &:hover {
      opacity: 0.8;
    }
  }

  &-wrap {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;

    background: #fff;
    color: $color-text;
    vertical-align: middle;
    white-space: nowrap;

    @media (min-width: $screen-sm-min) {
      max-width: 430px;
    }

    &::-webkit-scrollbar {
      width: 0;
      background: none;
    }
  }

  &-container {
    padding: 0 20px;

    @media (min-width: $screen-sm-min) {
      padding: 0 33px;
    }
  }

  &-link {
    margin: 20px 0 0 0;
    text-align: right;

    @media (min-width: $screen-sm-min) {
      margin: 35px 0 0 0;
    }

    &-item {
      position: relative;
      display: inline-block;
      padding: 0 12px;

      color: inherit;
      font-size: 17px;
      font-weight: bold;
      letter-spacing: 1.66px;
      line-height: 1;
      vertical-align: middle;

      transition: opacity 0.2s, color 0.2s;

      &:first-child {
        padding-left: 0;
      }

      &:last-child {
        padding-right: 0;
      }

      &:hover,
      &:focus {
        color: inherit;
        opacity: 0.8;
        text-decoration: none;
      }

      &.-home,
      &.-twitter {
        font-size: 19px;
      }

      &.-lang {
        font-size: 14px;

        svg {
          display: inline-block;
        }

        span {
          display: none;

          font-family: $font-family;
          font-size: 16px;
          font-weight: 700;
          letter-spacing: 1.85px;
          line-height: 19px;
        }

        &:lang(ar) {
          svg {
            display: none;
          }

          span {
            display: inline-block;
            vertical-align: middle;
          }
        }
      }
    }
  }

  &-menu {
    margin: 35px 0 0 0;

    &:lang(ar) {
      direction: rtl;
    }

    &-item {
      $this: &;
      position: relative;
      margin: 15px 0;

      &-text {
        position: relative;
        display: inline-block;
        padding: 10px 20px;

        color: $color-text;

        font-size: 16px;
        font-weight: bold;
        line-height: 1.1;

        @media (min-width: $screen-sm-min) {
          padding: 10px 33px;
          font-size: 20px;
        }

        span {
          position: relative;
          z-index: 2;
        }

        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          height: 100%;
          width: 50px;
          z-index: 0;
          transform: scaleX(0);
          transform-origin: left center;

          background: $color-fill;

          transition: all 0.2s;

          @media (min-width: $screen-sm-min) {
            width: 70px;
          }
        }

        &:lang(ar):before {
          left: auto;
          right: 0;
          transform-origin: right center;
        }

        &:hover,
        &:focus {
          color: inherit;
          text-decoration: none;

          &:before {
            transform: scaleX(0.7);
          }
        }
      }

      &-arr {
        display: inline-block;
        float: right;
        padding: 20px;
        vertical-align: middle;

        color: $color-text;
        font-size: 8px;
        line-height: 1;

        &:lang(ar) {
          float: left;
        }

        svg {
          transition: all 0.2s;
          transform-origin: center center;
        }

        &:hover,
        &:focus {
          color: inherit;
          text-decoration: none;
        }
      }

      &-sub {
        max-height: 0;
        width: 100%;
        overflow: hidden;
        white-space: normal;

        transition: max-height 0.4s;

        &:after {
          content: '';
          display: block;
          clear: both;
        }

        &-item {
          display: block;
          margin: 10px 0;
          float: left;
          width: 33%;
          height: 80px;
          text-align: center;

          color: $color-text;
          opacity: 0.4;

          &:lang(ar) {
            float: right;
          }

          &-icon {
            display: block;
            margin: 25px 0 0 0;

            font-size: 27px;
            line-height: 1;
          }

          &-text {
            display: block;
            margin: 13px 0 0 0;

            font-size: 12px;
            font-weight: 500;
            letter-spacing: 0.2px;
            line-height: 27px;
          }

          &:hover,
          &:focus {
            opacity: 0.5;
            color: inherit;
            text-decoration: none;
          }

          &.-active {
            color: $color-base;
            opacity: 1;
          }
        }
      }

      &.-open &-arr svg {
        transform: rotate(180deg);
      }

      &.-open &-sub {
        max-height: 500px;
      }

      &.-active {
        #{$this} {
          &-text {
            &:before {
              transform: scale(1);
            }
          }
        }
      }

      &.-primary {
        #{$this} {
          &-sub-item {
            &.-active {
              background: $color-primary;
            }
          }
        }
      }

      &.-secondary {
        #{$this} {
          &-sub-item {
            &.-active {
              background: $color-secondary;
            }
          }
        }
      }

      &.-tertiary {
        #{$this} {
          &-sub-item {
            &.-active {
              background: $color-tertiary;
            }
          }
        }
      }

      &.-quaternary {
        #{$this} {
          &-sub-item {
            &.-active {
              background: $color-quaternary;
            }
          }
        }
      }

      &.-quinary {
        #{$this} {
          &-sub-item {
            &.-active {
              background: $color-quinary;
            }
          }
        }
      }

      &.-senary {
        #{$this} {
          &-sub-item {
            &.-active {
              background: $color-senary;
            }
          }
        }
      }
    }
  }

  &-forecast {
    //display:table;
    //margin:30px -20px 0 -20px;
    display: block;
    margin: 30px 0px 0 -20px;
    overflow: auto;
    width: calc(100% + 40px);

    &-col {
      display: table-cell;
      vertical-align: middle;
      text-align: center;

      padding: 0 20px;
    }

    &-title {
      opacity: 0.45;
      font-size: 11px;
      font-weight: 700;
      letter-spacing: 0.15px;
    }

    &-icon {
      margin: 15px 0 0 0;

      opacity: 0.8;
      font-size: 21px;
      line-height: 1;
    }

    &-temp {
      margin: 8px 0 0 0;

      opacity: 0.45;
      font-size: 13px;
      font-weight: bold;
      letter-spacing: 0.22px;
      line-height: 31px;
    }
  }

  &-time {
    display: table;
    width: 100%;
    margin: 30px 0;

    &-row {
      display: table-row;
    }

    &-title {
      display: table-cell;
      vertical-align: middle;

      opacity: 0.45;
      font-size: 13px;
      font-weight: 500;
      letter-spacing: 0.22px;
      line-height: 35px;
    }

    &-time {
      display: table-cell;
      vertical-align: middle;

      font-size: 13px;
      font-weight: 500;
      letter-spacing: 0.22px;
      line-height: 35px;
      text-align: right;

      &:lang(ar) {
        text-align: left;
      }
    }
  }

  &.-open {
    display: block;

    #{$root} {
      &-backdrop {
        opacity: 1;
      }

      &-wrap {
        animation: rd-burder-show-simple 0.3s;

        @media (min-width: 385px) {
          animation: rd-burder-show 0.3s;
        }
      }
    }
  }
}

@keyframes rd-burder-show {
  0% {
    opacity: 0;
    transform: scaleX(0);
    transform-origin: left center;
  }

  100% {
    opacity: 1;
    transform: scaleX(1);
    transform-origin: left center;
  }
}

@keyframes rd-burder-show-simple {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
