.rd-objectlist {
    $root:&;

    &:lang(ar) {
        direction:rtl;
    }

    &-wrap {
        margin:60px 0 10px 0;

        @media (min-width:$screen-sm-min) {
            margin:127px 0 20px 0;
        }

        @media (min-width:$screen-md-min) {
            margin:250px 0 20px 0;
        }

        @media (min-width:$screen-lg-min) {
            margin:310px 0 30px 0;
        }
    }

    &-container {
        margin:0 0 0 40px;
    }

    &-header {
        max-width:500px;

        h1, h2, h3, h4 {
            padding:0;
            margin:0;

            font-size:26px;
            font-weight:900;

            @media (min-width:$screen-sm-min) {
                font-size:40px;
                line-height:49px;
            }

            @media (min-width:$screen-lg-min) {
                font-size:60px;
                line-height:67px;
            }

            span {
                display:block;
            }
        }
    }

    &-filter {
        max-width:500px;
        margin:12px -10px 0 -10px;

        &-item {
            $this:&;
            display:inline-block;
            margin:0 10px;

            color:inherit;

            font-size:13px;
            letter-spacing:0.28px;
            line-height:23px;
            vertical-align:middle;

            transition:opacity 0.2s;

            &-text {
                display:inline-block;
                opacity:0.4;
                vertical-align:middle;
            }

            &-count {
                display:inline-block;
                padding:0 8px;
                vertical-align:middle;
            }

            &-del {
                display:inline-block;
                vertical-align:middle;
                font-size:10px;
                line-height:1;
            }

            &:hover, &:focus {
                opacity:0.7;
                color:inherit;
                text-decoration:none;
            }
        }
    }

    &-search {
        margin:40px 50px 0 0;
    }

    &-img{
        &[style]{
            background-color: transparent;
        }
    }

    &-items {
        margin:40px 0 0 0;

        &.-xs {
            margin:0;
        }
    }

    &-item {
        position:relative;
    }

    /* Colors */
    &.-primary {
        #{$root} {
            &-header b, &-img {
                color:$color-primary;
            }
        }
    }

    &.-secondary {
        #{$root} {
            &-header b, &-item-img {
                color:$color-secondary;
            }
        }
    }

    &.-tertiary {
        #{$root} {
            &-header b, &-item-img {
                color:$color-tertiary;
            }
        }
    }

    &.-quaternary {
        #{$root} {
            &-header b, &-item-img {
                color:$color-quaternary;
            }
        }
    }

    &.-quinary {
        #{$root} {
            &-header b, &-item-img {
                color:$color-quinary;
            }
        }
    }

    &.-senary {
        #{$root} {
            &-header b, &-item-img {
                color:$color-senary;
            }
        }
    }
}
