.rd-avatar {

    &-wrap {
        display:table;
        width: 60px;
        height: 60px;
        margin: 0 auto 28px;

        background:$color-base;
        box-shadow: 0 0 20px rgba(0,0,0,.05);

        @media (min-width:$screen-sm-min) {
            width: 100px;
            height: 100px;
        }

        &.-xs {
            width:30px;
            height:30px;
            border-radius:100%;

            display:table;

            .rd-avatar-icon {
                font-size:13px;
            }
        }

        &.-sm {
            width:30px;
            height:30px;
            border-radius:100%;

            display:table;

            &-icon {
                font-size:13px;
            }

            .rd-avatar-icon {
                font-size:13px;
            }
        }

        &.-md {
            width:70px;
            height:70px;

            margin:0 20px 0 0;
        }

        &.-lg {
            width:80px;
            height:80px;
        }

        &.-inverse {
            background:#353535;
        }
    }

    &-inner {
        display:table-cell;
        vertical-align:middle;
        text-align:center;
        width:100%;
        height:100%;
    }

    &-icon {
        font-size:18px;
        color:lighten($color-text, 70%);

        @media (min-width:$screen-sm-min) {
            font-size:26px;
        }
    }
}
