.rd-loginbox {
    height: 100%;
    width:100%;
    background: $color-primary;
    display: table;

    &:lang(ar) {
        direction:rtl;
    }

    &.-overlay {
        background:rgba(147, 194, 82, .95);

        position:fixed;
        left:0;
        top:0;
        bottom:0;
        right:0;

        opacity:0;
        z-index:-1;
        display:block;
    }

    &.-visible {
        opacity:1;
        z-index:9999;
    }

    &-wrap {
        display:table;
        height:100%;
        width:100%;

        color:$color-base;

        &:lang(ar) {
            margin:50px 0 0 0;

            @media (min-width:$screen-sm-min) {
                margin:0;
            }

        }

    }

    &-inner {
        display:table-cell;
        vertical-align:middle;

    }

    &-container {
        padding: 0 60px 0 60px;

        @media (max-width:$screen-xs-min) {
            padding: 30px 20px 0 20px;
        }
        @media (min-width:$screen-sm-min) {
            padding: 0 0 0 130px;
        }

        @media (min-width:$screen-md-min) {
            padding: 0 0 0 60px;
        }

        @media (min-width:$screen-lg-min) {
            padding: 0 0 0 130px;
        }

        &:lang(ar) {
            @media (min-width:$screen-sm-min) {
                padding: 0 130px;
            }

            @media (min-width:$screen-md-min) {
                padding: 0 60px;
            }

            @media (min-width:$screen-lg-min) {
                padding: 0 130px;
            }
        }

        &-message {

            padding: 0 60px 0 60px;

            @media (max-width:$screen-sm-min) {
                text-align: center;
            }
        }

        &.-center {
            text-align:center;

            &:lang(ar) {
                text-align:right;
            }
        }

        &.-lg {
            padding:0;
            margin:0 20px;

            @media (min-width:$screen-xs-min) {
                margin:0 40px;
            }
            @media (min-width:$screen-sm-min) {
                max-width:633px;
                margin:0 auto;
            }
        }

    }

    &-form {
        min-width:400px;
        max-width:400px;
        position: relative;

        @media (max-width:$screen-sm-min) {
            min-width:1px;
            max-width:335px;
        }
    }

    &-logo {

        &-img {
            font-size:49px;
        }

        &-box {
            width:49px;
            margin:-12px;

            span {
                display:inline-block;
                height:8px;
                width:8px;
                margin:12px;
                border-radius:2.4px;
                background:#fff;
            }
        }

        &-text {
            margin:20px 0 0 0;
            font-size:37px;
        }
    }

    &-error {
        font-size:140px;
        line-height:198px;
        font-weight:900;

        @media (min-width:$screen-sm-min) {
            font-size:280px;
            line-height:328px;
        }

    }

    &-title {
        font-size: 60px;
        font-weight: 900;
        margin: -9px 0 10px;
        max-width: 270px;
        line-height: 67px;

        @media (max-width:$screen-sm-min) {
            font-size: 26px;
            line-height: 30px;
        }

        &:lang(ar) {
            max-width: 290px;
        }

        &.-full {
            max-width:initial;
        }

        &.-sm {
            max-width:290px;
        }
    }

    &-description {
        color: rgba($color-base, .7);
        line-height: 29px;
        font-size: 16px;

        @media (max-width:$screen-sm-min) {
            font-size: 14px;
        }

        &:lang(ar) {
            letter-spacing:.57px;
        }

        &.-sm {
            max-width:471px;
            margin:0 auto 20px;

            @media (min-width:$screen-md-min) {
                margin:0 auto 40px;
            }

            &:lang(ar) {
                margin:0 0 40px 0;
            }
        }
    }

    &-link {
        color: $color-base;
        letter-spacing: 0.57px;
        line-height: 29px;
        transition: all .2s;
        font-size: 16px;

        position: relative;

        transition: all .2s;

        @media (max-width:$screen-sm-min) {
            font-size: 14px;
        }

        &:before {
            content: '';
            position: absolute;
            left: 0;
            bottom: -5px;
            height: 2px;
            width: 100%;
            background: $color-base;
            transform: scaleX(0);
            transform-origin:right center;

            transition:transform 0.3s;
        }

        &:hover, &:focus, &:active {
            color: $color-base;
            text-decoration: none;

            &:before {
                transform: scaleX(1);
                transform-origin:left center;
            }
        }
    }

    &-icon {
        font-size: 63px;
        text-align: center;
    }

    &-social {

        margin-bottom: 22px;

        &-list {
            direction:initial;

            &:lang(ar) {

                text-align:right;
            }
        }

        &-item {
            display:inline-block;
            margin-right:17px;
            vertical-align:middle;

            &:lang(ar) {

                &:last-child {
                    margin-right:0;
                }
            }
        }

        &-link {
            position:relative;

            font-size:17px;
            line-height:1;
            color: $color-base;

            perspective: 1000px;

            &.-google-plus {
                font-size:22px;
            }

            > span {
                display:inline-block;
                position: relative;
                transform-origin: 50% 0;
                transform-style: preserve-3d;
                transition:all 0.25s ease-in-out;

                > span {
                    position: absolute;
                    top: 110%;
                    left: 0;
                    width: 100%;
                    height: 100%;

                    opacity:0;
                    transform-origin: 50% 0;
                    transform: rotateX(-90deg);
                    transition:all 0.25s ease-in-out;
                }
            }

            &:hover > span {
                transform: rotateX(90deg);
                color: $color-base;

                > span {
                    opacity:1;
                    transition-delay: 0.05s;
                    transform: rotateX(-90deg);
                    color: $color-base;
                }
            }

            &:active, &:focus {
                color:$color-base;
            }
        }

    }

    &-or {
        margin-bottom: 22px;
        position: relative;

        font-size:16px;
        letter-spacing: .57px;
        line-height: 29px;
        color: rgba($color-base, .5);

        &:after {
            content: '';
            position: absolute;
            height: 1px;
            background: rgba($color-base, .2);
            width: 70px;
            left: 30px;
            top: 15px;
        }

        &:lang(ar) {

            &:after {
                right: 30px;
            }
        }
    }

    &-message {
        display: table;
        height: 100%;

        @media (max-width:$screen-sm-min) {
            display: block;
        }

        &-col {
            display:table-cell;
            padding:0 48px;
            vertical-align:middle;

            @media (max-width:$screen-sm-min) {
                display: block;
                margin-bottom: 35px;
                padding: 0;
            }

            button {
                margin:40px auto 0;
                display:block;

                @media (min-width:$screen-sm-min) {
                    margin:40px 0 0 0;
                }
            }
        }

        &-icon {
            font-size: 86px;
            text-align: center;
        }

        &-text {
            display: inline-block;
            max-width: 585px;
            margin:20px 0 0 0;

            font-size:60px;
            font-weight: bold;
            line-height: 67px;

            @media (max-width:$screen-sm-min) {
                font-size: 26px;
                line-height: 30px;
                text-align: center;
            }

            &:lang(ar) {
                letter-spacing:0;
                max-width:620px;
            }

            mark {
                display:block;
                padding:0;
                background:none;
                border:none;
                color:rgba($color-base, 0.6);
            }

            &.-xl {
                max-width:852px;
            }
        }

    }


    &-restore {
        display: table;
        height: 100%;

        @media (max-width:$screen-md-min) {
            display: block;
        }

        &-col {
            display: table-cell;
            padding: 0 149px 0 0;
            vertical-align: top;

            @media (max-width:$screen-md-min) {
                display: block;
                margin-bottom: 55px;
                padding: 0;
            }

            &:lang(ar) {
                padding: 0 0 0 149px;

                &:last-child {
                    padding:0;
                }

                @media (max-width:$screen-md-min) {
                    display: block;
                    margin-bottom: 55px;
                    padding: 0;
                }
            }
        }

    }

    &-register {
        display: table;
        height: 100%;

        @media (max-width:$screen-md-min) {
            display: block;
        }

        &-col {
            display: table-cell;
            padding: 0 200px 0 0;
            vertical-align: top;

            @media (max-width:$screen-md-min) {
                display: block;
                margin-bottom: 55px;
                padding: 0;
            }

            &:lang(ar) {
                padding: 0 0 0 200px;

                &:last-child {
                    padding:0;
                }

                @media (max-width:$screen-md-min) {
                    display: block;
                    margin-bottom: 55px;
                    padding: 0;
                }
            }
        }

    }

    &-login {
        display: table;
        height: 100%;

        @media (max-width:$screen-md-min) {
            display: block;
        }

        &-col {
            display: table-cell;
            padding: 0 200px 0 0;
            vertical-align: top;

            @media (max-width:$screen-md-min) {
                display: block;
                margin-bottom: 55px;
                padding: 0;
            }

            &:lang(ar) {
                padding: 0 0 0 65px;

                &:last-child {
                    padding:0;
                }

                @media (max-width:$screen-md-min) {
                    display: block;
                    margin-bottom: 55px;
                    padding: 0;
                }
            }
        }
    }
}
