.rd-btn {
    display:inline;

    &_primary {
        position:relative;
        padding:16px 25px;
        z-index:1;

        border:none;
        background:$color-primary;
        color:$color-base;

        font-size:13px;
        font-weight:600;
        letter-spacing:1.5px;
        line-height:1;
        text-transform:uppercase;

        transition:all .2s;

        &:lang(ar) {
            letter-spacing:initial;
            font-weight:700;
        }

        &:hover {
            opacity:.8;
        }

        &.-lg {
            width:100%;
            height:69px;
            font-size:14px;
        }

        &.-md {
            padding:17px 31px;
        }

        /* Colors */
        &.-secondary {
            background:$color-secondary;
        }

        &.-tertiary {
            background:$color-tertiary;
        }

        &.-quaternary {
            background:$color-quaternary;
        }

        &.-quinary {
            background:$color-quinary;
        }

        &.-senary {
            background:$color-senary;
        }

        &.-white {
            background:$color-base;
            color:$color-text;

            outline:1px $color-base solid;

            overflow:hidden;

            span {
                position:relative;
            }

            &:before {
                content:'';
                height:100%;
                width:100%;
                left:-100%;
                position:absolute;
                top:0;
                background:$color-primary;
                z-index:-1;
            }

            &:hover {
                opacity:1;

                &:before {
                    animation:rd-btn-primary-hover .5s forwards;
                }
            }

            &:lang(ar) {
                min-width:111px;
            }
        }

        &.-black {
            background:$color-text;
        }

        &.-error {
            opacity:.5;
            color:$color-primary;

            &:before {
                content:none;
            }
            &:hover, &:focus {
                opacity:.5;
            }
        }

        &.-xl {
            padding:17px 48px;
        }

        &:disabled {
            opacity:1 !important;
        }

        &.-loading {
            span {
                visibility:hidden;
            }

            &:after {
                content:"";
                display:inline-block;
                position:absolute;
                top:50%;
                left:50%;
                margin:-13px 0 0 -13px;
                height:26px;
                width:26px;

                border:2px solid;
                border-radius:50%;
                border-bottom-color:transparent;

                animation:rd-loader-rotation 0.7s linear infinite;
            }
        }
    }

    &_apply {
        position:relative;
        height:43px;
        padding:0 15px;
        z-index:1;

        background:$color-text;
        border:none;

        font-size:13px;
        font-weight:bold;
        letter-spacing:1.5px;
        line-height:1;
        text-transform:uppercase;

        transition:all 0.2s;

        &:before {
            content:'';
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            z-index:-1;
            transform:scaleX(0);
            transform-origin:right center;

            background:lighten($color-text, 15%);

            transition:transform .3s;
        }

        span {
            display:inline-block;
            position:relative;
            color:$color-base;
            z-index:1;
        }

        &.-base {
            background:$color-base;
        }

        &:hover, &:focus {
            &:before {
                transform:scaleX(1);
                transform-origin:left center;
            }
        }
    }

    &_zoom {
        display:block;
        height:41px;
        width:41px;

        color:$color-text;
        background:$color-base;
        border:0 $color-fill solid;
        border-radius:20px;
        outline:none;

        font-size:10px;
        line-height:1;

        transition:all 0.3s;

        &:hover, &:focus {
            background:darken($color-base, 5%);
        }

        &.-plus {
            border-bottom-width:1px;
            border-bottom-left-radius:0;
            border-bottom-right-radius:0;
        }

        &.-minus {
            border-top-width:1px;
            border-top-left-radius:0;
            border-top-right-radius:0;
        }

        &.-inverse {
            background:$color-text;
            border-color:rgba($color-base, 0.07);
            color:$color-base;

            &:hover, &:focus {
                background:lighten($color-text, 7%);
            }

            &:active {
                background:lighten($color-text, 10%);
            }
        }
    }

    &_rounded {
        height:41px;
        padding:0 20px;
        margin:0 5px;

        color:$color-text;
        background:$color-base;
        border:0;
        border-radius:20px;
        outline:none;

        font-size:13px;
        font-weight:500;
        line-height:1;

        transition:all 0.3s;

        svg {
            margin:0 9px 0 0;
            font-size:21px;
        }

        span + svg {
            margin:0 0 0 9px;

            &:lang(ar) {
                margin:0 0 0 9px;
            }

            &.-caret-down {
                &:lang(ar) {
                    margin:0 9px 0 0;
                }
            }
        }

        &:hover, &:focus {
            background:darken($color-base, 5%);
        }

        &:active {
            background:darken($color-base, 7%);
        }

        &.-responsive {
            @media (max-width:$screen-xs-max) {
                width:41px;
                padding:0;
                border-radius:50%;

                svg {
                    margin:0;
                }

                span {
                    display:none;
                }
            }
        }

        &.-responsive-lg {
            @media (max-width:$screen-lg-max) {
                width:41px;
                padding:0;
                border-radius:50%;

                svg {
                    margin:0;
                }

                span {
                    display:none;
                }
            }
        }

        &.-inverse {
            background:$color-text;
            color:$color-base;

            &:hover, &:focus {
                background:lighten($color-text, 7%);
            }

            &:active {
                background:lighten($color-text, 10%);
            }
        }

        &.-xs {
            padding:0;
            width:41px;

            text-align:center;

            svg, span {
                margin:0;
            }

            svg {
                font-size:17px;
            }
        }

        &.-active {
            background:$color-secondary !important;
            color:$color-text !important;
        }
    }

    &_round {
        display:inline-block;
        height:40px;
        width:40px;
        padding:0;

        background:none;
        box-shadow:inset 0 0 0 18px $color-base, 0 0 0 0 rgba(0, 0, 0, 0);
        border:0;
        border-radius:100%;
        color:$color-text;

        font-size:14px;
        line-height:1;
        vertical-align:middle;
        text-align:center;

        transition:box-shadow 0.4s, transform 0.2s;

        &:hover {
            box-shadow:inset 0 0 0 0 $color-base, 0 0 0 2px $color-base;
        }

        &:active {
            transform:rotate(20deg);
        }

        &.-responsive {
            height:32px;
            width:32px;
            font-size:13px;

            @media (min-width:$screen-sm-min) {
                height:40px;
                width:40px;
                font-size:14px;
            }
        }

        &.-inverse {
            box-shadow:inset 0 0 0 18px $color-text, 0 0 0 0 rgba(0, 0, 0, 0);
            color:$color-base;

            &:hover {
                box-shadow:inset 0 0 0 0 $color-text, 0 0 0 2px $color-text;
                color:$color-text;
            }
        }
    }

    &_square {
        display:inline-block;
        height:54px;
        min-width:54px;

        background:none;
        border:0;
        color:$color-base;
        padding:0 19px;

        font-size:13px;
        font-weight:bold;
        letter-spacing:1.5px;
        line-height:1;
        vertical-align:middle;
        text-align:center;
        white-space:nowrap;
        text-transform:uppercase;

        transition:all 0.4s;

        &:lang(ar) {
            vertical-align:baseline;
        }

        span {
            display:inline-block;
            max-width:0px;
            opacity:0;
            overflow:hidden;
            vertical-align:middle;
            transition:all 0.4s;
        }

        svg {
            font-size:14px;
            vertical-align:middle;

            &:lang(ar) {
                transform:rotate(180deg);
            }
        }

        &:hover, &:focus {
            padding:0 27px;

            span {
                opacity:1;
                max-width:100px;
                padding:0 14px 0 0;
            }
        }

        &:active {
            opacity:0.8;
        }

        &.-sm {
            background:$color-primary;
            min-width:24px;
            height:24px;
            line-height:24px;
            transform:translateX(30px);
            transition:.2s;

            padding:0;

            &:lang(ar) {
                transform:translateX(-30px);
            }

            svg {
                font-size:8px;
                vertical-align:baseline;
            }

            &.-visible {
                transform:translateX(0);
            }
        }

        &.-no {
            &:hover {
                padding:initial;
            }
        }

        &.-inverse {
            color:$color-text;
        }

        /* Colors */
        &.-primary {
            background:$color-primary;
        }

        &.-secondary {
            background:$color-secondary;
        }

        &.-tertiary {
            background:$color-tertiary;
        }

        &.-quaternary {
            background:$color-quaternary;
        }

        &.-quinary {
            background:$color-quinary;
        }

        &.-senary {
            background:$color-senary;
        }

        &.-seventh {
            padding:0;
            min-width:initial;
            width:38px;
            height:38px;
            background: rgba(255, 255, 255, 0.5);
        }
    }
}

@keyframes rd-btn-primary-hover {
    0% {
        left:-100%;
    }
    40%, 60% {
        left:0%;
    }

    100% {
        left:100%;
    }
}
