.rd-footer {
  $root: &;

  &:lang(ar) {
    direction: rtl;
    text-align: right;
  }

  &-wrap {
    padding: 25px 0 33px 0;

    @media (min-width: $screen-md-min) {
      padding: 50px 0 70px 0;
    }
  }

  &-container {
    @extend .rd-container;
  }

  &-copyright {
    &-text {
      margin: 25px 0 30px 0;

      font-size: 10px;
      letter-spacing: 0.5px;
      line-height: 16px;
      text-align: justify;

      &:lang(ar) {
        font-size: 12px;
        font-weight: 200;
      }
    }

    &-store {
      margin: 0 -5px 10px -5px;

      &-item {
        display: inline-block;
        padding: 0 5px;
      }
    }
  }

  &-nav {
    width: 100%;
    display: flex;
    margin: -15px 0;

    &-item {
      position: relative;
      display: block;
      margin: 15px 0;
      color: $color-text;
      font-size: 13px;
      letter-spacing: 0.5px;
      line-height: 25px;

      &:before {
        content: "";
        display: inline-block;
        background: lighten($color-text, 80%);
        height: 1px;
        width: 16px;
        margin: -1px 11px 0 0;
        vertical-align: middle;
        position: absolute;
        top: 13px;
        left: -23px;
      }

      &:lang(ar) {
        font-size: 15px;
        font-weight: 200;

        &:before {
          margin: -1px 0 0 11px;
          right: -26px;
        }
      }

      &:hover,
      &:focus {
        color: lighten($color-text, 40%);
        text-decoration: none;
      }
    }

    &-col {
      display: block;
      padding-left: 23px;
      $indent: 10px;
      width: calc(50% - #{$indent});

      @media (min-width: $screen-lg-min) {
        display: table-cell;
      }
    }
  }

  &-subscribe {
    margin: 45px 0 0 0;

    @media (min-width: $screen-md-min) {
      margin: 0;
      //text-align:right;
      float: right;
    }

    &-header {
      display: inline-block;
      max-width: 239px;

      font-size: 26px;
      font-weight: 900;

      @media (min-width: $screen-lg-min) {
        font-size: 30px;
        font-weight: 900;
        line-height: 37px;
      }
    }

    &-description {
      max-width: 276px;
      margin: 14px 0 0 0;

      font-size: 13px;
      letter-spacing: 0.5px;
      line-height: 22px;

      color: lighten($color-text, 30%);

      &:lang(ar) {
        font-size: 15px;
        font-weight: 200;
        letter-spacing: 0;
      }
    }

    &-form {
      overflow: hidden;
      margin: 30px 0 0 0;

      input {
        @media (max-width: $screen-md-min) {
          width: 100%;
          max-width: 100% !important;
        }
      }
    }
  }

  &-separator {
    margin: 40px 0 0 0;
  }

  &-line {
    @media (min-width: $screen-sm-min) {
      width: 100%;
      height: 2px;
      background: $color-disabled;
    }
  }

  &-bundle {
    margin: 0 0 40px 0;
    line-height: 1;

    @media (min-width: $screen-lg-min) {
      margin: 0;
    }

    &-row {
      display: table;
      table-layout: fixed;

      @media (min-width: $screen-sm-min) {
        min-width: 65%;
      }

      @media (min-width: $screen-lg-min) {
        width: 100%;
      }
    }

    &-col {
      display: table-cell;
    }

    &-text {
      margin: 0 0 10px 0;

      font-size: 12px;
      letter-spacing: 1.5px;
      text-transform: uppercase;

      &:lang(ar) {
        font-size: 14px;
        letter-spacing: 0;
      }

      @media (min-width: $screen-sm-min) {
        display: inline-block;
        margin: 0 25px 0 0;
        vertical-align: middle;

        &:lang(ar) {
          margin: 0 0 0 25px;
        }
      }
    }

    &-logo {
      transition: opacity 0.2s;

      @media (min-width: $screen-lg-min) {
        display: inline-block;
        vertical-align: middle;
      }

      img {
        height: auto;
        max-width: 100%;
      }

      @at-root a#{&} {
        &:hover,
        &:focus {
          opacity: 0.8;
        }
      }
    }
  }

  &-social {
    margin: 0 -15px;

    @media (min-width: $screen-lg-min) {
      text-align: right;
    }

    &-item {
      color: $color-text;
      margin: 0 15px;
      font-size: 17px;
      line-height: 1;

      transition: color 0.2s;

      &:hover,
      &:focus {
        text-decoration: none;
      }
    }
  }

  &-bundles {
    line-height: 1;

    @media (min-width: $screen-md-min) {
      margin: 60px 0 0 0;
    }
  }

  /* Colors */
  &.-primary {
    #{$root} {
      &-line {
        background: $color-primary;
      }

      &-social {
        &-item {
          &:hover,
          &:focus {
            color: $color-primary;
          }
        }
      }
    }
    .rd-btn_square {
      background: $color-primary;
    }
  }

  &.-secondary {
    #{$root} {
      &-line {
        background: $color-secondary;
      }
      &-social {
        &-item {
          &:hover,
          &:focus {
            color: $color-secondary;
          }
        }
      }
    }
    .rd-btn_square {
      background: $color-secondary;
    }
  }

  &.-tertiary {
    #{$root} {
      &-line {
        background: $color-tertiary;
      }
      &-social {
        &-item {
          &:hover,
          &:focus {
            color: $color-tertiary;
          }
        }
      }
    }
    .rd-btn_square {
      background: $color-tertiary;
    }
  }

  &.-quaternary {
    #{$root} {
      &-line {
        background: $color-quaternary;
      }
      &-social {
        &-item {
          &:hover,
          &:focus {
            color: $color-quaternary;
          }
        }
      }
    }
    .rd-btn_square {
      background: $color-quaternary;
    }
  }

  &.-quinary {
    #{$root} {
      &-line {
        background: $color-quinary;
      }
      &-social {
        &-item {
          &:hover,
          &:focus {
            color: $color-quinary;
          }
        }
      }
    }
    .rd-btn_square {
      background: $color-quinary;
    }
  }

  &.-senary {
    #{$root} {
      &-line {
        background: $color-senary;
      }
      &-social {
        &-item {
          &:hover,
          &:focus {
            color: $color-senary;
          }
        }
      }
    }
    .rd-btn_square {
      background: $color-senary;
    }
  }
}
