.rd-close {
    position:absolute;
    z-index:1100;

    top: 22px;
    right: 33px;
    padding:12px;
    font-size:18px;
    line-height:1;

    @media (min-width:$screen-sm-min) {
        font-size:26px;
    }

    &-link {
        display:inline-block;
        color:$color-text;

        transition:transform 0.3s, opacity 0.2s;

        &:hover, &:focus {
            color:inherit;
            text-decoration:none;

            opacity:0.8;
        }

        &:active {
            transform:scale(0.5);
        }
    }

    &.-xs {
        top:27px;
        right:37px;
        padding:0;
        font-size:18px;
    }

    &.-inverse {
        a {
            color:$color-base;
        }
    }
}
