.rd-profilebox {

    position:relative;
    height:100%;
    width:100%;

    &:lang(ar) {
        direction:rtl;
    }

    &-header {
        width:100%;
        display:block;

        @media (min-width:$screen-md-min) {
            display:table;
        }

        &-inner {
            display:table;

            @media (min-width:($screen-sm-min + 1px)) {
                margin:0 0 0 -50px;
            }

            &:lang(ar) {
                @media (min-width:($screen-sm-min + 1px)) {
                    margin:0 -50px 0 0;
                }
            }
        }

        &-col {
            display:table-cell;
            vertical-align:top;
        }

        &-title {

            font-size:60px;
            font-weight:900;
            line-height:67px;
            letter-spacing:initial;

            text-transform:capitalize;

            margin-bottom:20px;

            max-width:486px;

            @media (max-width:$screen-lg-min) {
                font-size:40px;
                line-height:49px;
                max-width:324px;
            }

            @media (max-width:$screen-sm-min) {
                font-size:26px;
                line-height:30px;

                max-width:219px;
            }

            &:lang(ar) {
                font-weight:700;
                max-width:600px;
            }
        }

        &-left {
            display:block;
            vertical-align:top;

            @media (min-width:$screen-md-min) {
                display:table-cell;
            }
        }

        &-right {
            display:block;
            vertical-align:top;
            text-align:right;

            @media (min-width:$screen-md-min) {
                display:table-cell;
            }

            &:lang(ar) {
                text-align:left;
            }
        }
    }

    &-nav {
        display:inline-block;
        text-align:left;
        background:$color-primary;
        margin:50px 0 0 0;
        width:100%;

        @media (min-width:$screen-sm-min + 1px) {
            width:416px;
            height:185px;
            margin:50px 0 0 40px;
        }
        @media (min-width:$screen-lg-min) {
            width:536px;
            height:205px;
        }
        @media (min-width:$screen-xl-min) {
            width:536px;
            height:235px;
        }

        &:lang(ar) {
            text-align:right;

            @media (min-width:$screen-sm-min + 1px) {
                margin:50px 40px 0 0;
            }
        }

        &-inner {
            display:table;
            width:100%;
            height:100%;
            padding:35px 25px;

            @media (min-width:$screen-sm-min + 1px) {
                padding:28px 45px;
            }
            @media (min-width:$screen-lg-min) {
                padding:35px 65px;
            }

            &:lang(ar) {

                @media (min-width:$screen-sm-min + 1px) {
                    padding:20px 45px;
                }
                @media (min-width:$screen-lg-min) {
                    padding:35px 65px;
                }
            }
        }

        &-col {
            display:table-cell;
            width:50%;

            @media (max-width:$screen-sm-min) {
                width:100%;
                display:block;
            }
        }

        &-item {

            margin-bottom:25px;

            @media (min-width:$screen-sm-min + 1px) {
                margin-bottom:31px;
            }
            @media (min-width:$screen-xl-min) {
                margin-bottom:48px;
            }

            &:lang(ar) {
                margin-bottom:16px;

                @media (min-width:$screen-sm-min + 1px) {
                    margin-bottom:20px;
                }
                @media (min-width:$screen-xl-min) {
                    margin-bottom:32px;
                }
            }

            &.-indent {
                @media (min-width:$screen-sm-min + 1px) {
                    margin-bottom:0;
                }
            }

            &.-last {
                @media (max-width:$screen-sm-min) {
                    margin-bottom:0;
                }
            }

            &:hover, &:focus, &:active {
                opacity:.8;
            }
        }

        &-icon {
            color:$color-base;
            display:inline-block;
            font-size:18px;
        }

        &-link {
            color:$color-base;
            display:inline-block;
            vertical-align:middle;

            font-size:13px;
            letter-spacing:1.5px;
            font-weight:700;
            text-transform:uppercase;
            margin-left:25px;

            transition:.2s;

            svg {
                color:$color-base;
            }

            &:hover, &:focus, &:active {
                color:$color-base;
                text-decoration:none;
            }

            &:lang(ar) {
                letter-spacing:0;
                margin-right:22px;
            }
        }
    }

    &-breadcrumb {

        margin-bottom:20px;

        &:lang(ar) {
            direction:ltr;
            text-align:right;
        }

        &-link {
            font-size:13px;
            letter-spacing:1.5px;
            font-weight:600;
            text-transform:uppercase;
            text-decoration:none;

            color:$color-text;

            display:inline-block;

            margin-right:20px;
            transition:.2s;

            &:lang(ar) {
                letter-spacing:initial;
                margin-right:initial;
                margin-left:20px;
            }

            span {
                vertical-align:text-bottom;

                &:lang(ar) {
                    transform:rotate(180deg);
                }
            }

            svg {
                position:relative;
                top:-2px;
                left:0;
                transition:left .2s;
            }

            &:hover, &:focus, &:active {
                color:$color-text;
                text-decoration:none;
                opacity:.8;

                svg {
                    left:5px;
                }
            }
        }

        &-icon {
            display:inline-block;
            margin-left:11px;
        }
    }

    &-menu {

        margin-top:-37px;

        @media (max-width:$screen-md-min) {
            position:relative;
            margin:50px 0;
            display:block;

            &:after {
                position:absolute;
                content:'';
                left:0;
                height:1px;
                width:558px;
                background:lighten($color-text, 85%);
            }
        }

        @media (max-width:$screen-sm-min) {
            margin:0 0 30px;

            &:after {
                width:100%;
                max-width:400px;
            }
        }

        &-inner {
            display:table-cell;
            vertical-align:middle;
        }

        &-link {
            padding:0 29px;
            display:block;

            height:60px;
            line-height:60px;

            font-size:13px;
            text-align:left;
            font-weight:600;
            letter-spacing:1.5px;
            text-decoration:none;
            color:lighten($color-text, 70%);

            &:hover, &:focus, &:active {
                text-decoration:none;
                color:lighten($color-text, 70%);

                opacity:.8;
            }

            &.-active {
                background:$color-primary;
                color:$color-base;
            }

            @media (max-width:$screen-md-min) {
                display:inline-block;
                padding:0;
                margin:0 30px 0 0;

                font-size:18px;
                letter-spacing:0;
                text-align:left;

                position:relative;

                &.-active {
                    background:none;
                    color:$color-text;

                    &:after {
                        content:'';
                        position:absolute;
                        left:0;
                        bottom:0;
                        width:60px;
                        height:4px;
                        background:$color-primary;
                    }
                }
            }

            &:lang(ar) {
                letter-spacing:0;
                text-align:right;

                @media (max-width:$screen-md-min) {
                    margin:0 0 0 30px;

                    &.-active {
                        &:after {
                            left:initial;
                            right:0;
                        }
                    }
                }
            }
        }

        span {

            @media (max-width:$screen-sm-min) {
                display:none;
            }

        }
    }

    &-fill {
        position:fixed;
        left:0;
        bottom:0;

        width:385px;
        height:100%;
        background:$color-fill;

        &:lang(ar) {
            right:0;
            left:initial;
        }

        &.-sm {
            width:190px;

            @media (max-width:$screen-lg-min) {
                width:120px;
            }

            @media (max-width:$screen-md-min) {
                width:90px;
            }
        }

        @media (max-width:$screen-md-min) {
            width:90px;
        }

        @media (max-width:$screen-sm-min) {
            display:none;
        }
    }

    &-title {
        margin:0 0 65px 40px;
        display:inline-block;
        position:relative;

        color:$color-text;

        @media (max-width:$screen-md-min) {
            margin:0 0 0 47px;
        }

        @media (max-width:$screen-sm-min) {
            margin:28px 0 0 20px;
        }

        &:lang(ar) {
            margin:0 40px 65px 0;

            @media (max-width:$screen-md-min) {
                margin:0 47px 0 0;
            }

            @media (max-width:$screen-sm-min) {
                margin:28px 20px 0 0;
            }
        }

        span {
            vertical-align:middle;
        }

        &:hover, &:focus, &:active {
            color:$color-text;
            opacity:.8;

            text-decoration:none;

            svg {
                left:-10px;

                @media (max-width:$screen-sm-min) {
                    left:-5px;
                }
            }
        }

        &-icon {
            font-size:28px;
            position:relative;
            color:$color-text;

            display:inline-block;
            position:relative;
            z-index:1;

            transition:.2s;

            @media (max-width:$screen-sm-min) {
                font-size:18px;
            }

            svg {
                position:relative;
                transition:left .2s;
                left:0;
                vertical-align:middle;
            }

            &:lang(ar) {
                transform:rotate(180deg);
            }
        }

        &-text {
            display:inline-block;
            margin:0 0 0 35px;

            font-size:60px;
            line-height:67px;
            font-weight:900;
            text-transform:capitalize;

            @media (max-width:$screen-md-min) {
                font-size:40px;
            }

            @media (max-width:$screen-sm-min) {
                font-size:26px;
                margin:0 0 0 23px;
            }

            &:lang(ar) {
                margin:0 35px 0 0;

                @media (max-width:$screen-sm-min) {
                    margin:0 23px 0 0;
                }
            }
        }
    }

    &-content {
        position:relative;
        @include clearfix;

        &.-primary {
            @media (min-width:$screen-md-min) {
                margin-top:32px;
            }
        }

        &.-index {
            margin:0 0 60px 20px;

            @media (min-width:$screen-sm-min) {
                margin:0 0 60px 90px;
            }
            @media (min-width:$screen-md-min) {
                margin:0 63px 60px 109px;
            }
            @media (min-width:$screen-lg-min) {
                margin:0 105px 60px 139px;
            }

            &:lang(ar) {
                margin:0 20px 60px 0;

                @media (min-width:$screen-sm-min) {
                    margin:0 90px 60px 0;
                }
                @media (min-width:$screen-md-min) {
                    margin:0 109px 60px 63px;
                }
                @media (min-width:$screen-lg-min) {
                    margin:0 139px 60px 105px;
                }
            }

        }

        .rd-form-submit {
            text-align:right;

            &:lang(ar) {
                direction:ltr;
            }

            @media (max-width:$screen-sm-min) {
                text-align:center;
            }
        }

        &-nav {
            overflow-x:auto;
            white-space:nowrap;

            margin:40px 0;

            @media (min-width:$screen-sm-min) {
                margin:60px 0;
            }
            @media (min-width:$screen-lg-min) {
                margin:50px 0;
            }
            @media (min-width:$screen-xl-min) {
                margin:72px 0;
            }

            &-link {
                display:inline-block;
                position:relative;
                background:$color-fill;
                vertical-align:middle;

                font-size:13px;
                letter-spacing:1.5px;
                font-weight:700;
                color:lighten($color-text, 65%);
                text-decoration:none;

                padding:22px 32px;

                transition:.3s;

                &:before {
                    position:absolute;
                    content:'';
                    left:0;
                    top:0;
                    background:$color-primary;
                    width:0;
                    height:100%;
                    z-index:1;

                    transition:width 0.4s;
                }

                &:hover, &:focus {
                    color:$color-text;
                    text-decoration:none;
                }

                &:lang(ar) {
                    letter-spacing:0;
                }

                &.-active {
                    color:$color-base;
                    position:relative;
                    text-decoration:none;
                    transition:.2s;

                    &:before {
                        width:100%;
                    }

                    &:lang(ar) {

                        &:before {
                            right:0;
                            left:initial;
                        }
                    }
                }

                span {
                    position:relative;
                    z-index:2;
                }
            }
        }

        &-inner {
            padding:0 20px 0 0;
            overflow-x:auto;
            overflow-y:hidden;

            &:lang(ar) {
                padding:0 0 0 20px;
            }

            @media (min-width:$screen-md-min) {
                overflow-x:initial;
            }

            &-item {
                display:none;

                &-wrap {
                    padding-top:9px;
                }

                &-icon {
                    font-size:42px;

                    margin-bottom:25px;
                }

                &-title {
                    font-size:16px;
                    //catch from prod <-- start
                    letter-spacing: 0;
                    //catch from prod <-- end
                    line-height:19px;

                    font-weight:700;

                    margin-bottom:15px;
                }

                &-subtitle {
                    font-size:16px;
                    line-height:27px;

                    span {
                        display:block;
                    }
                }

                &-comments {
                    width:100%;
                    @media (min-width:$screen-sm-min) {
                        max-width:590px;
                    }

                    &:lang(ar) {
                        max-width:640px;
                    }
                }

                &-table {
                    min-width:690px;
                    position:relative;

                    @media (min-width:$screen-sm-min) {
                        width:100%;
                    }

                    &:lang(ar) {
                        direction:rtl;
                        min-width:565px;
                    }

                    tr {
                        margin-bottom:60px;

                        font-size:14px;
                        letter-spacing:0;
                        text-align:left;

                        &:lang(ar) {
                            font-size:15px;
                            text-align:right;
                        }

                        color:lighten($color-text, 50%);
                    }

                    td {
                        display:inline-block;
                        margin:0 0 50px 0;
                        line-height:27px;

                        width:18%;

                        &:nth-child(1) {
                            width:40%;
                            min-width:40%;

                            padding:0 7% 0 0;
                        }

                        &:nth-child(2) {
                            width:10%;
                        }

                        &:nth-child(3) {
                            width:25%;
                            padding:0 0 0 3%;

                            @media (min-width:$screen-md-min) {
                                width:20%;

                            }
                        }

                        &:nth-child(4) {
                            text-align:right;
                            width:15%;

                            @media (min-width:$screen-md-min) {
                                width:20%;
                            }

                            @media (max-width:$screen-md-min) {
                                width:10%;
                            }
                        }

                        &:nth-child(5) {
                            width:10%;
                            padding: 0 0 0 2%;
                        }

                        &:lang(ar) {
                            &:nth-child(1) {
                                width:35%;
                                min-width:initial;
                                padding:0 0 0 7%;
                            }

                            &:nth-child(2) {
                                width: 10%;
                                padding:0 1% 0 0;
                            }

                            &:nth-child(3) {
                                width:25%;
                                padding:0 3% 0 0;

                                @media (min-width:$screen-md-min) {
                                    padding:0;
                                }

                                @media (max-width:$screen-md-min) {
                                    width:28%;
                                }
                            }

                            &:nth-child(4) {
                                text-align:left;
                                width:20%;

                                @media (max-width:$screen-md-min) {
                                    width:10%;
                                }
                            }

                            &:nth-child(5) {
                                width:10%;
                                padding: 0 2% 0 0;
                            }
                        }
                    }

                    &-title {
                        font-size:16px;
                        letter-spacing:0;
                        line-height:80px;

                        font-weight:700;

                        color:$color-text;

                        &:lang(ar) {
                            font-size:15px;
                        }
                    }

                    &-status {
                        position:relative;
                        font-size:13px;
                        letter-spacing:1.5px;
                        line-height:80px;

                        font-weight:700;

                        color:$color-text;

                        span {
                            vertical-align:middle;
                            display:inline-block;
                            @media (max-width:$screen-md-min) {
                                display:none;
                            }

                            &:lang(ar) {
                                font-size:15px;
                                letter-spacing:0;
                            }
                        }

                        &:lang(ar) {
                            text-align:left;
                            direction:initial;
                        }
                    }

                    &-icon {
                        display:inline-block;
                        margin-left:17px;

                        font-size:18px;

                        &.-del {
                            color:$color-error;
                        }
                    }
                }
            }

            #my-submit {
                display:block;
            }
        }
    }

    &-twitter {
        font-size:16px;
        letter-spacing:.57px;
        line-height:29px;
        color:$color-text;

        transition:.2s;

        &:lang(ar) {
            text-align:right;
            display:block;
            direction:ltr;
        }

        &:hover, &:focus, &:active {
            text-decoration:none;
            opacity:.8;
            color:$color-text;
        }
    }

    &-avatar {

        display:inline-block;
        margin:0 60px 0 0;
        text-align:left;

        @media (max-width:$screen-sm-min) {
            position:initial;
            margin:0 0 40px 0;
            text-align:center;
        }

        &:lang(ar) {
            margin:0 0 0 60px;
            text-align:center;

            @media (max-width:$screen-sm-min) {
                margin:0 0 40px 0;
            }
        }

        &.-index {
            margin:0 90px 0 0;

            @media (max-width:$screen-lg-min) {
                margin:0 60px 0 0;
            }
            @media (max-width:$screen-md-min) {
                margin:0 40px 0 0;
            }
            @media (max-width:$screen-sm-min) {
                margin:0 30px 0 0;
            }

            &:lang(ar) {
                margin:0 0 0 90px;

                @media (max-width:$screen-lg-min) {
                    margin:0 0 0 60px;
                }
                @media (max-width:$screen-md-min) {
                    margin:0 0 0 40px;
                }
                @media (max-width:$screen-sm-min) {
                    margin:0 0 0 30px;
                }
            }
        }

        &-wrap {
            display:table;
            width:60px;
            height:60px;
            margin:0 auto 28px;

            background:$color-base;
            box-shadow:0 0 20px rgba(0, 0, 0, .05);

            @media (min-width:$screen-sm-min) {
                width:100px;
                height:100px;
            }
        }

        &-inner {
            display:table-cell;
            vertical-align:middle;
            text-align:center;
            width:100%;
            height:100%;
        }

        &-icon {
            font-size:26px;
            color:lighten($color-text, 80%);
        }

        &-tumb {
            background-repeat:no-repeat;
            background-position:center center;
            background-size:cover;
            width:60px;
            height:60px;
            margin:0 auto 28px;

            @media (min-width:$screen-sm-min) {
                width:100px;
                height:100px;
            }

            &.-lg {
                width:80px;
                height:80px;

                @media (min-width:$screen-sm-min) {
                    width:100px;
                    height:100px;
                }
            }

        }

        &-link {
            position:relative;
            font-size:13px;
            letter-spacing:1.5px;
            font-weight:600;
            text-transform:uppercase;
            text-decoration:none;

            color:$color-text;

            transition:.2s;

            cursor:pointer;

            &:hover, &:focus, &:active {
                color:$color-text;
                text-decoration:none;
                opacity:.8;
            }

            &:lang(ar) {
                letter-spacing:0;
            }

            input {
                cursor:pointer;
                filter:alpha(opacity=1);
                -moz-opacity:0.01;
                opacity:0.01;
                position:absolute;
                right:0;
                top:0;
                width:100%;
                font-size:32px;
            }
        }
    }

    &-hamburger {
        display:none;
        position:relative;
        left:-163px;
        margin:40px 0 57px 0;

        text-decoration:none;

        @media (max-width:$screen-sm-min) {
            left:20px;
            margin:24px 0 42px 0;
        }

        @media (max-width:$screen-md-min) {
            display:block;
        }

        &:hover {
            text-decoration:none;
            opacity:.8;
        }

        &-text {
            font-size:15px;
            letter-spacing:.66px;
            font-weight:600;
            line-height:18px;

            color:$color-text;

            display:inline-block;
        }

        &-icon {
            color:$color-primary;
            font-size:16px;

            display:inline-block;
            margin-right:12px;
        }
    }

    &-wrap {
        width:100%;
        height:100%;
        display:block;

        padding-top:40px;

        @media (min-width:$screen-md-min) {
            padding-top:110px;
        }

        &.-big {
            padding-top:82px;

            @media (min-width:$screen-sm-min) {
                padding-top:112px;
            }
            @media (min-width:$screen-lg-min) {
                padding-top:182px;
            }
        }
    }

    &-inner {
        width:100%;
        height:100%;
        display:block;
        vertical-align:middle;
    }

    &-row {
        display:table;
        width:100%;
        height:100%;
        position:relative;

        @media (max-width:$screen-md-min) {
            display:block;
        }
    }

    &-col {
        display:table-cell;
        vertical-align:middle;

        &.-offseted {
            width:190px;

            @media (max-width:$screen-lg-min) {
                width:120px;
            }

            @media (max-width:$screen-md-min) {
                width:90px;
            }
        }

        &.-left {
            width:385px;

            @media (max-width:$screen-sm-min) {
                width:initial;
                max-width:400px;
            }
        }

        @media (max-width:$screen-md-min) {
            display:block;
        }
    }

    &-container {

        &.-index {
            @media (max-width:$screen-md-min) {
                margin:0 0 0 90px;
            }
            @media (max-width:$screen-sm-min) {
                margin:0;
            }

            &:lang(ar) {

                @media (max-width:$screen-md-min) {
                    margin:0 90px 0 0;
                }
                @media (max-width:$screen-sm-min) {
                    margin:0;
                }
            }
        }

        @media (max-width:$screen-md-min) {
            margin:0 0 0 40px;
        }

        @media (max-width:$screen-sm-min) {
            margin:0 20px;
        }

        &:lang(ar) {

            @media (max-width:$screen-md-min) {
                margin:0 40px 0 0;
            }

            @media (max-width:$screen-sm-min) {
                margin:0 20px;
            }
        }
    }

    &-form {

        display:table;

        &:lang(ar) {
            text-align:right;
        }

        &.-left {
            margin:0 0 0 -50px;

            @media (max-width:$screen-md-min) {
                margin:0;
            }

            &:lang(ar) {
                margin:0 -50px 0 0;

                @media (max-width:$screen-md-min) {
                    margin:0;
                }
            }
        }

        &.-right {

            margin:0 0 0 110px;

            @media (max-width:$screen-sm-min) {
                margin:0;
            }

            &:lang(ar) {
                margin:0 110px 0 0;

                @media (max-width:$screen-sm-min) {
                    margin:0;
                }
            }
        }

        @media (max-width:$screen-sm-min) {
            display:block;
            max-width:400px;
        }

        label {

            .rd-checkbox-text {
                color:lighten($color-text, 70%);
            }

            .rd-checkbox-box {
                border:1px solid lighten($color-text, 70%);
                color:$color-primary;
            }

            input {

                &:checked {

                    ~ .rd-checkbox-text {
                        color:$color-text;
                    }

                    + .rd-checkbox-box {
                        border:1.2px solid $color-primary;
                    }
                }
            }
        }

        &-col {
            display:table-cell;
            vertical-align:top;

            &.-right {
                width:400px;

                @media (max-width:$screen-sm-min) {
                    width:100%;
                }
            }

            &.-center {
                text-align:center;
            }

            @media (max-width:$screen-sm-min) {
                display:block;
            }
        }

        &-text {
            font-size:13px;
            letter-spacing:1.5px;
            font-weight:600;
            text-transform:uppercase;
            text-decoration:none;

            color:$color-text;

            transition:.2s;

            &:hover, &:focus, &:active {
                color:$color-text;
                text-decoration:none;
                opacity:.8;
            }

            &:lang(ar) {
                letter-spacing:0;
                line-height:24px;
                font-weight:700;
            }
        }
    }
    .rd-entry-item-inner {
        background:$color-fill;
    }
}
