.rd-toolbar {
    direction:initial;
    position:relative;
    padding:23px 0;
    height:66px;
    width:100%;
    z-index:4;

    @media (min-width:$screen-md-min) {
        padding:38px 0;
        position:absolute;
        left:0;
        right:0;
        height:95px;
    }

    &-outer {
        height:calc(100% - 66px);

        @media (min-width:$screen-md-min) {
            height:100%;
            padding-top:95px;
        }
    }

    &-item {
        cursor:pointer;
        display:inline-block;
        margin:0 18px;

        color:inherit;
        font-size:19px;
        line-height:1;

        transition:all 0.2s;

        &:first-child {
            margin-left:0;
        }

        &:last-child {
            margin-right:0;
        }

        &:lang(ar) {
             direction:rtl;
        }

        @at-root a#{&}, & a[data-toggle] {
            color:inherit;
            opacity:0.5;

            &:hover {
                color:inherit;
                opacity:0.8;
            }
        }

        > span {
            display:inline-block;
            opacity:0.5;
        }

        &.-right {
             float:right;
        }

        &.-active {
            color:inherit;
            opacity:1 !important;
        }
    }

    &-social {
        overflow:hidden;
        margin:0;

        &-list {
            direction:initial;
            display: flex;

            &:lang(ar) {
                 text-align:right;
            }
        }

        &-item {
            display:inline-block;
            margin-right:27px;
            vertical-align:middle;

            &:last-child {
                margin-right:0;
            }

            &:lang(ar) {

                &:last-child {
                    margin-right:0;
                }
            }
        }

        &-link {
            position:relative;

            font-size:17px;
            line-height:1;
            color:$color-text !important;

            perspective:1000px;

            &.-google-plus {
                font-size:22px;
            }

            &-whatsApp{

               background-color: red;
               display: none;
                

            }

            > span {
                display:inline-block;
                position:relative;
                transform-origin:50% 0;
                transform-style:preserve-3d;
                transition:all 0.25s ease-in-out;

                > span {
                    position:absolute;
                    top:110%;
                    left:0;
                    width:100%;
                    height:100%;

                    opacity:0;
                    transform-origin:50% 0;
                    transform:rotateX(-90deg);
                    transition:all 0.25s ease-in-out;
                }
            }

            &:hover > span {
                transform:rotateX(90deg);
                color:$color-text;

                > span {
                    opacity:1;
                    transition-delay:0.05s;
                    transform:rotateX(-90deg);
                    color:$color-text;
                }
            }

            &:active, &:focus {
                color:$color-text;
            }
        }

    }
}

.rd-toolbar-social-item {
    &:nth-child(3) {
        display: none;
    }
}


.rd-toolbar-social-item {

    @media (min-width:$screen-md-min) {
    &:nth-child(6) {
        display: none;
    }
}
}
