.rd-carousel {
    position:relative;
    width:100%;
    height:100%;

    /*rtl:ignore*/
    direction:ltr;

    &-control {
        display:none;

        @media (min-width:$screen-lg-min) {
            display:block;
        }

        &-item {
            display:block;
            position:absolute;
            top:50%;
            height:127px;
            width:53px;
            margin:-(127px / 2) 0 0 0;
            z-index:2;

            background:$color-base;
            color:$color-text;

            font-size:13px;
            line-height:127px;
            text-align:center;

            transition:all 0.2s;

            &:hover, &:focus {
                color:$color-text;
                transform:translateX(-4px);
            }

            &:active {
                transform:translateX(-8px);
            }

            &.-left {
                left:0;
            }

            &.-right {
                right:0;

                &:hover, &:focus {
                    transform:translateX(4px);
                }

                &:active {
                    transform:translateX(8px);
                }
            }
        }
    }

    &-items {
        width:100%;
        height:100%;
        overflow:hidden;
    }

    /* Owl Carousel Approach */
    .owl {
        &-carousel {
            position:relative;
            height:100%;

            .owl-stage-outer, .owl-stage, .owl-item {
                height:100%;
            }
        }

        &-item {
            width: 0;
            opacity:0.9;
            overflow: hidden;
            transition:all 0.3s;

            &[style] {
                overflow: unset;
            }

            &.active {
                transition:opacity 0.3s 0.3s;
                opacity:1;
            }
        }

        &-dots {
            display:block;
            position:absolute;
            left:0;
            right:0;
            bottom:15px;

            white-space:nowrap;
            text-align:center;

            @media (min-width:$screen-sm-min) {
                bottom:25px;
            }

            @media (min-width:$screen-lg-min) {
                display:none;
            }
        }

        &-dot {
            display:inline-block;
            height:6px;
            width:6px;
            margin:0 4px;

            opacity:0.35;
            border-radius:50%;
            background:$color-base;

            @media (min-width:$screen-sm-min) {
                margin:0 7px;
            }

            &.active {
                opacity:1;
            }
        }
    }

    &-item {
        width:100%;
        height:100%;

        &-overlay {
            position: absolute;
            display: table;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            height: 100%;
            width: 100%;

            cursor:pointer;

            &-icon {
                display: table-cell;
                vertical-align: middle;
                text-align: center;
                color: $color-base;
                font-size: 45px;
            }
        }
    }

    &-img {
        cursor:pointer;
        height:100%;
        width:100%;

        background-size:cover;

        &.-containTimeline{
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            background-color: #7F7F7F;
        }

        &.-contain {
            background-size:contain;
            background-position:center;
            background-repeat:no-repeat;
          //catch from prod <-- start
          &Timeline {
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            background-color: #7F7F7F;
          }
          //catch from prod <-- end
        }

        &-caption {
            position:absolute;
            left:0;
            right:0;
            bottom:0;
            padding:40px 0;

            color:$color-base;
            background:linear-gradient(to top, rgba($color-text, .5), rgba(0, 0, 0, 0));
            text-shadow:1px 1px 1px $color-text;

            font-size:16px;
            text-align:center;

            @media (min-width:$screen-sm-min) {
                padding:60px 0;
            }

            @media (min-width:$screen-md-min) {
                padding:30px 0;
            }
        }
    }
}
