.rd-splitbox {
    $root:&;
    overflow-x:hidden;

    @media (min-width:$screen-md-min) {
        height:100%;
    }

    &-inner {
        height:100%;
    }

    &-overlay {
        @media (min-width:$screen-md-min) {
            position:absolute;
            top:0;
            bottom:0;
            left:0;
            right:0;
            height:100%;
            z-index:1;

            box-shadow:
                    inset 0 100px 100px -70px $color-text,
                    inset 0 -100px 100px -70px $color-text;

            pointer-events:none;
        }

        @media (min-width:$screen-lg-min) {
            box-shadow:
                    inset 0 180px 200px -130px $color-text,
                    inset 0 -180px 200px -130px $color-text;
        }
    }

    &-left {
        position:relative;
        height:205px;
        background-color: white;
        transition:width 0.6s;

        @media (min-width:$screen-xs-min) {
            height:285px;
        }

        @media (min-width:$screen-sm-min) {
            height:430px;
        }

        @media (min-width:$screen-md-min) {
            float:left;
            width:50%;
            height:100%;
            overflow:hidden;
        }

        &.-lg {
            @media (min-width:$screen-xl-min) {
                width:(100% / 6) * 4;
            }
        }

        &.-full {
            height:100%;
        }

        &.-push {
            @media (min-width:$screen-md-min) {
                float:right;
            }
        }
    }

    &-right {
        position:relative;

        transition:width 0.6s;

        @media (min-width:$screen-md-min) {
            float:left;
            width:50%;
            height:100%;
        }

        &.-sm {
            @media (min-width:$screen-xl-min) {
                width:(100% / 6) * 2;
            }
        }

        &.-modal {
            display:none;

            @media (max-width:$screen-sm-max) {
                position:fixed;
                top:0;
                left:0;
                right:0;
                bottom:0;
                z-index:110;

                background:$color-graybase;
            }

            @media (min-width:$screen-md-min) {
                display:block;
            }

            #{$root} {
                &-close {
                    @media (max-width:$screen-sm-max) {
                        transform:scale(1);
                    }
                }
            }

            &.-visible {
                display:block;
            }
        }

        &.-overlay {
            @media (min-width:$screen-md-min) {
                box-shadow:0 2px 14px rgba($color-text, 0.5);
            }
        }
    }

    &-container {
        padding:0 20px;

        @media (min-width:$screen-sm-min) {
            padding:0 105px 0 40px;
        }

        @media (min-width:$screen-md-min) {
            padding:0 40px;
        }

        @media (min-width:$screen-xl-min) {
            padding:0 60px 0 80px;
        }

        &.-xs {
            padding:0 20px;

            @media (min-width:$screen-sm-min) {
                padding:0 108px 0 40px;
            }

            @media (min-width:$screen-md-min) {
                padding:0 55px 0 60px;
            }

            &:lang(ar) {

                @media (min-width:$screen-sm-min) {
                    padding:0 40px 0 108px;
                }

                @media (min-width:$screen-md-min) {
                    padding:0 60px 0 55px;
                }
            }
        }

        &.-sm {
            padding:0 31px 0 20px;

            @media (min-width:$screen-sm-min) {
                padding:0 146px 0 60px;
            }

            @media (min-width:$screen-md-min) {
                padding:0 33px 0 40px;
            }

            @media (min-width:$screen-lg-min) {
                padding:0 33px 0 60px;
            }

            &:lang(ar) {
                padding:0 20px 0 31px;

                @media (min-width:$screen-sm-min) {
                    padding:0 60px 0 146px;
                }

                @media (min-width:$screen-md-min) {
                    padding:0 40px 0 33px;
                }
            }
        }

        &.-lg {

            @media (min-width:$screen-md-min) {
                padding:0 220px 0 245px;
            }
            @media (min-width:$screen-xl-min) {
                padding:0 480px 0 491px;
            }

            &:lang(ar) {

                @media (min-width:$screen-md-min) {
                    padding:0 245px 0 220px;
                }
                @media (min-width:$screen-xl-min) {
                    padding:0 491px 0 480px;
                }
            }
        }

        &.-xl {

            @media (min-width:$screen-md-min) {
                padding:0 225px 0 295px;
            }
            @media (min-width:$screen-xl-min) {
                padding:0 442px 0 467px;
            }

            &:lang(ar) {
                max-width:647px;
                margin:0 auto;

                @media (min-width:$screen-sm-min) {
                    padding:0;
                }
            }
        }
    }

    &-content {
        position:relative;
        height:100%;
        overflow:auto;
        overflow-x:hidden;
        -webkit-overflow-scrolling:touch;
        z-index:3;

        &::-webkit-scrollbar {
            width:0px;
            background:transparent;
        }
        &.-noscroll {
            @media (max-width:$screen-xs-max) {
                overflow:visible;
            }
        }
    }

    &-replaceable {
        display:none;
        height:100%;

        &.-visible {
            display:block;
        }
    }

    &-fill {
        display:none;
        position:absolute;
        top:0;
        right:0;
        bottom:0;
        width:80px;
        z-index:2;

        background:$color-fill;

        transition:all 0.6s cubic-bezier(.65, .37, .4, .92);

        @media (min-width:$screen-sm-min) {
            width:251px;
        }

        @media (min-width:$screen-md-min) {
            width:171px;
        }

        @media (min-width:$screen-xl-min) {
            width:251px;
        }

        &.-sm {
            width:75px;

            @media (min-width:$screen-lg-min) {
                width:95px;
            }
        }

        &.-lg {
            width:75px;

            @media (min-width:$screen-lg-min) {
                width:190px;
            }
        }

        &.-inverse {
            opacity:0.04;
        }

        &.-left {
            right:100%;
            margin-right:-80px;

            @media (min-width:$screen-sm-min) {
                margin-right:-251px;
            }

            @media (min-width:$screen-md-min) {
                margin-right:-171px;
            }

            @media (min-width:$screen-xl-min) {
                margin-right:-251px;
            }

            &.-sm {
                margin-right:-75px;

                @media (min-width:$screen-lg-min) {
                    margin-right:-95px;
                }
            }

            &.-lg {
                margin-right:-75px;

                @media (min-width:$screen-lg-min) {
                    margin-right:-190px;
                }
            }
        }
    }

    &-close {
        position:absolute;
        z-index:999;
        top:12px;
        right:18px;
        padding:12px;
        font-size:18px;
        line-height:1;

        transform:scale(0);
        transition:all 0.4s;

        @media (min-width:$screen-md-min) {
            top:22px;
            right:22px;
            font-size:35px;
        }

        a {
            display:inline-block;
            color:$color-text;

            transition:transform 0.3s, opacity 0.2s;

            &:hover, &:focus {
                color:inherit;
                text-decoration:none;

                opacity:0.8;
            }

            &:active {
                opacity:0.6;
            }
        }

        &.-inverse {
            a {
                color:$color-base;
            }
        }

        &.-visible {
            transform:scale(1);
        }
    }
    &.-noscroll {
        @media (max-width:$screen-xs-max) {
            overflow:visible;
        }
    }

    &.-full {
        height:100%;
    }

    &.-inverse {
        background:$color-graybase;
        color:$color-base;
    }

    &.-base {
        background:$color-base;
        color:$color-text;
    }
}