.rd-index {
	width: 100%;
	height: 100%;

	&-wrap {
		display: table;
		width: 100%;
		height: 100%;
	}

	&-inner {
		display: table-cell;
		vertical-align: middle;
	}

	&-container {
		margin: 0 60px;
	}

	&-title {
		font-size: 48px;
		font-weight: 600;
		margin-bottom: 60px;
	}

	&-item {
		display: block;
		margin-bottom: 20px;

		&-text {
			display: inline-block;
		}

		&-link {
			display: inline-block;
			margin-left: 10px;
		}
	}
}
