.rd-tooltip {
    display:inline;

    &_object {
        $root:&;

        position:absolute;
        display:block;
        padding:10px 25px;
        z-index:10;

        background:$color-base;
        white-space:nowrap;

        transition:transform 0.4s 0.2s;
        transform:scaleX(0);
        transform-origin:left center;

        &:lang(ar) {
            direction:rtl;
            transform-origin:right center;

            #{$root} {
                &-img {
                    margin:0 -50px 0 0;
                }

                &-message {
                    transform:translateX(20px);
                }
            }

            &.-middle {
                left:initial;
                right:50%;
            }
        }

        &-img {
            display:inline-block;
            height:50px;
            width:50px;
            margin:0 0 0 -50px;
            vertical-align:middle;

            background-position:center center;
            background-size:cover;

            transform:scale(0);
            transition:transform 0.4s;
        }

        &-message {
            display:inline-block;
            margin:0 17px;
            vertical-align:middle;
            opacity:0;
            transform:translateX(-20px);
            transition:all 0.4s;
        }

        &-title {
            display:block;
            color:$color-text;
            font-size:16px;
            font-weight:bold;
            letter-spacing:0.34px;
            line-height:23px;
        }

        &-text {
            //display:none;
            color:lighten($color-text, 70%);
            font-size:12px;
            font-weight:normal;
            letter-spacing:0.26px;
        }

        &.-reverse {
            direction:rtl;
            transform-origin:right center;

            #{$root} {
                &-img {
                    margin:0 -50px 0 0;
                }

                &-message {
                    transform:translateX(20px);
                }
            }

            &.-middle {
                left:initial;
                right:50%;
            }

            &:lang(ar) {
                direction:ltr;
                transform-origin:left center;

                #{$root} {
                    &-img {
                        margin:0 0 0 -50px;
                    }

                    &-message {
                        transform:translateX(-20px);
                    }
                }

                &.-middle {
                    left:50%;
                    right:initial;
                }
            }
        }

        &.-middle {
            top:50%;
            left:50%;
        }

        &.-overlap {
            margin:-35px 0 0 0;
        }

        &.-visible {
            transition-delay:0s;
            transform:scaleX(1);
        }

        &.-visible &-img {
            transform:scale(1);
        }

        &.-visible &-message {
            transition-delay:0.2s;
            transform:translateX(0px)!important;
            opacity:1;
        }
    }

    &_status {
        @extend .rd-tooltip_object;

        top:-5px;
        right:35px;
        background:$color-text;

        white-space:initial;
        text-align:left;

        padding:15px 0;
        width:231px;

        transition:transform 0.3s 0.2s;
        transform-origin:right center;

        @media (min-width:$screen-md-min) {
            right:140px;
        }

        &:lang(ar) {
            transform-origin:left center;
            text-align:right;
            left:50px;

            @media (min-width:$screen-md-min) {
                left:150px;
            }

            &-message {
                transform:translateX(-20px);
            }
        }

        &-message {
            display:inline-block;
            margin:0 17px;
            vertical-align:middle;
            opacity:0;
            transform:translateX(20px);
            transition:all 0.3s;

            &:lang(ar) {
                transform:translateX(-20px);
            }
        }

        &-description {
            font-size:13px;
            letter-spacing:.22px;
            line-height:17px;
            font-weight:500;

            color:$color-base;
        }

        &.-visible &-message {
            transition-delay:0.2s;
            transform:translateX(0px);
            opacity:1;
        }
    }

}
