.rd-artbox {
    $root:&;

    width:100%;
    background:$color-fill;

    &:lang(ar) {
        direction:rtl;
    }

    &-wrap {
        padding-top:60px;
        display:table;
        width:100%;
        height:100%;

        @media (min-width:$screen-md-min) {
            padding-top:110px;
        }
    }

    &-inner {
        display:table-cell;
        vertical-align:middle;
    }

    &-container {
        margin:0 20px;

        @media (min-width:$screen-sm-min) {
            margin:0 40px;
        }
        @media (min-width:$screen-md-min) {
            margin:0 44px;
        }
    }

    &-header {
        display:none;

        @media (min-width:$screen-md-min) {
            display:block;
        }

        &-wrap {
            display:block;
            width:100%;

            @media (min-width:$screen-sm-min) {
                display:table;
            }
        }
        &-col {
            display:block;

            @media (min-width:$screen-sm-min) {
                display:table-cell;
                vertical-align:middle;
            }

            &.-right {
                @media (min-width:$screen-sm-min) {
                    text-align:right;
                }
            }
        }
    }

    &-title {
        font-size:26px;
        line-height:30px;
        font-weight:900;

        display:inline-block;

        margin-bottom:25px;

        span.-loading{
            position: relative;

            &:after {
                content:"";
                display:inline-block;
                position:absolute;
                top:50%;
                left:7px;
                margin:-13px 0 0 -13px;
                height:26px;
                width:26px;

                border:2px solid;
                border-radius:50%;
                border-bottom-color:transparent;

                animation:rd-loader-rotation 0.7s linear infinite;
            }
        }

        @media (min-width:$screen-sm-min) {
            margin-bottom:0;
        }

        @media (min-width:$screen-md-min) {
            font-size:60px;
            line-height:67px;

            margin-left:15px;
        }
    }

    &-filters {
        margin:15px 0 0 15px;

        &-item {
            display:inline-block;
            vertical-align:middle;
            margin:0 0 0 40px;
            line-height:1;

            &:first-child {
                margin:0;
            }

            &:lang(ar) {
                margin:0 40px 0 0;

                &:first-child {
                    margin:0;
                }
            }
        }

        &-text {
            display:inline-block;
            vertical-align:middle;

            font-size:13px;
            letter-spacing:1px;
            line-height:27px;
            font-weight:700;

            color:$color-gray;

            &:lang(ar) {
                letter-spacing:0.2px;
            }
        }

        &-number {
            display:inline-block;
            vertical-align:middle;

            font-size:13px;
            letter-spacing:1px;
            line-height:27px;
            font-weight:700;

            color:$color-error;
        }

        &-icon {
            display:inline-block;
            vertical-align:middle;
            margin:0 0 0 10px;

            font-size:10px;

            color:lighten($color-text, 40%);

            &:hover {
                opacity:0.8;
                color:lighten($color-text, 40%);
                text-decoration:none;
            }

            &:lang(ar) {
                margin:0 10px 0 0;
            }
        }
    }

    &-nav {
        @media (min-width:$screen-sm-min) {
            margin-top:14px;
        }

        &:lang(ar) {
            text-align:left;
        }

        &-item {
            display:inline-block;
            vertical-align:middle;
            margin-left:47px;

            &:first-child {
                margin-left:0;
            }

            &:last-child {
                display:none;

                @media (min-width:$screen-sm-min) {
                    display:inline-block;
                }
            }

            &:lang(ar) {
                margin-left:initial;
                margin-right:47px;
            }
        }

        &-link {
            display:inline-block;
            font-size:14px;
            letter-spacing:.88px;
            font-weight:600;

            color:$color-text;

            transition:.2s;

            &:hover, &:focus, &:active {
                color:$color-text;
                text-decoration:none;
                opacity:.8;
            }

            &:lang(ar) {
                font-size:13px;
                font-weight:700;
                letter-spacing:0;
            }

            &-icon {
                display:inline-block;
                vertical-align:top;
                margin-left:11px;

                font-size:12px;

                transform:rotate(0deg);
                transition:all 0.2s ease-in-out;

                &:lang(ar) {
                    margin-left:initial;
                    margin-right:11px;
                }
            }
        }
    }

    /* Colors */
    &.-primary {
        #{$root} {
            &-title {
                span {
                    color:$color-primary;
                }
            }
        }
    }

    &.-secondary {
        #{$root} {
            &-title {
                span {
                    color:$color-secondary;
                }
            }
        }
    }

    &.-tertiary {
        #{$root} {
            &-title {
                span {
                    color:$color-tertiary;
                }
            }
        }
    }

    &.-quaternary {
        #{$root} {
            &-title {
                span {
                    color:$color-quaternary;
                }
            }
        }
    }

    &.-quinary {
        #{$root} {
            &-title {
                span {
                    color:$color-quinary;
                }
            }
        }
    }

    &.-senary {
        #{$root} {
            &-title {
                span {
                    color:$color-senary;
                }
            }
        }
    }
}
