.rd-tabbar {
    $root:&;
    display:none;

    @media (min-width:$screen-lg-min) {
        display:block;
        position:fixed;
        left:0;
        right:0;
        bottom:0;
        height:60px;
        z-index:110;
        pointer-events:auto;

        direction:ltr;
    }

    &-outer {
        position:relative;
        height:100%;

        @media (min-width:$screen-lg-min) {
            padding-bottom:60px;
        }

        &.-auto {
            height:auto;
        }

        &.-calc {
            @media (min-width:$screen-lg-min) {
                padding-bottom:0px;
                height:calc(100% - 60px);
            }
        }
    }

    &-inner {
        display:table;
        width:100%;
        height:100%;
        table-layout:fixed;
    }

    &:lang(en) {
        direction: rtl;
    }

    &.-inverse {
        .rd-tabbar-item.-inverse:lang(en) {
            color:$color-base;
            border-color:rgba($color-base, 0.2);
        }
    }

    &-item {
        position:relative;
        display:table-cell;

        cursor:pointer;
        color:$color-text;
        border:1px rgba($color-text, 0.1) solid;
        border-right:0;
        border-bottom:0;

        direction:rtl;
        font-size:13px;
        font-weight:bold;
        letter-spacing:1.5px;
        vertical-align:middle;
        text-align:center;
        text-transform:uppercase;

        &:lang(en) {
            color:$color-base;
            border-color:rgba($color-base, 0.2);

            &:last-child {
                border-left:0;
            }
        }

        transition:background 0.4s 0.2s, color 0.2s;

        &:lang(ar) {
            font-size:14px;
            font-weight:500;

            &:first-child {
                border-left:0;
            }
        }

        &-text {
            position:relative;
            z-index:1;

            &:lang(ar) {
                letter-spacing:0;
                font-weight:700;
            }
        }

        &:before {
            content:"";
            position:absolute;
            display:block;
            top:-1px;
            left:-1px;
            right:-1px;
            min-height:60px;
            bottom:0;
            z-index:1;
            transform:scaleX(0);
            transform-origin:left center;

            transition:transform 0.3s;
        }

        &:lang(en) {
            &:before {
                transform-origin:right center;
            }
        }

        &.-reverse {
            &:before {
                transform-origin:right center;
            }

            &:lang(en) {
                &:before {
                    transform-origin:left center;
                }
            }
        }

        &:hover {
            color:$color-base !important;
            text-decoration:none;

            &:before {
                transform:scaleX(1);
            }
        }

        &:focus {
            text-decoration:none;
        }

        &.-primary {
            &:before {
                background:$color-primary;
            }
        }

        &.-secondary {
            &:before {
                background:$color-secondary;
            }
        }

        &.-tertiary {
            &:before {
                background:$color-tertiary;
            }
        }

        &.-quaternary {
            &:before {
                background:$color-quaternary;
            }
        }

        &.-quinary {
            &:before {
                background:$color-quinary;
            }
        }

        &.-senary {
            &:before {
                background:$color-senary;
            }
        }

        &.-inverse {
            color:$color-base;
            border-color:rgba($color-base, 0.2);

            &:lang(en) {
                color:$color-text;
                border-color:rgba($color-text, 0.1);
            }
        }

        &.-active {
            color:$color-base !important;

            &:before {
                transition-duration:0.6s;
                transform:scaleX(1);
            }
        }

        &.-differently {
            color:$color-text !important;
        }
    }

    &.-offseted {
        @media (min-width:$screen-md-min) {
            right:102px;
        }
    }

    &.-opaque {
        #{$root} {
            &-item {
                color:$color-text;
                background:rgba($color-base, 0.7);
                border-color:rgba($color-text, 0.1);
            }
        }
    }

    &.-fixed {
        #{$root} {
            &-item {
                color:$color-text;
                background:$color-base;
                border-color:rgba($color-text, 0.1);
                transition-delay:0s, 0s;
            }
        }
    }

    &.-hidden {
        display:none;
    }

    &.-disabled {
        pointer-events:none;

        #{$root} {
            &-item {
                color:$color-text !important;
                background:none;
                border-color:rgba($color-text, 0.1);

                &:before {
                    transform:scaleX(0);
                    transform-origin:right center;
                }
            }
        }

        &.-opaque {
            #{$root} {
                &-item {
                    background:$color-base;
                }
            }
        }
    }
}
