@import "variables";
.rd-infographic {
    width:100%;
    height:100%;
    overflow:hidden;

    color:$color-base;
    background:$color-graybase;

    &-wrap {
        height:100%;
        position:relative;
    }

    &-container {
        position:relative;
        margin:0 20px;
        height:100%;

        @media (min-width:$screen-lg-min) {
            margin:0 120px 0 60px;
        }

        @media (min-width:$screen-xl-min) {
            margin:0 178px 0 80px;
        }
    }

    &-item {
        $root:&;

        display:none;
        position:absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
        height:100%;
        width:100%;

        &.-in, &.-out {
            display:block;
        }

        &.-in {
            z-index:10;
        }

        &:lang(ar) {
            text-align:right;
        }

        &-body {
            height:100%;
            display:table;

            @media (min-width:$screen-lg-min) {
                position:relative;
                z-index:1;
            }

            &-inner {
                display:table-cell;
                vertical-align:middle;
            }
        }

        &-content {
            visibility: hidden;
            opacity: 0;

            @media (min-width:$screen-lg-min) {
                visibility: visible;
                opacity: 1;
                width:388px;
            }

            @media (min-width:$screen-xl-min) {
                width:435px;
            }

            &:lang(ar) {
                direction:rtl;
            }

            &.-visible {
                @media (max-width:$screen-md-max) {
                    visibility: visible;
                    opacity: 1;
                    transition: opacity .5s ease;
                    position:fixed;
                    width:100%;
                    height:calc(100vh - 70px);
                    top:0;
                    right:0;
                    left:0;
                    z-index:1100;
                    padding:80px 20px;
                    overflow:auto;
                    overflow-x:hidden;
                    -webkit-overflow-scrolling:touch;

                    background:$color-graybase;
                }
            }

            &-header {
                padding:0 0 0 40px;

                @media (min-width:$screen-lg-min) {
                    padding:0;
                }
            }

            &-box {
                max-width:480px;
                @media (max-width:$screen-lg-min) {
                    max-width: calc(100% - 101px);
                }
                @media (max-width:$screen-md-min) {
                    max-width: 100%;
                }

                &:lang(ar) {
                    float:right;
                }
            }

            &-close {
                position:absolute;
                top:85px;
                left:20px;

                font-size:18px;
                color:$color-base;

                transition:.2s;

                &:hover, &:focus {
                    color:$color-base;
                    text-decoration:none;
                    opacity:.8;
                }

                @media (min-width:$screen-sm-min) {
                    font-size:26px;
                    top:95px;
                }

                @media (min-width:$screen-lg-min) {
                    display:none;
                }
            }
        }

        &-title {
            font-size:32px;
            letter-spacing:0;
            line-height:32px;

            font-weight:900;

            margin:0 0 30px 0;

            @media (min-width:$screen-sm-min) {
                //catch from prod <-- start
                font-size:24px;
                //catch from prod <-- end
                line-height:67px;
            }
        }

        &-text {
            //catch from prod <-- start
            font-size:20px;
            //catch from prod <-- end
            letter-spacing:0;
            line-height:28px;

            font-weight:300;

            margin:0 0 30px 0;
            //max-width:480px;

            @media (min-width:$screen-sm-min) {
                //catch from prod <-- start
                font-size:20px;
                //catch from prod <-- end
                line-height:35px;
            }

            &:lang(ar) {
                float:right;
            }
        }

        &-brief {
            font-size:14px;
            letter-spacing:0;
            line-height:23px;

            color:darken($color-base, 30%);

            margin:0 0 30px 0;
        }

        &-details {
            width:100%;
            height:100%;

            display:table;

            &-col {
                display:table-cell;
                width:50%;
            }

            &-text {
                font-size:13px;
                letter-spacing:0;
                line-height:20px;

                font-weight:700;
                text-transform:uppercase;

                margin:0 0 30px 0;

                span {
                    color:rgba($color-base, .3);
                }
            }

            &-ranked {
                font-size:24px;
                letter-spacing:0;
                line-height:35px;

                font-weight:300;

                display:inline-block;

                span {
                    display:block;
                    font-size:14px;
                    letter-spacing:0;
                    line-height:23px;

                    color:rgba($color-base, .3);
                }

                &-count {
                    font-size:24px;
                    letter-spacing:0;
                    line-height:35px;

                    font-weight:300;

                    display:inline-block;
                    vertical-align:top;
                    margin:0 0 0 40px;
                }
            }
        }

        &-graphic {
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;

            @media (min-width:$screen-lg-min) {
                left:388px;
            }

            @media (min-width:$screen-xl-min) {
                left:435px;
            }

            &-inner {
                display:flex;
                height:100%;
                width:100%;
                margin:auto 0;
                align-items:center;
                justify-content:center;

                @media (min-width:$screen-sm-min) {
                    padding:100px 60px;
                }

                @media (min-width:$screen-lg-min) {
                    justify-content:flex-end;
                }
            }

            &-canvas {
                display:none;

                @media (min-width:$screen-sm-min) {
                    display:block;
                }

                @media (min-width:$screen-lg-min)  {
                    transform:scale(0.7);
                    transform-origin:right center;
                }

                @media (min-width:$screen-xl-min) and (min-height:850px) {
                    transform:none;
                }

                @media (min-width:1980px) and (min-height:1100px) {
                    transform:scale(1.3);
                }
            }

            &-img {
                margin:auto 0;

                @media (min-width:$screen-sm-min) {
                    display:none;
                }

                img {
                    max-width:100%;
                    max-height:100%;

                    &:first-child {
                        display:block
                    }

                    &:last-child {
                        display:none;
                    }

                    &:lang(ar) {

                        &:first-child {
                            display:none;
                        }

                        &:last-child {
                            display:block;
                        }
                    }
                }
            }

            &.-six {
                @media (min-width:$screen-lg-min) {
                    width:65%;
                }

                @media (min-width:$screen-xl-min) {
                    width:70%;
                }
            }
        }

        &-chart {
            img {
                max-width:100%;

                &:first-child {
                    display:block
                }

                &:last-child {
                    display:none;
                }

                &:lang(ar) {

                    &:first-child {
                        display:none;
                    }

                    &:last-child {
                        display:block;
                    }
                }
            }
        }

        &.-full {

            #{$root} {

                &-body {

                    &-inner {
                        vertical-align:top;
                        padding:123px 0 0 0;

                        @media (min-width:$screen-xl-min) {
                            padding:135px 0 0 0;
                        }
                    }
                }

                &-content {
                    width:400px;

                    @media (min-width:$screen-xl-min) {
                        width:510px;
                    }

                    &.-visible {
                        width:100%;
                    }
                }
            }
        }

        &.-reverse {

            #{$root} {

                &-body {
                    float:right;
                }

                &-graphic {
                    left:0;

                    @media (min-width:$screen-lg-min) {
                        right:388px;
                    }

                    @media (min-width:$screen-xl-min) {
                        right:435px;
                    }

                    &-inner {
                        @media (min-width:$screen-lg-min) {
                            padding:100px 60px 100px 0;
                            justify-content:flex-start;
                        }
                    }

                    &-canvas {
                        transform-origin:left center;
                    }
                }
            }
        }
    }

    &-nav {
        position:absolute;
        right:33px;
        top:0;
        bottom:0;
        z-index:20;

        display:none;
        height:100%;

        @media (min-width:$screen-lg-min) {
            display:table;
        }

        &-inner {
            display:table-cell;
            vertical-align:middle;
        }

        &-item {
            cursor:pointer;
            position:relative;
            display:block;
            width:40px;
            height:1px;

            padding:20px 0;

            &:before {
                position:absolute;
                right:0;
                top:50%;
                content:'';
                width:25px;
                height:1px;

                background:darken($color-base, 50%);

                transition:width .3s, background 0.2s;
            }

            &:hover {
                &:before {
                    background:$color-base;
                }
            }

            &.-active {
                &:before {
                    width:40px;
                    background:$color-base;
                }
            }
        }
    }

    &-control {
        display:table;
        position:absolute;
        left:0;
        right:0;
        bottom:25px;
        width:100%;
        z-index:20;
        text-align:center;

        &:lang(ar) {
            direction:rtl;
        }

        @media (min-width:$screen-sm-min) {
            bottom:40px;
        }

        @media (min-width:$screen-lg-min) {
            display:none;
        }

        &-arr {
            display:table-cell;
            padding:0 10px;

            color:$color-base;
            font-size:18px;
            line-height:1;
            vertical-align:middle;

            @media (min-width:$screen-xs-min) {
                padding:0 20px;
            }

            @media (min-width:$screen-sm-min) {
                padding:0 40px;
            }

            @media (min-width:$screen-lg-min) {
                display:none;
            }

            &:lang(ar) {
                transform:rotate(180deg);
            }

            &:hover, &:focus {
                color:inherit;
                text-decoration:none;
            }

            &.-left {
                text-align:left;
            }

            &.-right {
                text-align:right;
            }
        }

        &-text {
            position:relative;
            display:table-cell;

            color:$color-base;

            direction:ltr;
            vertical-align:bottom;



            @media (min-width:$screen-lg-min) {
                display:none;
            }

            &:before {
                @media (min-width:$screen-lg-min) {
                    content:"";
                    display:inline-block;
                    position:absolute;
                    bottom:0;
                    height:0;
                    width:2px;

                    background:$color-primary;

                    transition:all 0.4s;
                }
            }

            svg {
                display:inline;
                margin:0 10px 0 0;
                font-size:20px;
                vertical-align:middle;

                @media (min-width:$screen-lg-min) {
                    display:none;
                }
            }

            span {
                display:inline;
                vertical-align:middle;
                line-height: 26px;

                font-size:26px;
                font-weight:900;

                transition:all 0.3s;

                @media (min-width:$screen-lg-min) {
                    font-size:13px;
                    font-weight:bold;
                    line-height:1;
                    letter-spacing:1.5px;

                    padding:0 0 10px 10px;

                    &:lang(ar) {
                        font-size:17px;
                    }
                }

                @media (max-width:$screen-mobile) {
                    font-size: 16px;
                }
            }

            &:hover, &:focus {
                color:$color-base;
                text-decoration:none;
            }

            &.-active {
                display:table-cell;

                @media (min-width:$screen-lg-min) {
                    color:$color-base;

                    span {
                        padding:0 0 29px 10px;

                        font-size:17px;
                        letter-spacing:1.96px;
                    }

                    &:before {
                        height:44px;
                    }
                }
            }
        }
    }

    .chart_0, .chart_1, .chart_2, .chart_3, .chart_4, .chart_5, .chart_6 {
        position:relative;
        canvas{
            position: absolute;
            top: 0;
            bottom: 0;
            margin: auto;
        }
