    .chart_0 {
        width:745px;
        height:467px;
        overflow:hidden;
        display:flex;
        align-items:center;
        justify-content:center;

        .hover {
            position:absolute;
            width:745px;
            height:467px;
            left:0px;
            top:0px;

            &-item {
                position:relative;
                width:100px;
                height:100%;
                float:left;

                &-background {
                    position:absolute;
                    width:100%;
                    height:100%;
                    left:0px;
                    top:0px;
                    opacity:0;
                    transition:opacity .2s;
                    background:linear-gradient(to top, rgba(255, 255, 255, .25), rgba(0, 0, 0, 0));
                }

                &-text_0 {
                    transition:opacity .2s;
                    opacity:0;
                }

                &-text_1 {
                    transition:opacity .2s;
                    opacity:.5;
                }

                &:nth-of-type(1) {
                    left:0px;
                }
                &:nth-of-type(2) {
                    left:2px;
                }
                &:nth-of-type(3) {
                    left:4px;
                }
                &:nth-of-type(4) {
                    left:5px;
                }
                &:nth-of-type(5) {
                    left:6px;
                }
                &:nth-of-type(6) {
                    left:7px;
                }
                &:nth-of-type(7) {
                    left:9px;
                }

                &:hover .hover-item-text_0, &:hover .hover-item-text_1 {
                    opacity:1;
                }

                &:hover .hover-item-background {
                    opacity:1;
                }
            }
        }

        &.-en {
            .hover {
                transform:scale(-1, 1);
                left:-35px;
            }
        }
    }

