.rd-rating {
    direction: rtl;
    text-align: left;

    position:relative;

    &:lang(ar) {
        text-align: right;
        &.-disabled {
            transform: scale(-1, 1);
            text-align: left;
        }
        &.-inverse {
            text-align: left;
            transform: scale(-1, 1);
            .rd-rating{
                text-align: left;
                //transform: scale(-1, 1);
            }
        }
    }

    &-tooltip {
        direction:ltr;
        position:absolute;
        left:0;
        bottom:40px;
        width:191px;
        height:60px;

        font-size:13px;
        letter-spacing:0.22px;
        line-height:17px;

        background:$color-text;
        color:$color-base;

        opacity:0;
        z-index:-1;

        transition:opacity ease-in .3s;

        &.-visible {
            opacity:1;
            z-index:1;
        }

        &-inner {
            padding:15px;

            &:lang(ar) {
                transform: scaleX(-1);
                direction: rtl;
                text-align: right;
            }
        }

        &-text-inner {
            display: inline-block;
        }
    }

    &-icon {
        position:relative;
        font-size:14px;
        color:darken($color-base, 60%);

        display:inline-block;
        vertical-align:top;
        margin-right:3px;

        transition: all .2s;

        cursor:pointer;

        &:hover,
        &:hover ~ .rd-rating-icon,
        &.-selected,
        &.-selected ~ .rd-rating-icon {
            color: $color-base;
        }

        &.-half {
            position: absolute;
            left: -4px;

            display:none;

            &.-visible {
                color: $color-base;
                display:inline-block;
            }
        }
    }

    &.-disabled {
        pointer-events:none;
    }

    &.-inverse {
        margin:24px 0 0 0;
    }

    &.-inverse &-icon {

        color: lighten($color-text, 80%);

        &:hover,
        &:hover ~ .rd-rating-icon,
        &.-selected,
        &.-selected ~ .rd-rating-icon {
            color: $color-text;
        }

        &.-half {
            color:$color-text;
        }
    }
}