.rd-comments {
    $root:&;

    &:lang(ar) {
        direction:rtl;
    }

    &-empty {
        margin:40px 0 50px 0;
        max-width:218px;

        &-icon {
            font-size:39px;
            opacity:0.2;
        }

        &-text {
            margin:23px 0 0 0;

            font-size:16px;
            font-weight:normal;
            letter-spacing:0;
            line-height:27px;
        }
    }

    &-login {
        margin:13px 0 0 0;

        &-text {
            font-size:16px;
            letter-spacing:0;
            line-height:29px;
        }

        &-link {
            font-weight:700;
            transition:.2s;

            &:hover, &:active, &:focus {
                text-decoration:none;
                opacity:.7;
            }
        }
    }

    &-social {
        margin:30px 0 50px 0;

        &.-xs {
            margin:0;
        }

        &-list {
            direction:initial;

            &:lang(ar) {

                text-align:right;
            }
        }

        &-item {
            display:inline-block;
            margin-right:17px;
            vertical-align:middle;

            &:last-child {
                margin-right:0;
            }

            &:lang(ar) {

                &:last-child {
                    margin-right:0;
                }
            }
        }

        &-link {
            position:relative;

            font-size:17px;
            line-height:1;
            color:$color-text;

            perspective:1000px;

            &.-google-plus {
                font-size:22px;
            }

            > span {
                display:inline-block;
                position:relative;
                transform-origin:50% 0;
                transform-style:preserve-3d;
                transition:all 0.25s ease-in-out;

                > span {
                    position:absolute;
                    top:110%;
                    left:0;
                    width:100%;
                    height:100%;

                    opacity:0;
                    transform-origin:50% 0;
                    transform:rotateX(-90deg);
                    transition:all 0.25s ease-in-out;
                }
            }

            &:hover > span {
                transform:rotateX(90deg);
                color:$color-text;

                > span {
                    opacity:1;
                    transition-delay:0.05s;
                    transform:rotateX(-90deg);
                    color:$color-text;
                }
            }

            &:active, &:focus {
                color:$color-text;
            }
        }

    }

    &-form {
        margin:55px 0 0 0;
        position:relative;

        transition:.3s padding;

        &.-visible {
            padding:81px 0 0 0;
        }

        &-reply {
            position:absolute;
            top:5px;
            left:0;
            right:0;
            opacity:0;

            padding:20px;
            background:$color-fill;

            transition:.3s opacity;

            &.-inverse {
                background:#202020;
            }

            &.-visible {
                opacity:1;
            }
        }

        &-del {
            display:inline-block;
            vertical-align:top;
            margin:3px 0 0 20px;

            color:$color-error;

            transition:.2s;

            &:hover, &:active, &:focus {
                color:inherit;
                opacity:.8;
            }

            &:lang(ar) {
                margin:5px 20px 0 0;
            }
        }
    }

    &-items {
        margin:50px 0 0 0;
    }

    &-item {
        $this:&;

        margin:40px 0;
        position:relative;

        transition:opacity 0.2s;

        &-inner {
            display:table;
        }

        &-col {
            display:table-cell;
            vertical-align:top;
        }

        &-controls {
            position:absolute;
            right:0;

            &:lang(ar) {
                left:0;
                right:initial;
            }

            .rd-dropdown-menu {
                position:absolute;
                right:0;
                top:30px;

                padding:8px 38px 7px 21px;

                box-shadow:0 6px 10px rgba($color-text, .05);

                @media (min-width:$screen-sm-min) {
                    right:initial;
                    left:-35px;
                }

                &:lang(ar) {
                    padding:8px 21px 7px 38px;
                }
            }

            &-icon {
                color:inherit;

                &:hover, &:focus {
                    color:inherit;
                    text-decoration:none;
                }

                &.-active {
                    color:$color-secondary;
                }
            }

            &-action {
                &-item {
                    display:block;

                    color:$color-disabled;

                    font-size:13px;
                    font-weight:500;
                    line-height:35px;

                    &:hover, &:active, &:focus {
                        color:inherit;
                        opacity:.8;
                        text-decoration:none;
                    }
                }
            }
        }

        &-author {
            display:inline-block;
            margin:-5px 0 0 0;

            &-photo {
                display:inline-block;
                width:30px;
                height:30px;
                margin:0 20px 0 0;
                vertical-align:top;

                background-size:cover;
                border-radius:100%;

                &:lang(ar) {
                    margin:0 0 0 30px;
                }

                &.-primary {

                    @media (min-width:$screen-sm-min) {
                        width:70px;
                        height:70px;

                        border-radius:0;
                    }
                }
            }

            &-title {
                margin:3px 0 0 0;

                display:inline-block;
                vertical-align:middle;

                &-name {
                    font-size:16px;
                    font-weight:bold;
                    letter-spacing:0;
                }

                &-time {
                    margin:4px 0 0 0;

                    opacity:0.3;
                    color:inherit;
                    font-size:13px;
                    letter-spacing:0.28px;

                    &:lang(ar) {
                        margin:-5px;

                        direction:rtl;
                        text-align:right;
                    }
                }
            }

        }

        &-text {
            margin:10px 0 0 55px;

            font-size:16px;
            letter-spacing:0;
            line-height:27px;

            &:lang(ar) {
                margin:10px 55px 0 0;
            }

            &.-primary {
                margin:10px 0 0 0;
            }
        }

        &-action {
            padding:5px 0 0 55px;
            margin:0 -10px;

            &.-primary {
                padding:5px 0 0 0;
            }

            &-item {
                display:inline-block;
                margin:0 10px;

                font-size:13px;
                font-weight:bold;
                letter-spacing:1px;
                line-height:1;
                text-transform:uppercase;
                vertical-align:middle;

                > a {
                    color:inherit;
                    transition:opacity 0.2s;

                    &:hover, &:focus {
                        opacity:0.8;
                        color:inherit;
                        text-decoration:none;
                    }
                }

                &.-xs {
                    margin:0 10px 0 0;

                    &:lang(ar) {
                        margin:0;
                    }
                }
            }
        }

        &-like {
            direction:initial;
            display:inline-block;
            color:inherit;
            user-select:none;
            transition:transform 0.2s;

            &-icon {
                display:inline-block;
                font-size:16px;

                transition:all 0.2s;
            }

            &-num {
                color:inherit;
                display:inline-block;
                margin:0 0 0 11px;
                vertical-align:middle;
            }

            &:active &-icon {
                transform:scale(1.2);
            }
        }

        &-reply {
            padding:0 0 0 20px;

            @media (min-width:$screen-sm-min) {
                padding:0 0 0 55px;
            }

            &:lang(ar) {
                padding:0 20px 0 0;

                @media (min-width:$screen-sm-min) {
                    padding:0 55px 0 0;
                }
            }
        }

        &.-hover {
            cursor:pointer;
            opacity:0.8;
        }

        &.-deleted {
            #{$this} {
                &-text {
                    opacity:0.3;
                }

                &-action {
                    display:none;
                }
            }
        }
    }

    /* Colors */
    &.-primary {
        #{$root} {
            &-item-like.-liked {
                #{$root} {
                    &-item-like-icon {
                        color:$color-primary;
                    }
                }
            }

            &-item-text a {
                color:$color-primary;
            }

            &-login-link {
                color:$color-primary;
            }
        }
    }

    &.-secondary {
        #{$root} {
            &-item-like.-liked {
                #{$root} {
                    &-item-like-icon {
                        color:$color-secondary;
                    }
                }
            }

            &-item-text a {
                color:$color-secondary;
            }

            &-login-link {
                color:$color-secondary;
            }
        }
    }

    &.-tertiary {
        #{$root} {
            &-item-like.-liked {
                #{$root} {
                    &-item-like-icon {
                        color:$color-tertiary;
                    }
                }
            }

            &-item-text a {
                color:$color-tertiary;
            }

            &-login-link {
                color:$color-tertiary;
            }
        }
    }

    &.-quaternary {
        #{$root} {
            &-item-like.-liked {
                #{$root} {
                    &-item-like-icon {
                        color:$color-quaternary;
                    }
                }
            }

            &-item-text a {
                color:$color-quaternary;
            }

            &-login-link {
                color:$color-quaternary;
            }
        }
    }

    &.-quinary {
        #{$root} {
            &-item-like.-liked {
                #{$root} {
                    &-item-like-icon {
                        color:$color-quinary;
                    }
                }
            }

            &-item-text a {
                color:$color-quinary;
            }

            &-login-link {
                color:$color-quinary;
            }
        }
    }

    &.-senary {
        #{$root} {
            &-item-like.-liked {
                #{$root} {
                    &-item-like-icon {
                        color:$color-senary;
                    }
                }
            }

            &-item-text a {
                color:$color-senary;
            }

            &-login-link {
                color:$color-senary;
            }
        }
    }
}
