.rd-input {
    display:inline;

    &_light {
        $this:&;
        position:relative;

        color:$color-text;

        input {
            margin:0;
            padding:0 0 12px 32px;
            width:100%;

            background:none;
            border:none;
            border-bottom:1px solid rgba($color-base, 0.2);
            border-radius:0;
            outline:none;

            font-size:16px;
            line-height:21px;
            letter-spacing:0.28px;

            color:$color-base;

            transition:all 0.2s;

            @include placeholder(rgba($color-base, 0.4));

            &:hover, &:focus {
                border-bottom:1px solid rgba($color-base, 0.4);
            }

            &:invalid {
                box-shadow:none;
                outline:0;
            }

            &:disabled {
                pointer-events:none;
            }
        }

        &-icon {
            position:absolute;
            left:0;
            bottom:3px;
            width:32px;

            font-size:16px;

            color:rgba($color-base, 0.4);
        }

        &.-disabled {
            pointer-events:none;
            opacity:0.5;
        }
    }
}
