.pnlm-container {
    .rd-panobox-pin {
        &:lang(ar) {
            font-family: "Riyadhcity", "Roboto", sans-serif;
        }
    }
}

.rd-panobox {
    width:100%;
    height:100%;
    overflow:hidden;

    &:lang(ar) {
        direction:rtl;
    }

    &-canvas {
        direction:ltr;
        position:relative;
        width:100%;
        height:100%;

        &-img {
            width:100%;
            height:100%;
            background-size:cover;
        }
    }

    &-pin {
        //position:absolute;
        //left:30%;
        //top:50%;
        cursor:pointer;
    }

    &-nav {
        position:absolute;
        left:0;
        bottom:0;

        width:100%;
        height:92px;
    }

    &-items {
        padding-left:20px;

        display:table;

        width:100%;
        height:92px;

        background:$color-text;
    }

    &-item {
        display:inline-block;
        min-width:261px;
        position:relative;

        transition:.2s;

        &:lang(ar) {
            &:before {
                left:initial;
                right:30px;
            }
        }

        &:before {
            content:'';
            position:absolute;
            left:30px;
            top:0;
            bottom:0;

            width:121px;
            height:100%;
            background:repeating-linear-gradient(-45deg, transparent, transparent 10px, lighten($color-text, 50%) 11px, lighten($color-text, 50%) 12px);
            opacity:0;

            transition:all 0.2s;
        }

        &:hover, &:focus, &:active {
            text-decoration:none;
            opacity:.8;
        }

        &-inner {
            display:table;
            height:92px;
        }

        &-col {
            display:table-cell;
            vertical-align:middle;
        }

        &-img {
            display:block;
            position:relative;

            width:60px;
            height:60px;

            background-size:cover;

            &:before {
                content:"";
                position:absolute;
                left:0;
                top:0;

                width:100%;
                height:100%;

                opacity:0;
                background:$color-secondary;

                transition:all 0.2s;
            }
        }

        &-title {
            display:block;
            font-size:14px;
            letter-spacing:.3px;
            line-height:23px;

            font-weight:700;

            color:$color-base;

            padding:0 0 0 28px;
            max-width:180px;

            &:lang(ar) {
                padding:0 28px 0 0;
            }
        }

        &.-active {
            &:before {
                opacity:0.4;
            }
        }

        &.-active &-title {
            color:$color-secondary;
        }

        &.-active &-img {
            &:before {
                opacity:.5;
            }
        }
    }
}
