.rd-checkbox {
    position:relative;
    z-index:2;

    &:lang(ar) {
        direction:rtl;
    }

    &.-primary {
        &:lang(ar) {
            top:10px;
        }
    }

    &-link {
        color:$color-base;
        display:inline-block;

        &:hover, &:focus, &:active {
            opacity:.8;
            text-decoration:none;
            color:$color-base;
        }

        &:lang(ar) {
            margin:0 10px 0 0;
        }
    }

    &-box {
        position:relative;
        display:inline-block;
        vertical-align:middle;
        width:18px;
        height:18px;

        cursor:pointer;
        border:1px solid rgba($color-base, .4);

        text-align:center;
        //vertical-align:middle;

        transition:opacity 0.2s;

        &:after {
            @include icon-styles;
            content:icon-char(check-bold);

            position:relative;
            top:-1px;
            font-size:7px;
            opacity:0;
            transition:opacity 0.2s;
            //vertical-align:top;
        }

        &:lang(ar) {

            &:after {
                top:-6px;
            }
        }
    }

    &-text {
        cursor:pointer;
        display:inline-block;
        margin:0 17px;

        font-size:13px;
        font-weight:700;
        text-transform:uppercase;
        letter-spacing:1.5px;
        line-height:15px;
        vertical-align:middle;

        color:rgba($color-base, .5);

        transition:opacity 0.2s;

        &.-grey {
            color:#9D9D9D;
        }

        &.-secondary {
            font-size:14px;
            font-weight:bold;
        }

        &:lang(ar) {
            letter-spacing:0;
        }
    }

    input {
        position:absolute;
        padding:0;
        margin:0;
        z-index:-999;
        left:-9999px;
        opacity:0;

        &:disabled {
            + .rd-form-submit-col .rd-checkbox-box {
                cursor:default;
                border-style:dashed;
                opacity:1 !important;
            }

            + .rd-checkbox-box {
                cursor:default;
                border-style:dashed;
                opacity:1 !important;
            }

            ~ .rd-form-submit-col .rd-checkbox-text {
                cursor:default;
                opacity:1 !important;
            }

            ~ .rd-checkbox-text {
                cursor:default;
                opacity:1 !important;
            }
        }

        &:checked {
            + .rd-form-submit-col .rd-checkbox-box:after {
                opacity:1;
            }

            + .rd-checkbox-box:after {
                opacity:1;
            }
        }
    }

    &:hover &-text {
        opacity:0.85;
    }

    &.-secondary {
        .rd-checkbox-box {
            position:absolute;
        }

        .rd-checkbox-text {
            padding-left:17px;
        }
    }

    &_switch {

        label {
            display:inline-block;
            vertical-align:middle;
            position:relative;
            height:20px;
            width:35px;
            margin:0;
        }

        input {
            display:none;
        }

        &-slider {
            position:absolute;
            bottom:0;
            left:0;
            right:0;
            top:0;

            background:#DFE0E0;
            border-radius:10px;
            cursor:pointer;

            transition:.3s;

            &:before {
                content:"";
                position:absolute;
                left:2px;
                bottom:2px;
                width:16px;
                height:16px;

                background:$color-base;
                border-radius:50%;

                transition:.3s;
            }
        }

        input:checked + &-slider {
            background:$color-tertiary;
        }

        input:checked + &-slider:before {
            transform:translateX(15px);
        }

        &-text {
            display:inline-block;
            vertical-align:middle;
            margin:0 0 0 10px;

            &:lang(ar) {

                margin:0 10px 0 0;
            }
        }
    }
}
