.rd-searchbox {
    position:relative;
    width:100%;
    height:100%;

    background:$color-fill;

    &:lang(ar) {
        direction:rtl;
    }

    &-box {
        $this:&;
        display:table;
        position:absolute;
        top:0;
        left:0;
        right:0;
        height:78px;
        line-height:1;
        width:100%;
        z-index:10;

        background:$color-base;
        pointer-events:auto;

        transition:height 0.4s;

        @media (min-width:$screen-md-min) {
            height:100%;
        }

        &:lang(ar) {
            direction:rtl;
        }

        &-inner {
            display:table-cell;
            vertical-align:middle;
        }

        &-form {

            &-icon {
                display:inline-block;
                vertical-align:middle;
                margin:0 30px;

                color:lighten($color-text, 80%);
                font-size:16px;

                transition:all 0.2s;

                @media (min-width:$screen-md-min) {
                    margin:0 30px;
                    font-size:37px;
                }

                &:lang(ar) {
                    margin:0 90px 0 30px;

                    @media (min-width:$screen-sm-min) {
                        margin:0 125px 0 30px;
                    }
                }
            }

            &-input {
                display:inline-block;
                vertical-align:middle;

                width:50%;

                @media (min-width:$screen-sm-min) {
                    width:60%;
                }

                @media (min-width:$screen-md-min) {
                    width:70%;
                }

                input {
                    width:100%;

                    color:$color-text;
                    border:0;
                    background:none;
                    outline:none;

                    font-size:20px;
                    font-weight:normal;

                    transition:all 0.2s;

                    @media (min-width:$screen-md-min) {
                        font-weight:bold;
                        font-size:82px;
                    }

                    &:lang(ar) {
                        max-width:160px;

                        @media (min-width:$screen-xs-min) {
                            max-width:200px;
                        }
                        @media (min-width:$screen-sm-min) {
                            max-width:767px;
                        }
                    }

                    @mixin placeholder {
                        color:lighten($color-text, 80%) !important;
                    }

                    &.-placeholder {
                        @include placeholder;
                    }
                    &::-webkit-input-placeholder {
                        @include placeholder;
                    }
                    &::-moz-placeholder {
                        @include placeholder;
                    }
                    &:-moz-placeholder {
                        @include placeholder;
                    }
                    &:-ms-input-placeholder {
                        @include placeholder;
                    }
                }
            }

            &-sort {
                float:right;
                display:none;
                vertical-align:middle;
                padding:5px 125px 0 0;
                transform:scale(0);
                opacity:0;
                transition:opacity 0.4s;

                @media (min-width:$screen-sm-min) {
                    display:inline-block;
                }

                &-toggle {
                    color:$color-text;
                    transition:.2s;

                    &:hover, &:focus {
                        text-decoration:none;
                        color:$color-text;
                        opacity:.8;
                    }

                    &:active {
                        color:$color-text;
                    }
                }

                &:lang(ar) {
                    float:left;
                    padding:10px 0 0 40px;
                }
            }

            &.-sm &-icon {
                margin:0 30px;
                font-size:16px;

                &:lang(ar) {
                    margin:5px 90px 0 30px;

                    @media (min-width:$screen-sm-min) {
                        margin:5px 125px 0 30px;
                    }
                }
            }

            &.-sm &-input {
                input {
                    font-size:20px;
                    font-weight:normal;
                }
            }

            &.-sm &-sort {
                opacity:1;
                transform:scale(1);
                transition-delay:0.2s;
            }
        }

        &.-collapsed {
            height:78px;

            #{$this} {
                &-form {
                    @extend .-sm;
                }
            }
        }
    }

    &-empty {
        position:absolute;
        top:0;
        right:0;
        bottom:0;
        left:0;
        width:100%;
        height:100%;
        display:table;

        text-align:center;

        background:$color-fill;

        @media (min-width:$screen-sm-min) {
            text-align:left;
        }

        @media (max-height:500px) {
            top:78px;
        }

        &:lang(ar) {
            @media (min-width:$screen-sm-min) {
                text-align:right;
            }
        }

        &-inner {
            display:table-cell;
            vertical-align:middle;
            width:100%;
            height:100%;
        }

        &-container {
            margin:0 20px;

            @media (min-width:$screen-md-min) {
                margin:0 40px;
            }
        }

        &-icon {
            display:block;
            vertical-align:top;
            font-size:40px;
            color:lighten($color-text, 70%);

            margin:0 40px;

            @media (min-width:$screen-sm-min) {
                display:inline-block;
            }
        }

        &-text {
            display:inline-block;
            vertical-align:middle;

            &-title {
                font-size:30px;
                line-height:67px;
                font-weight:700;

                color:lighten($color-text, 80%);

                @media (min-width:$screen-sm-min) {
                    font-size:60px;
                    line-height:67px;

                    margin:0 0 15px 0;
                }

                @media (min-width:$screen-md-min) {
                    font-size:82px;
                    line-height:67px;
                }

                &:lang(ar) {
                    margin:0 0 20px 0;
                    line-height:30px;

                    @media (min-width:$screen-sm-min) {
                        margin:0 0 35px 0;
                        line-height:67px;
                    }
                }
            }

            &-desc {
                font-size:14px;
                letter-spacing:.3px;
                line-height:20px;

                color:lighten($color-text, 60%);

                max-width:240px;

                @media (min-width:$screen-sm-min) {
                    max-width:initial;
                }

                &:lang(ar) {
                    max-width:initial;
                }
            }
        }
    }

    &-loader {
        margin:80px 0;
    }

    &-result {
        padding-top:78px;
        height:100%;

        &-tabs {
            position:relative;
            z-index:1;
            padding:35px 0 25px 0;
        }

        &-content {
            margin:0 -20px;

            font-size:0; // Inline Extra Margin Block Hack

            @media (min-width:$screen-md-min) {
                margin:0 -40px;
            }

            @media (min-width:$screen-xl-min) {
                margin:0 -60px;
            }
        }

        &-item {
            display:inline-block;
            position:relative;
            padding:0 20px;
            width:100%;
            vertical-align:top;

            font-size:$font-size;

            color:$color-text;

            transition:.2s;

            @media (min-width:$screen-sm-min) {
                width:(100% / 2);
            }

            @media (min-width:$screen-md-min) {
                padding:0 40px;
            }

            @media (min-width:$screen-lg-min) {
                width:(100% / 3);
            }

            @media (min-width:$screen-xl-min) {
                padding:0 60px;
            }

            &:hover, &:focus {
                color:$color-text;
                text-decoration:none;

                opacity:.8;
            }
        }
    }

    &-page {
        display:none;

        &:lang(ar) {

            .row > div {
                float:right;
            }
        }
    }

    #articles {
        display:block;
    }
}
