.rd-timeline {
    $root:&;

    height:100%;
    width:100%;

    color:$color-base;
    background:$color-graybase;

    &-wrap {
        height:100%;
    }

    &-map {
        height:100%;
        width:100%;
        overflow:hidden;

        &-overlay {
            position:absolute;
            bottom:75px;
            left:-400px;

            transition:left 0.4s;

            @media (max-width:$screen-md-max) {
                left:40px !important;
            }

            @media (max-width:$screen-xs-max) {
                left:20px !important;
            }

            @media (min-width:$screen-sm-min) {
                bottom:105px;
            }

            @media (min-width:$screen-lg-min) {
                bottom:155px;
            }

            &-item {
                position:absolute;
                bottom:0;

                opacity:0;
                transition:all 0.3s;

                &.-active {
                    opacity:1;
                    z-index:1;
                }
            }
        }

        &-render {
            width:70%;
            height:100%;

            @media (min-width:$screen-lg-min) {
                width:60%;
            }

            svg {
                background:$color-graybase;
                position:relative;
                height:130%;
                top:50%;
                left:60%;
                transform:translateX(-50%) translateY(-50%);

                polygon {
                    cursor:pointer;
                    fill:$color-base;
                    stroke:rgba(255, 255, 255, 0);
                    stroke-width:1;
                    opacity:0.05;
                    transition:fill 1s, stroke 1s, opacity 0.2s;

                    &:hover {
                        opacity:0.07;
                    }

                    &.point {
                        opacity:1;
                        pointer-events:none;
                        stroke:none;
                    }

                    &.selected {
                        fill:rgba(0, 0, 0, 0);
                        stroke:$color-text;

                        &:hover {
                            opacity:0.2;
                        }
                    }
                }

                .roads {
                    opacity:0.07;

                    @supports (mix-blend-mode:overlay) {
                        mix-blend-mode:overlay;
                        opacity:0.5;
                    }

                    path {
                        fill:none;
                        stroke:$color-base;
                        stroke-width:1.5;
                        stroke-linejoin:round;
                        stroke-miterlimit:10;
                    }
                }
            }
        }

    }

    &-slider {
        &-item {
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            display:none;

            &.-in, &.-out {
                display:block;
            }

            &.-in {
                z-index:1;
            }
        }
    }

    &-box {
        position:fixed;
        display:none;
        top:0;
        right:0;
        left:0;
        bottom:0;
        overflow:hidden;
        z-index:1100;

        background:$color-graybase;

        transition:right 0.5s;

        @media (min-width:$screen-lg-min) {
            display:block;
            position:absolute;
            top:140px;
            right:34px;
            left:auto;
            bottom:260px;
            width:460px;
            z-index:10;

            background:none;
        }

        &.-visible {
            display:block;
        }

        &-close {
            position:absolute;
            top:34px;
            left:20px;
            z-index:20;

            mix-blend-mode:difference;

            font-size:18px;
            line-height:1;

            a {
                cursor:pointer;
                color:inherit;

                &:hover, &:focus {
                    color:inherit;
                    text-decoration:none;
                }
            }

            @media (min-width:$screen-sm-min) {
                top:50px;
                left:40px;
                font-size:26px;
            }

            @media (min-width:$screen-lg-min) {
                display:none;
            }
        }

        &-title {
            padding:25px 55px;

            @media (min-width:$screen-sm-min) {
                padding:35px 105px 35px 0;
            }
            /* turach */
            @media (max-width: 1270px) and (min-width: $screen-sm-min) {
                padding:35px 105px 35px;
            }
            /* turach <--end */

            &:lang(ar) {
                padding:25px 30px;
                text-align:right;

                @media (min-width:$screen-sm-min) {
                    padding:35px 40px;
                }

                @media (min-width:$screen-lg-min) {
                    padding-right:57px;
                }
            }

            h1, h2, h3 {
                margin:0;
                padding:0;

                font-size:26px;
                font-weight:900;
                line-height:30px;

                @media (min-width:$screen-sm-min) {
                    font-size:40px;
                    line-height:49px;
                }

                @media (min-width:$screen-lg-min) {
                    font-size:50px;
                    line-height:67px;
                }
            }

            h4 {
                display:block;
                margin:0;
                padding:0;

                font-size:11px;

                @media (min-width:$screen-sm-min) {
                    font-size:13px;
                }

                @media (min-width:$screen-lg-min) {
                    padding:0 0 10px 0;

                    font-size:20px;
                    font-weight:bold;
                }
            }
        }

        &-content {
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            overflow:auto;
            overflow-x:hidden;
            -webkit-overflow-scrolling:touch;

            &::-webkit-scrollbar {
                width:0px;
                background:transparent;
            }
        }

        &-text {
            padding:0 30px 0 20px;

            @media (min-width:$screen-sm-min) {
                padding:0 40px;
            }

            @media (min-width:$screen-lg-min) {
                padding:0 57px 0 0;
            }

            &:lang(ar) {
                direction:rtl;
                text-align:right;
                text-indent: -1.2em;
            }

            p {
                margin:0 0 18px 0;
                font-size:14px;
                font-weight:normal;
                line-height:23px;
                letter-spacing:0.3px;

                &.-lead, &:first-child {
                    margin:0 0 32px 0;
                    opacity:1;

                    font-size:24px;
                    font-weight:300;
                    line-height:35px;
                    letter-spacing:0;
                }
            }
        }
    }

    &-control {
        display:table;
        position:absolute;
        left:0;
        right:0;
        bottom:25px;
        width:100%;
        text-align:center;

        &:lang(ar) {
            direction:rtl;
        }

        @media (min-width:$screen-sm-min) {
            bottom:40px;
        }

        @media (min-width:$screen-lg-min) {
            bottom:76px;
            table-layout:fixed;

            border-bottom:1px rgba($color-base, 0.2) solid;
        }

        &-arr {
            display:table-cell;
            padding:0 20px;

            color:$color-base;
            font-size:18px;
            line-height:1;
            vertical-align:middle;

            @media (min-width:$screen-sm-min) {
                padding:0 40px;
            }

            @media (min-width:$screen-lg-min) {
                display:none;
            }

            &:lang(ar) {
                transform:rotate(180deg);
            }

            &:hover, &:focus {
                color:inherit;
                text-decoration:none;
            }

            &.-left {
                text-align:left;
            }

            &.-right {
                text-align:right;
            }
        }

        &-item {
            position:relative;
            display:none;

            color:$color-base;

            direction:ltr;
            vertical-align:bottom;
            white-space:nowrap;

            @media (min-width:$screen-lg-min) {
                display:table-cell;

                color:lighten($color-text, 40%);
            }

            &:before {
                @media (min-width:$screen-lg-min) {
                    content:"";
                    display:inline-block;
                    position:absolute;
                    bottom:0;
                    height:0;
                    width:2px;

                    background:$color-primary;

                    transition:all 0.4s;
                }
            }

            svg {
                display:inline-block;
                margin:0 10px 0 0;
                font-size:20px;
                vertical-align:middle;

                @media (min-width:$screen-lg-min) {
                    display:none;
                }
            }

            span {
                display:inline-block;
                vertical-align:middle;

                font-size:26px;
                font-weight:900;

                transition:all 0.3s;

                @media (min-width:$screen-lg-min) {
                    font-size:13px;
                    font-weight:bold;
                    line-height:1;
                    letter-spacing:1.5px;

                    padding:0 0 10px 10px;

                    &:lang(ar) {
                        font-size:16px;
                        letter-spacing:0;
                    }
                }
            }

            &:hover, &:focus {
                color:$color-base;
                text-decoration:none;
            }

            &.-active {
                display:table-cell;

                @media (min-width:$screen-lg-min) {
                    color:$color-base;

                    span {
                        padding:0 0 29px 10px;

                        font-size:17px;
                        letter-spacing:1.96px;

                        &:lang(ar) {
                            font-size:16px;
                            letter-spacing:1.2px;
                        }
                    }

                    &:before {
                        height:44px;
                    }
                }
            }
        }
    }
}
