.rd-authors {
    width:100%;
    height:100%;

    &-fill {
        position:absolute;
        left:0;
        top:0;
        bottom:0;
        z-index:-1;
        height:100%;
        width:102px;
        background:$color-senary;

        @media (min-width:$screen-sm-min) {
            width:299px;
        }
        @media (min-width:$screen-lg-min) {
            width:491px;
        }

    }

    &-lines {
        position:absolute;
        top:0;
        bottom:0;
        z-index:-1;
        height:100%;
        background:repeating-linear-gradient(-45deg, transparent, transparent 10px, lighten($color-text, 50%) 11px, lighten($color-text, 50%) 12px);
        opacity:.3;

        display:none;

        @media (min-width:$screen-sm-min) {
            left:149px;
            width:300px;
            display:block;
        }
        @media (min-width:$screen-lg-min) {
            width:419px;
            left:285px;
        }
    }

    &-wrap {
        padding-top:110px;
    }

    &-container {
        margin:0 20px;

        @media (min-width:$screen-lg-min) {
            margin:0 44px;
        }

        @media (min-width:$screen-md-min) {
            margin:0 40px;
        }
    }

    &-title {
        display:inline-block;
        margin-bottom:20px;

        color:$color-text;

        line-height:1;

        @media (min-width:$screen-sm-min) {
            margin-bottom:42px;
        }

        &:hover, &:focus {
            color:$color-text;
            text-decoration:none;

            svg {
                position:relative;
                left:-10px;
            }
        }

        &-icon {
            position:relative;
            display:inline-block;
            z-index:1;

            color:$color-text;

            font-size:18px;
            vertical-align:middle;

            @media (min-width:$screen-sm-min) {
                font-size:28px;
            }

            svg {
                position:relative;
                transition:.3s;
                left:0;
            }
        }

        &-text {
            font-size:26px;
            line-height:67px;
            font-weight:900;

            margin-left:23px;

            display:inline-block;
            vertical-align:middle;

            @media (min-width:$screen-sm-min) {
                font-size:40px;
            }
            @media (min-width:$screen-md-min) {
                font-size:60px;
                margin-left:45px;
            }
        }
    }

    &-action {
        width:33.3333%;
        position:relative;
        float:left;

        margin:0 auto 15px;

        &:before {
            content:'';
            padding-top:119%;
            float:left;
        }

        &:after {
            content:"";
            display:table;
            clear:both;
        }

        @media (min-width:$screen-sm-min) {
            width:20%;
            margin:0 auto 30px;
        }
        @media (min-width:$screen-xl-min) {
            width:16.6666%;
        }

        &:hover, &:active, &:focus {
            text-decoration:none;
            color:$color-text;
        }

        &:hover &-link-icon {
            margin-right:-5px;

            &:lang(ar) {
                margin-right:5px;
            }
        }

        &-line {
            position:absolute;
            top:0;
            right:7px;
            bottom:0;
            left:7px;

            background:repeating-linear-gradient(-45deg, transparent, transparent 10px, lighten($color-text, 50%) 11px, lighten($color-text, 50%) 12px);
            opacity:.3;

            @media (min-width:$screen-sm-min) {
                right:9px;
                left:9px;
            }
            @media (min-width:$screen-lg-min) {
                right:10px;
                left:10px;
            }
            @media (min-width:$screen-xl-min) {
                right:15px;
                left:15px;
            }
        }

        &-link {
            display:block;
            position:absolute;
            top:25%;
            right:20px;
            text-align:right;

            color:$color-text;

            @media (min-width:$screen-sm-min) {
                top:30%;
                right:30px;
            }
            @media (min-width:$screen-xl-min) {
                right:45px;
            }

            &-title {
                font-size:14px;
                line-height:20px;
                letter-spacing:0.3px;
                font-weight:900;

                margin:0 0 10px 0;

                @media (min-width:$screen-md-min) {
                    font-size:26px;
                    line-height:30px;
                    letter-spacing:0;

                    margin:0 0 18px 0;
                }
                @media (min-width:$screen-xl-min) {
                    font-size:30px;
                    line-height:37px;
                }

                span {
                    display:block;
                }
            }

            &-icon {
                color:$color-senary;

                transition:.2s;

                &:lang(ar) {
                    text-align:left;
                    transform:rotate(-180deg);
                }
            }
        }

    }

    &-list {

        text-align:center;

        @media (min-width:$screen-sm-min) {
            text-align:left;
        }

        &-row {
            margin:0 -10px;
            @include clearfix;

            @media (min-width:$screen-md-min) {
                margin:0 -9px;
            }

            @media (min-width:$screen-lg-min) {
                margin:0 -12px;
            }

            @media (min-width:$screen-xl-min) {
                margin:0 -15px;
            }
        }

        &-item {
            width:50%;
            position:relative;
            float:left;

            margin:0 auto 15px;

            cursor:pointer;

            &:before {
                content:'';
                padding-top:119%;
                float:left;
            }

            &:after {
                content:"";
                display:table;
                clear:both;
            }

            @media (min-width:$screen-xs-min * 1.5) {
                width:33.3333%;
            }
            @media (min-width:$screen-sm-min) {
                width:25%;
                margin:0 auto 30px;
            }
            @media (min-width:$screen-lg-min) {
                width:20%;
            }
            @media (min-width:$screen-xl-min) {
                width:16.6666%;
            }

            &:hover &-name {
                bottom:25px;

                @media (min-width:$screen-sm-min) {
                    bottom:35px;
                }
            }

            &.-sm {
                width:33.3333%;

                @media (min-width:$screen-sm-min) {
                    width:20%;
                }
                @media (min-width:$screen-xl-min) {
                    width:16.6666%;
                }
            }

            &.-sm &-name {
                display:none;

                @media (min-width:$screen-sm-min) {
                    display:block;
                }
            }

            &-tumb {
                position:absolute;
                top:0;
                right:7px;
                bottom:0;
                left:7px;
                box-shadow: inset 0 -270px 160px -180px $color-text;
                background-position:center center;
                background-repeat:no-repeat;
                background-size:cover;

                filter:grayscale(100%);
                filter:gray;

                @media (min-width:$screen-sm-min) {
                    right:9px;
                    left:9px;
                }
                @media (min-width:$screen-lg-min) {
                    right:10px;
                    left:10px;
                }
                @media (min-width:$screen-xl-min) {
                    right:15px;
                    left:15px;
                }
            }

            &-name {
                position:absolute;
                left:30px;
                bottom:15px;

                font-size:14px;
                letter-spacing:.3px;
                line-height:20px;
                font-weight:600;
                color:$color-base;

                text-align:left;

                transition:bottom .2s;

                @media (min-width:$screen-md-min) {
                    left:40px;
                    bottom:20px;

                    font-size:18px;
                    letter-spacing:.39px;
                    line-height:23px;
                }

                span {
                    display:block;
                }

                &:lang(ar) {
                    right:30px;
                    left:initial;

                    text-align:right;

                    @media (min-width:$screen-md-min) {
                        right:40px;
                    }
                }
            }
        }
    }
}
