.rd-sprite {
    display:inline-block;
    background-image: url(../img/sprites/sprites.png);
    background-repeat: no-repeat;
    &.-adalogo_en {
        background-position: -195px 0px;
        width: 245px;
        height: 45px;
    }
    &.-logo-4 {
        background-position: -257px -59px;
        width: 110px;
        height: 17px;
    }
    &.-logo-cuberto {
        background-position: -195px -46px;
        width: 117px;
        height: 9px;
    }
    &.-logo-entertainment {
        background-position: -117px -99px;
        width: 47px;
        height: 31px;
    }
    &.-logo-footer {
        background-position: 0px 0px;
        width: 194px;
        height: 58px;
    }
    &.-logo-public-transportation {
        background-position: -134px -59px;
        width: 122px;
        height: 34px;
    }
    &.-appstore-soon {
        background-position: 0px -99px;
        width: 116px;
        height: 40px;
    }
    &.-google-play-soon {
        background-position: 0px -59px;
        width: 133px;
        height: 39px;
    }

        @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
            background-image: url(../img/sprites/sprites@2x.png);
            background-size: 440px 139px;
        }
}