.rd-infobox {
    $root:&;

    position:relative;
    height:100%;
    width:100%;

    &:lang(ar) {
        direction:rtl;
    }

    &-inner {
        height:100%;
        width:100%;
    }

    &-content {
        position:relative;
        height:100%;
        width:100%;
        overflow:auto;
        -webkit-overflow-scrolling:touch;
        z-index:1;

        &::-webkit-scrollbar {
            width:0px;
            background:transparent;
        }
    }

    &-left {
        float:left;
        height:100%;
        width:100%;
        overflow:hidden;

        transition:width 0.6s;

        &.-sm {
            @media (min-width:$screen-md-min) {
                width:50%;
            }
        }
    }

    &-right {
        position:fixed;
        top:0;
        left:0;
        bottom:0;
        height:100%;
        width:0%;
        overflow:hidden;
        z-index:999;

        background:$color-fill;

        transition:width 0.4s;

        @media (min-width:$screen-md-min) {
            position:relative;
            float:right;
            transition:none;
        }

        &.-visible {
            width:100%;

            @media (min-width:$screen-md-min) {
                width:50%;
            }
        }
    }

    &-fill {
        position:absolute;
        top:0;
        left:0;
        bottom:0;
        width:57px;
        height:100%;

        background:$color-fill;
        transition:all 0.6s cubic-bezier(.65, .37, .4, .92);

        @media (min-width:$screen-md-min) {
            width:77px;
        }

        @media (min-width:$screen-xl-min) {
            width:95px;
        }

        &.-center {
            display:none;
            left:(100% / 3);

            @media (min-width:$screen-lg-min) {
                display:block;
            }
        }

        &.-right {
            display:none;
            left:(100% / 2);

            @media (min-width:$screen-sm-min) {
                display:block;
            }

            @media (min-width:$screen-lg-min) {
                left:(100% / 3) * 2;
            }
        }

        &.-hidden {
            opacity:0;
        }

        &.-half {
            @media (min-width:$screen-md-min) {
                left:50%;
                width:50%;
            }
        }
    }

    &-container {
        padding:0 20px;

        @media (min-width:$screen-md-min) {
            padding:0 40px;
        }

        @media (min-width:$screen-xl-min) {
            padding:0 60px;
        }
    }

    &-header {
        padding:90px 0 0 0;

        @media (min-width:$screen-md-min) {
            padding:110px 0 0 0;
        }

        &-inner {
            display:table;
            width:100%;
        }

        &-col {
            display:table-cell;
            vertical-align:middle;

            &:lang(ar) {
                text-align:right;
            }

            &.-right {
                text-align:right;

                &:lang(ar) {
                    text-align:left;
                }
            }
        }

        &-title {
            display:inline-block;

            a, h1, h2, h3, h4 {
                display:inline-block;
                padding:0;
                margin:0;

                font-size:26px;
                line-height:30px;
                font-weight:900;
                vertical-align:middle;

                color:$color-text;

                @media (min-width:$screen-sm-min) {
                    font-size:40px;
                    line-height:49px;
                }

                @media (min-width:$screen-lg-min) {
                    font-size:60px;
                    line-height:67px;
                }
            }

            &:hover, &:focus, &:active {
                color:$color-text;
                opacity:.8;

                text-decoration:none;

                svg {
                    left:-10px;

                    @media (max-width:$screen-sm-min) {
                        left:-5px;
                    }
                }
            }

            &-icon {
                color:$color-text;

                display:inline-block;
                vertical-align:middle;
                margin:0 25px 0 0;

                font-size:28px;
                line-height:1;

                transition:.2s;

                @media (max-width:$screen-md-min) {
                    font-size:18px;
                }

                @media (min-width:$screen-md-min) {
                    margin:0 40px 0 0;
                }

                svg {
                    position:relative;
                    transition:left .2s;
                    left:0;
                    display:inline-block;
                    vertical-align:middle;
                }

                &:lang(ar) {
                    transform:rotate(180deg);
                    margin:0 0 0 25px;

                    @media (min-width:$screen-md-min) {
                        margin:0 0 0 40px;
                    }
                }
            }
        }
    }

    &-nav {
        display:none;
        margin:0 -25px;

        @media (min-width:$screen-sm-min) {
            display:block;
        }

        &-item {
            display:inline-block;
            margin:0 25px;
            vertical-align:middle;
        }

        &-link {
            display:inline-block;
            font-size:14px;
            font-weight:600;
            letter-spacing:.88px;
            line-height:1;

            color:inherit;

            transition:.2s;

            &:hover, &:focus {
                opacity:.8;
                color:inherit;
                text-decoration:none;
            }

            &:lang(ar) {
                font-size:13px;
                font-weight:700;
                letter-spacing:0;
            }

            &-icon {
                display:inline-block;
                vertical-align:top;
                margin-left:11px;

                font-size:12px;

                transform:rotate(0deg);
                transition:all 0.2s ease-in-out;

                &:lang(ar) {
                    margin-left:initial;
                    margin-right:11px;
                }
            }
        }
    }

    &-items {
        margin:35px 0 0 0;
        width:100%;
        font-size:0; // Inline Extra Margin Block Hack

        @media (min-width:$screen-lg-min) {
            margin:80px 0 0 0;
        }

        &:after {
            content:"";
            display:block;
            clear:both;
        }

        &.-byone {
            #{$root} {
                &-item {
                    @media (min-width:$screen-md-min) {
                        width:100% !important;
                    }
                }
            }
        }
    }

    &-item {
        display:inline-block;
        position:relative;
        padding:0 20px;
        width:100%;
        vertical-align:top;

        font-size:$font-size;

        @media (min-width:$screen-sm-min) {
            width:(100% / 2);
        }

        @media (min-width:$screen-md-min) {
            padding:0 40px;
        }

        @media (min-width:$screen-lg-min) {
            width:(100% / 3);
        }

        @media (min-width:$screen-xl-min) {
            padding:0 60px;
        }
    }

    &-close {
        position:absolute;
        z-index:1100;
        top:12px;
        right:18px;
        padding:12px;

        font-size:18px;
        line-height:1;

        transform:scale(0);
        transition:all 0.4s;

        @media (min-width:$screen-sm-min) {
            top:22px;
            right:22px;
            font-size:26px;
        }

        a {
            display:inline-block;
            color:inherit;

            transition:transform 0.3s, opacity 0.2s;

            &:hover, &:focus {
                color:inherit;
                text-decoration:none;

                opacity:0.8;
            }

            &:active {
                opacity:0.6;
            }
        }

        &.-inverse {
            a {
                color:$color-base;
            }
        }

        &.-visible {
            transform:scale(1);
        }
    }

    /* Colors */
    &.-primary {
        #{$root} {
            &-header {
                &-title {
                    span {
                        color:$color-primary;
                    }
                }
            }
        }
    }

    &.-secondary {
        #{$root} {
            &-header {
                &-title {
                    span {
                        color:$color-secondary;
                    }
                }
            }
        }
    }

    &.-tertiary {
        #{$root} {
            &-header {
                &-title {
                    span {
                        color:$color-tertiary;
                    }
                }
            }
        }
    }

    &.-quaternary {
        #{$root} {
            &-header {
                &-title {
                    span {
                        color:$color-quaternary;
                    }
                }
            }
        }
    }

    &.-quinary {
        #{$root} {
            &-header {
                &-title {
                    span {
                        color:$color-quinary;
                    }
                }
            }
        }
    }

    &.-senary {
        #{$root} {
            &-header {
                &-title {
                    span {
                        color:$color-senary;
                    }
                }
            }
        }
    }
}