.rd-modal {

    &_full {
        display:none;
        position:fixed;
        top:0;
        right:0;
        bottom:0;
        left:0;
        z-index:1000;
        overflow:hidden;
        padding:0 !important;

        pointer-events:none;

        @media (min-width:$screen-md-min) {
            z-index:105;
        }

        &-dialog {
            position:relative;
            height:100%;
            width:100%;
            overflow:hidden;
            overflow-y:auto;
            -webkit-overflow-scrolling: touch;
            z-index:1;
            transform:scale(0.7);

            background:$color-base;
            pointer-events:auto;

            opacity:0;
            transition:opacity 0.5s, transform 0.3s;

            &.-inverse {
                background:$color-graybase;
            }
        }

        &-close {
            position:absolute;
            pointer-events:auto;
            z-index:200;
            top:0;
            right:0;

            line-height:1;

            // mix-blend-mode:exclusion;
            transition:all 0.2s;
           

            a {
                display:block;
                padding:20px;
                color:black;
                background-color: white;
                transition:opacity 0.2s;

                &:hover {
                    opacity:0.8;
                    color:black;
                    text-decoration:none;
                }

                &:active {
                    opacity:0.6;
                }
            }

            &.-lg {
                // right:10px;
                font-size:18px;

                @media (min-width:$screen-md-min) {
                    top:14px;
                    right:14px;
                    font-size:26px;
                    mix-blend-mode:exclusion;
                  a {
                    background:none;
                    color: $color-base;
                   
                  }
                }
            }

            &.-xs {
                right:0;
                top:7px;
                padding:0;
                font-size:18px;
            }

            &.-inverse {
                mix-blend-mode:normal;
            }
        }

        &.in &-dialog {
            opacity:1;
            transform:scale(1);
        }

        &.-front {
            z-index:1000;
        }

        &.-offseted {
            @media (min-width:$screen-md-min) {
                right:102px;
            }
        }
    }

    &_active {
        display:block;
        opacity:1;
    }

    &_viewer {
        display:none;
        position:fixed;
        top:0;
        right:0;
        bottom:0;
        left:0;
        padding:0;
        z-index:1050;
        overflow:hidden;
        text-align:center;
        outline:0;

        @media (min-width:$screen-lg-min) {
            padding:40px 30px;
        }

        @media (min-width:$screen-xl-min) {
            padding:140px 130px;
        }

        &.-md {
            @media (min-width:$screen-md-min) {
                padding:90px 200px;
            }
            &[style="display: block;"]{
                .owl-stage-outer{
                    animation-name: appear;
                    animation-duration: 1s;
                    animation-fill-mode: forwards;
                }
            }
        }

        &-dialog {
            position:relative;
            height:100%;
            width:100%;
            margin:0 auto;
            text-align:left;
        }

        &-body {
            height:100%;
            width:100%;
            overflow:auto;
            overflow-x:hidden;
            -webkit-overflow-scrolling: touch;

            &.-inverse {
                background:lighten($color-text, 15%);
            }
        }

        &-share {
            position:absolute;
            top:40px;
            left:40px;
            z-index:1;

            font-size:16px;
            line-height:1;

            a {
                color:lighten($color-text, 60%);

                transition:all 0.2s;

                &:hover, &:focus {
                    color:$color-base;
                }
            }
        }

        &-close {
            position:absolute;
            z-index:1100;
            top:22px;
            right:22px;
            padding:12px;

            color:$color-base;
            mix-blend-mode:difference;

            font-size:18px;
            line-height:1;

            @media (min-width:$screen-sm-min) {
                font-size:35px;
                //catch from prod <-- start
                color: #7f7f7f;
                //catch from prod <-- end
            }

            a {
                display:inline-block;
                color:inherit;

                transition:transform 0.3s, opacity 0.2s;

                &:hover, &:focus {
                    opacity:0.8;
                    color:inherit;
                    text-decoration:none;
                }

                &:active {
                    opacity:0.6;
                }
            }
        }

        &-video {
            width:100%;
            height:100%;
            overflow:hidden;
        }

        iframe {
            width:100%!important;
            height:100%!important;
        }
    }

    &_alert {
        display:none;
        position:absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
        z-index:1050;
        pointer-events:none;
        text-align:center;
        pointer-events:none;

        &:before {
            content:"";
            display:inline-block;
            vertical-align:middle;
            height:100%;
        }

        &-backdrop {
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            pointer-events:auto;

            background:rgba(0,0,0,0.7);
        }

        &-dialog {
            position:relative;
            display:inline-block;
            margin:0 20px;
            max-width:280px;
            text-align:left;
            pointer-events:auto;
            vertical-align:middle;
            pointer-events:auto;

            background:$color-base;
            color:$color-text;

            @media (min-width:$screen-sm-min) {
                max-width:354px;
            }

            @media (min-width:$screen-xl-min) {
                max-width:444px;
            }
        }

        &-body {
            padding:20px;

            @media (min-width:$screen-sm-min) {
                padding:30px;
            }

            @media (min-width:$screen-xl-min) {
                padding:30px 40px;
            }

            &:lang(ar) {
                text-align:right;
            }
        }

        &-close {
            position:absolute;
            top:24px;
            right:24px;

            color:$color-text;

            font-size:12px;
            line-height:1;

            &:lang(ar) {
                top:18px;
                left:12px;

                @media (min-width:$screen-sm-min) {
                    top:18px;
                    left:18px;
                }
            }

            a {
                color:inherit;

                &:hover, &:focus {
                    color:inherit;
                    text-decoration:none;
                }
            }
        }

        &-title {
            margin:0 0 8px 0;

            font-size:16px;
            font-weight:bold;
            letter-spacing:0.34px;
            line-height:23px;

            @media (min-width:$screen-sm-min) {
                margin:0 0 11px 0;

                font-size:18px;
                letter-spacing:0.39px;
            }
        }

        &-text {
            opacity:0.7;
            font-size:13px;
            letter-spacing:0.28px;
            line-height:20px;

            @media (min-width:$screen-sm-min) {
                font-size:14px;
                letter-spacing:0.3px;
                line-height:23px;
            }
        }
    }

    &_box {
        position:fixed;
        top:0;
        right:0;
        bottom:0;
        left:0;
        z-index:1050;
        display:none;
        overflow:hidden;
        overflow-y:auto;
        -webkit-overflow-scrolling:touch;
        text-align:center;
        outline:0;

        &:before {
            content:"";
            display:inline-block;
            vertical-align:middle;
            height:100%;

            @media (max-width:$screen-xs-max) {
                display:initial;
            }
        }

        &-dialog {
            position:relative;
            display:inline-block;
            width:320px;
            margin:0 auto;
            text-align:left;
            vertical-align:middle;

            @media (min-width:$screen-xs-min) {
                width:370px;
            }
            @media (min-width:$screen-sm-min) {
                width:520px;
            }

            &-overlay {
                background:$color-text;
            }

            &.-lg {
                width:780px;

                @media (max-width:$screen-md-min) {
                    width:630px;
                }

                @media (max-width:$screen-sm-min) {
                    width:initial
                }
            }
        }

        &-top {
            background-position:center center;
            background-size:cover;
            height:360px;
        }

        &-content {
            position:relative;
            overflow:hidden;

            background:$color-base;

            padding:50px;

            @media (max-width:$screen-sm-min) {
                padding:20px;
            }

            &.-xs {
                padding:30px;
            }
        }

        &-close {
            position:absolute;
            right:20px;
            top:20px;

            border:none;
            background:none;

            font-size:18px;
            line-height:1;
        }

        &-header {
            width:100%;
            margin-bottom:40px;

            &-title {
                color:$color-text;
                font-size:40px;
                font-weight:900;
                &:lang(ar) {
                    text-align: right;
                }

                &.-indent {
                    margin-bottom:13px;
                }

                @media (max-width:$screen-sm-min) {
                    font-size:26px;
                }
            }

            &-description {
                font-size:18px;
                letter-spacing:.39px;
                line-height:23px;
                font-weight:300;
                color:$color-text;

                margin-bottom:40px;

                @media (max-width:$screen-sm-min) {
                    font-size:12px;
                }

                &:lang(ar) {
                    font-weight:400;
                    text-align: right;
                }
            }

            &-actions {
                display:table-cell;
                vertical-align:middle;
                text-align:right;

                a {
                    color:$color-smooth;
                    font-size:12px;
                    transition:color 0.2s;

                    &:hover, &:focus {
                        color:$color-danger;
                        text-decoration:none;
                    }
                }
            }
        }

        .rd-form {
            margin:0 auto;
        }
    }
}

/* Bootstrap Approach */
.modal-backdrop.in {
    opacity:.95;
}

@keyframes appear {
    0% {
        opacity: 0;
    }
    70% {
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}