.rd-wrap {
    height:100%;
}

.rd-screen {
    height:100%;
}

.rd-layout {
    background:$color-fill;
}

.rd-content {
    height:100%;
    overflow:auto;
    -webkit-overflow-scrolling:touch;
    -ms-overflow-style:none;

    &::-webkit-scrollbar {
        width:0px;
        background:transparent;
    }
}

.rd-container {
    padding:0 20px;

    @media (min-width:$screen-sm-min) {
        padding:0 33px;
    }

    @media (min-width:$screen-md-min) {
        padding:0 45px;
    }
}
