.rd-radiobox {

    &-box {
        position:relative;
        display:inline-block;
        vertical-align:middle;
        width:18px;
        height:18px;

        cursor:pointer;
        border:1px solid rgba($color-text, .15);
        border-radius:50%;

        transition:opacity 0.2s;

        &:after {
            content:'';
            position:absolute;
            top:-1px;
            left:-1px;
            opacity:0;
            width:18px;
            height:18px;

            border:5px solid $color-primary;
            border-radius:50%;

            transition:opacity 0.2s;
        }
    }

    &-text {
        cursor:pointer;
        display:inline-block;
        margin:0 17px;

        font-size:13px;
        font-weight:700;
        text-transform:uppercase;
        letter-spacing:1.5px;
        line-height:15px;
        vertical-align:middle;

        color:rgba($color-text, .3);

        transition:opacity 0.2s;

        &:lang(ar) {
            letter-spacing:0;
        }
    }

    input {
        position:absolute;
        padding:0;
        margin:0;
        z-index:-999;
        left:-9999px;
        opacity:0;

        &:checked {

            + .rd-radiobox-box:after {
                opacity:1;
            }
        }
    }

    &:hover &-text {
        opacity:0.85;
    }
}
