.cluster {
    z-index: 3!important;
}
.gm-style {
    .rd-mapbox-pin {
        &.-visible {
            &:lang(ar) {
                font-family: "Riyadhcity", "Roboto", sans-serif;
            }
        }
    }
}
.rd-mapbox {
    position:relative;
    width:100%;
    height:100%;

    &-control {
        &-item {
            position:absolute;
            z-index:1;

            &.-zoom {
                top:50%;
                left:32px;
                margin:-64px 0 0 0;

                button:last-child {
                    margin:5px 0 0 0;
                }
            }

            &.-filter {
                left:32px;
                bottom:32px;
                z-index:2;

                @media (min-width:$screen-lg-min) {
                    bottom:32px + 60px;
                }

                &:lang(ar) {
                    left:initial;
                    right:32px;
                }
            }

            &.-open {
                left:20px;
                bottom:90px;

                text-align:center;

                &:lang(ar) {
                    left:30px;
                    bottom:90px;
                }

                @media (min-width:$screen-md-min) {
                    display:none;
                }
            }

            &.-traffic {
                right:32px;
                bottom:32px;

                @media (min-width:$screen-lg-min) {
                    bottom:32px + 60px;
                }

                &:lang(ar) {
                    right:200px;
                    //left:32px;
                }
            }
        }
    }

    &-canvas {
        width:100%;
        height:100%;
        overflow:hidden;
        &.-black{
            background-color: $color-graybase;
            &>.rd-loader_inline{
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto;
                z-index: 1;
                color: white;
            }
        }
        /* Google Maps approach */
        @media (min-width:$screen-md-min) {
            .gm-style {
                > div {
                    &:nth-child(2) {
                        bottom:95px !important;
                    }
                }

                .gmnoprint, .gmnoscreen {
                    bottom:95px !important;
                }
            }
        }
    }

    &-point {
        display:inline-block;
        height:30px;
        width:30px;

        cursor:pointer;

        font-size:13px;
        font-weight:bold;
        letter-spacing:0;
        line-height:30px;
        text-align:center;

        color:$color-base;

        transition:.2s;

        &:hover {
            opacity:0.9;
        }

        @media (min-width:$screen-xs-min) {
            height:34px;
            width:34px;

            font-size:16px;
            line-height:34px;
        }

        @media (min-width:$screen-sm-min) {
            height:38px;
            width:38px;

            font-size:16px;
            line-height:38px;
        }

        /* Colors */
        &.-primary {
            background:$color-primary;
        }

        &.-secondary {
            background:$color-secondary;
        }

        &.-tertiary {
            background:$color-tertiary;
        }

        &.-quaternary {
            background:$color-quaternary;
        }

        &.-quinary {
            background:$color-quinary;
        }

        &.-senary {
            background:$color-senary;
        }

        &.-inverse {
            background:$color-base;
            color:$color-text;
        }
    }
  &-pin {
        display:inline-block;
        position:relative;
        top:20.5px;
        height:41px;
        width:41px;
        padding:14px;
        z-index:2!important;
        cursor:pointer;
        transform:scale(0);
        transition: transform;


        &[style*="z-index: 1"] {
              z-index: 4 !important;
              transition-delay: 0s!important;
          }
        &[style*="z-index: auto"] {
            transition-delay: .5s!important;
        }
        &:before {
            content:"";
            display:block;
            position:absolute;
            height:100%;
            width:100%;
            top:0;
            left:0;

            background:rgba($color-base, 0.1);
            border-radius:50%;

            animation:rd-mapbox-pin-glow 2s infinite alternate;
            animation-play-state:paused;
        }

        &:after {
            content:"";
            display:block;
            position:relative;
            height:13px;
            width:13px;
            z-index:1;

            background:$color-secondary;
            border-radius:50%;

            animation:rd-mapbox-pin-hover 1s infinite alternate;
            animation-play-state:paused;
            transition:transform;
        }
          &.-current:after{
              background:#fe0036;
          }
        &.-inverse {
            top:12px;
            height:34px;
            width:34px;
            padding:12px;

            &:before {
                background:rgba($color-text, 0.1);
                border:1px lighten($color-text, 60%) solid;
            }

            &:after {
                height:10px;
                width:10px;

                background:$color-text;

                animation:rd-mapbox-pin-hover 1s infinite alternate;
            }
        }

        &.-visible {
            transform: scale(1);
            transition-delay: .5s;
            &:before, &:after {
                animation-play-state: running;
            }
        }

        &.-active {
            top:22.5px;
            height:45px;
            width:45px;
            padding:14px;

            &:before {
                background:$color-secondary;
            }

            &:after {
                height:17px;
                width:17px;

                background:$color-text;
                animation:none;
            }

            &.-blue{
                &:before{
                    background: #4a90e2;
                }
                &:after{
                    background: #000;
                }
            }
        }

        &.-disabled {
            &:after {
                background:rgba($color-base, 0.3);
            }
        }

        &.-current {
            top:93.5px;
            height:187px;
            width:187px;
            padding:87px;
            z-index: 4!important;
            cursor:default;

            &:after {
                border:2px $color-base solid;
            }

            &:before {
                animation:rd-mapbox-pin-glow-2 2s infinite;
            }
            .rd-tooltip_object{
                display: none;
             }
        }

        &.-blue{
            &:after{
                background: #4a90e2;
            }
        }
    }
}
.-metro{
    &-station {
        $this:&;

        display:block;
        position:relative;
        top:20px;
        height:40px;
        width:40px;
        padding:15px;
        z-index:1!important;

        transform: scale(1);

        &:before {
            content:"";
            display:block;
            position:absolute;
            height:100%;
            width:100%;
            top:0;
            left:0;
            //:not('.-metro-station')
            opacity:0;
            background:rgba($color-base, 0.1);
            border-radius:50%;

            transform:scale(0.75);

            transition:.2s;
        }

        &:hover {

            &:before {
                opacity:1;
                z-index:2;
                animation: none;
                transform:scale(1);
            }
        }

        &:hover &-color {
            transform:scale(1.02);

            border:2px solid #262626;
        }

        &-color {
            display:block;
            position:absolute;
            height:10px;
            width:10px;
            z-index:1;

            border:3px solid #262626;
            border-radius:50%;

            transition:.2s;
        }

        &.-active {
            padding:11px;

            &:before {
                opacity:1;
                z-index:2;
                top:-3px;
                left:-3px;
                transform:scale(1);
                background-color: rgba(255,255,255,.1);
                animation:rd-mapbox-pin-glow-3 2s infinite alternate;
            }

            #{$this} {

                &-color {
                    width:18px;
                    height:18px;

                    transform:scale(1);

                    border:3px solid #262626;
                }
            }
        }

        &.rd-mapbox-pin:after{
            content: normal;
        }
    }
    &-transfer {
        position:relative;
        width:40px;
        height:40px;
        display:flex;
        align-items:center;
        justify-content:center;
        top:20px;
        z-index:1!important;

        &:before {
            content:"";
            display:block;
            position:absolute;
            height:100%;
            width:100%;
            top:0;
            left:0;

            opacity:0;
            background:rgba($color-base, 0.1);
            border-radius:50%;

            transform:scale(0.75);

            transition:.2s;
        }

        &:hover {

            &:before {
                opacity:1;
                z-index:2;

                transform:scale(1);
            }
        }

        &.-active {

            &:before {
                opacity:1;
                z-index:2;
                background-color: rgba(255,255,255,.1);
                transform:scale(1);
                animation:rd-mapbox-pin-glow-3 2s infinite alternate;
            }
        }

       &:after{
        content: normal !important;
      }
    }
}

@keyframes rd-mapbox-pin-hover {
    0% {
        opacity:1;
    }
    100% {
        opacity:0.3;
    }
}

@keyframes rd-mapbox-pin-glow {
    0% {
        transform:scale(1);
    }
    100% {
        transform:scale(0.85);
    }
}

@keyframes rd-mapbox-pin-glow-2 {
    0% {
        transform:scale(0);
        opacity:1;
    }

    90% {
        opacity:0;
    }

    100% {
        transform:scale(1);
        opacity:0;
    }
}

@keyframes rd-mapbox-pin-glow-3 {
    0% {
        transform:scale(0.85);
    }
    100% {
        transform:scale(1);
    }
}
