.rd-social {
    margin:30px 0 50px 0;

    &.-xs {
        margin:0;
    }

    &-list {
        direction:initial;

        &:lang(ar) {

            text-align:right;
        }
    }

    &-item {
        display:inline-block;
        margin-right:17px;
        vertical-align:middle;

        &:last-child {
            margin-right:0;
        }

        &:lang(ar) {
            &:last-child {
                margin-right:0;
            }
        }
    }

    &-link {
        position:relative;

        color:inherit;
        font-size:17px;
        line-height:1;

        perspective: 1000px;

        &.-google-plus {
            font-size:22px;
        }

        > span {
            display:inline-block;
            position: relative;
            transform-origin: 50% 0;
            transform-style: preserve-3d;
            transition:all 0.25s ease-in-out;

            > span {
                position: absolute;
                top: 110%;
                left: 0;
                width: 100%;
                height: 100%;

                opacity:0;
                transform-origin: 50% 0;
                transform: rotateX(-90deg);
                transition:all 0.25s ease-in-out;
            }
        }

        &:hover > span {
            transform: rotateX(90deg);
            color:inherit;

            > span {
                opacity:1;
                transition-delay: 0.05s;
                transform: rotateX(-90deg);
                color:inherit;
            }
        }

        &:active, &:focus {
            color:inherit;
        }
    }

}
