.rd-nav {
    display:inline;

    &:lang(ar) {
        direction:rtl;
    }

    &_tabs {
        display:block;

        background:$color-base;

        &-item {
            display:inline-block;
            position:relative;
            padding:22px 12px;

            color:$color-text;
            opacity:0.5;

            font-size:13px;
            font-weight:700;
            letter-spacing:1.5px;

            transition:all 0.2s;

            @media (min-width:$screen-lg-min) {
                padding:22px 20px;
            }

            &:lang(ar) {
                letter-spacing:0;
            }

            span {
                position:relative;
                z-index:1;
            }

            svg, i {
                position:relative;
                z-index:1;
                font-size:17px;
            }

            &:after {
                content:"";
                position:absolute;
                display:block;
                top:0;
                left:0;
                bottom:0;
                height:100%;
                width:100%;
                z-index:0;
                transform:scaleX(0);
                transform-origin:left center;

                background:$color-secondary;

                transition:transform 0.4s;
            }

            &:lang(ar):after {
                transform-origin:right center;
            }

            &:hover, &:focus {
                color:$color-text;
                opacity:0.8;
                text-decoration:none;
            }

            &.-active {
                cursor:default;
                opacity:1 !important;

                &:after {
                    transform:scaleX(1);
                }
            }

        }

        &.-responsive &-item {
            opacity:1;
            width:55px;
            text-align:center;

            @media (min-width:$screen-sm-min) {
                width:auto;
                text-align:left;
                opacity:0.5;
            }

            span {
                display:none;

                @media (min-width:$screen-sm-min) {
                    display:inline;
                }
            }

            svg, i {
                @media (min-width:$screen-sm-min) {
                    display:none;
                }
            }
        }
    }

    &_flat {
        display:block;
        margin:0 -11px;
        padding:5px 0;

        overflow-x:auto;
        overflow-y:hidden;
        white-space:nowrap;

        &::-webkit-scrollbar {
            width:0;
            background:transparent;
        }

        -ms-overflow-style:none;

        &-item {
            position:relative;
            display:inline-block;
            margin:0 11px;

            color:lighten($color-text, 70%);
            font-size:14px;
            font-weight:bold;
            letter-spacing:0.88px;

            &:after {
                content:"";
                position:absolute;
                display:block;
                height:2px;
                width:0px;
                left:0;
                bottom:-5px;

                background:$color-primary;

                transition:all 0.2s;
            }

            &.-active {
                color:$color-primary;

                &:after {
                    width:20px;
                }
            }

            &:hover, &:focus {
                color:$color-primary;
                text-decoration:none;
            }
        }
    }

}

@keyframes rd-nav_tabs-item-in {
    0% {
        width:0%;
    }

    50% {
        width:0%;
    }

    100% {
        width:50%;
    }
}

@keyframes rd-nav_tabs-item-out {
    0% {
        width:50%;
        transform:scaleX(1);
    }

    25%, 75% {
        width:100%;
        transform:scaleX(1);
    }

    100% {
        width:100%;
        transform:scaleX(0);
    }
}
